                                  :show-labels="false"
                                  :class="{ 'has-error' : errors.has('parent_zone') }"
                                  name="parent_zone"
                                  track-by="name"
                                  label="name"
                                  placeholder=""
                                  class="border border-solid border-grey-light rounded w-full py-px mt-1"
                                  data-vv-as="Parent Zone">
                          <template slot="singleLabel"
                                    slot-scope="props">
                            <div class="flex items-center ">
                              <div class="mx-2">
                                <span class="text-sm">Zone {{ props.option.zone_code }}</span>
