                              v-model="form.zone_pastor"
                              name="parent_district"
                              track-by="name"
                              label="name"
                              placeholder=""
                              class="border border-solid border-grey-light rounded py-px mt-1"
                              data-vv-as="Zone Pastor">
                      <template slot="singleLabel"
