<script>
import { mapState } from "vuex";
export default {
  name: "ZoneEdit",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Edit Zone",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      districts: [],
      zone_pastors: [],
      zones: [],
      zone: [],
      showInvitation: false,
      invitation: {
        email: ""
      },
      form: {
        zone_code: null,
        parent_district: null,
        parent_zone: null,
        zone_pastor: null
      }
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    this.get();
    this.getZonePastors();
    this.getDistricts();
    this.getZones();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("zones/profile", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          type_user: "member-group",
          zone_id: this.$route.params.id
        });
        this.zone = data.response;
        this.form.zone_code = this.zone.zone_code;
        this.getCurrentPastor(data.response.zone_pastor_id);
      } catch (error) {
        console.log(error);
        return error;
      }
    },
    async getDistricts() {
      try {
        const { data } = await window.axios.post("districts/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: "1"
        });
        this.districts = data.response;
        this.form.parent_district = this.districts.find(
          d => d.district_id == this.zone.parent_id
        );
      } catch (error) {
        return error;
      }
    },
    async getZones() {
      try {
        const { data } = await window.axios.post("zones/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: "1"
        });
        this.zones = data.response;
        this.form.parent_zone = this.zones.find(
          z => z.zone_id == this.zones.parent_id
        );
      } catch (error) {
        return error;
      }
    },
    async getZonePastors() {
      try {
        const { data } = await window.axios.post("groups/list-user-assign", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.church.church_id,
          parent_id: this.user.group.parent_id
        });
        this.zone_pastors = data.response ? data.response : [];
      } catch (error) {
        return error;
      }
    },
    async getCurrentPastor(user_id) {
      try {
        const { data } = await window.axios.post("users/profile", {
          user_id: user_id,
          type_user: "member-group"
        });

        this.zone_pastors.push({
          first_name: data.response.first_name,
          last_name: data.response.last_name,
          profile_picture: data.response.profile_picture,
          user_id: data.response.user_id
        });
        this.form.zone_pastor = this.zone_pastors.find(
          p => p.user_id == this.zone.zone_pastor_id
        );
      } catch (error) {
        return error;
      }
    }
  }
};
</script>
