<template>
  <v-dialog v-model="show" max-width="500px">
    <div
      class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
    >
      <h1 class="text-blue-darker mb-3 font-medium">Transfer</h1>
      <div class="flex justify-center">
        <button
          class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
          @click="showSector = true"
        >
          <p
            class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
          >Sector</p>
        </button>
        <button
          class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
          @click="showSectorSupervisor = true"
        >
          <p
            class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
          >Supervisor</p>
        </button>
      </div>
    </div>
    <v-dialog v-model="showSector" max-width="400px">
      <div
        class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
      >
        <h1 class="text-blue-darker mb-1 font-normal">Transfer</h1>
        <h2 class="text-blue-darker mb-8 font-normal text-base">Sector</h2>
        <div class="w-2/3">
          <div class="mb-4">
            <label class="text-sm">Sector</label>
            <v-select
              v-validate="'required'"
              :options="sectors"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('sector') }"
              v-model="transfer_sector.sector"
              track-by="id"
              label="id"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="sector"
              data-vv-as="sector"
            />
          </div>
          <div>
            <label class="text-sm">Target</label>
            <div class="flex justify-between">
              <div class="w-2/5">
                <v-select
                  v-validate="'required'"
                  :options="districts"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('district_id') }"
                  v-model="transfer_sector.target_district"
                  track-by="district_id"
                  label="district_id"
                  placeholder
                  class="border border-solid border-grey-light rounded my-1 w-full"
                  name="district_id"
                  data-vv-as="name"
                />
              </div>
              <div class="w-2/5">
                <v-select
                  v-validate="'required'"
                  :options="zones"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('zone_id') }"
                  v-model="transfer_sector.target_zone"
                  track-by="zone_id"
                  label="zone_id"
                  placeholder
                  class="border border-solid border-grey-light rounded my-1 w-full"
                  name="zone_id"
                  data-vv-as="name"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="border border-solid w-full my-8"/>
        <div class="flex w-full justify-end px-6">
          <button
            class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
            @click="transferZone()"
          >
            <span
              class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
            >Transfer</span>
          </button>
        </div>
      </div>
    </v-dialog>
    <v-dialog v-model="showSectorSupervisor" max-width="350px">
      <div
        class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
      >
        <h1 class="text-blue-darker mb-1 font-normal">Transfer</h1>
        <h2 class="text-blue-darker mb-8 font-normal text-base">Supervisor</h2>
        <div class="w-2/3">
          <div class="mb-3">
            <label class="text-sm">Name</label>
            <v-select
              v-validate="'required'"
              :options="users_transfer"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('supervisor') }"
              v-model="transfer_sector_supervisor.supervisor"
              track-by="user_id"
              label="user_id"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="supervisor"
              data-vv-as="supervisor"
            />
          </div>
          <div>
            <label class="text-sm">Target</label>
            <div class="flex justify-between">
              <div class="w-2/5">
                <v-select
                  v-validate="'required'"
                  :options="districts"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('district_id') }"
                  v-model="transfer_sector_supervisor.target_district"
                  track-by="district_id"
                  label="district_id"
                  placeholder
                  class="border border-solid border-grey-light rounded my-1 w-full"
                  name="district_id"
                  data-vv-as="name"
                >
                  <template slot="singleLabel" slot-scope="props">
                    <div class="flex items-center">
                      <div class="mx-2">
                        <span class="text-sm">D{{ props.option.district_code }}</span>
                      </div>
                    </div>
                  </template>
                  <template slot="option" slot-scope="props">
                    <div class="flex items-center">
                      <div class="mx-2">
                        <span class="text-sm">D{{ props.option.district_code }}</span>
                      </div>
                    </div>
                  </template>
                </v-select>
              </div>
              <div class="w-2/5">
                <v-select
                  v-validate="'required'"
                  :options="zones"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('zone_id') }"
                  v-model="transfer_sector_supervisor.target_zone"
                  track-by="zone_id"
                  label="zone_id"
                  placeholder
                  class="border border-solid border-grey-light rounded my-1 w-full"
                  name="zone_id"
                  data-vv-as="name"
                >
                  <template slot="singleLabel" slot-scope="props">
                    <div class="flex items-center">
                      <div class="mx-2">
                        <span class="text-sm">Z{{ props.option.zone_code }}</span>
                      </div>
                    </div>
                  </template>
                  <template slot="option" slot-scope="props">
                    <div class="flex items-center">
                      <div class="mx-2">
                        <span class="text-sm">Z{{ props.option.zone_code }}</span>
                      </div>
                    </div>
                  </template>
                </v-select>
              </div>
            </div>
          </div>
        </div>
        <div class="border border-solid w-full my-8"/>
        <div class="flex w-full justify-end px-6">
          <button
            class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
            @click="transferSectorSupervisor()"
          >
            <span
              class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
            >Transfer</span>
          </button>
        </div>
      </div>
    </v-dialog>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "TransferZones",
  data() {
    return {
      show: false,
      showSector: false,
      showSectorSupervisor: false,
      transfer_sector: {
        sector: null,
        target_district: null,
        target_zone: null
      },
      transfer_sector_supervisor: {
        supervisor: null,
        target_district: null,
        target_zone: null
      },
      sectors: [],
      districts: [],
      zones: [],
      users_transfer: []
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    EventBus.$on("show-transfer-zones", () => {
      this.show = !this.show;
    });
    this.getSectors();
    this.getGroupsTransfer();
    this.getUsersTransfer();
  },
  methods: {
    async getSectors() {
      const { data } = await window.axios.post("zones/sectors-groups-list", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        zone_id: this.user.group.parent_id,
        active: "1"
      });

      this.sectors = data.response;
    },
    async getGroupsTransfer() {
      const { data } = await window.axios.post("groups/list-groups-target", {
        user_id: this.user.user_id,
        role_id: this.user.role
      });

      this.districts = data.response.districts;
      this.zones = data.response.zones;
    },
    async getUsersTransfer() {
      const { data } = await window.axios.post("groups/list-user-transfer", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        church_id: this.church.church_id,
        parent_id: this.user.group.parent_id
      });

      this.users_transfer = data.response ? data.response : [];
    },
    async transferSector() {
      await window.axios.post("zones/transfer-sector", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        sector_id: this.transfer_sector.sector,
        district_id: this.transfer_sector.target_district.id,
        district_code: this.transfer_sector.target_district.district_code,
        zone_id: this.transfer_sector.target_zone.zone_id,
        zone_code: this.transfer_sector.target_zone.zone_code
      });

      this.showSector = false;
    },
    async transferSectorSupervisor() {
      await window.axios.post("zones/transfer-supervisor", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        supervisor_id: this.transfer_sector_supervisor.supervisor.user_id,
        district_id: this.transfer_sector_supervisor.target_district
          .district_id,
        zone_id: this.transfer_sector_supervisor.target_zone.zone_id
      });

      this.showSectorSupervisor = false;
    }
  }
};
</script>
