        <div>
          <h1 class="font-normal text-blue-darkest">Districts</h1>
          Showing <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ districts.length }}</span>out of {{districts.length}}
          <span class="px-2 bg-blue-lighter rounded text-blue mx-2">active</span>distritcs
