                      hide-actions
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <tr class="group">
              <td :class="props.item.is_complete == 0 ? 'group-hover:border-red' : 'group-hover:border-blue'"
                  class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px">
                <span class="-ml-2 w-8">
                  <v-tooltip right
                             color="red">
                    <v-icon v-if="props.item.is_complete == 0"
                            slot="activator"
                            color="red"
                            class="m-0 p-0 cursor-pointer">info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div class="bg-purple rounded-full text-white text-sm w-8 h-8 flex items-center justify-center">D{{ props.item.district_code }}</div>
