              <td class="text-center text-sm text-blue">{{ props.item.zones.padStart(2, "0") }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.sectors.padStart(2, "0") }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.cells.padStart(2, "0") }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.members }}</td>
              <td>
                <v-menu :nudge-bottom="10"
