<script>
import { mapGetters } from "vuex";

export default {
  name: "ChurchDistricts",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Districts",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        {
          text: "Zone ID",
          value: "district_code"
        },
        { text: "District Pastors", value: "district_pastor" },
        { text: "Zones", value: "zones", align: "center" },
        { text: "Sectors", value: "sectors", align: "center" },
        { text: "Cells", value: "cells", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "protein" }
      ],
      districts: [],
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user"
    })
  },
  mounted() {
    this.get();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("districts/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: 1
        });
        this.districts = data.response;
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
