<template>
  <div class="w-full flex justify-center">
    <div class="bg-white rounded-lg h-auto pt-8 pb-6 shadow h-auto" style="width: 451px">
      <div class="text-center mb-12">
        <h1 class="font-normal text-3xl text-blue-darker">Invitacin Lder</h1>
      </div>
      <form @submit.prevent="submit">
        <div class="px-20">
          <div class="mb-6">
            <label class="text-grey text-sm">ID de Clula</label>
            <div class="flex w-full -mb-3">
              <div class="w-1/4 pr-1">
                <v-text-field
                  id="district_code"
                  name="district_code"
                  value="0"
                  class="text-center"
                  disabled
                />
              </div>
              <div class="w-1/4 px-1">
                <v-text-field
                  v-validate="'required'"
                  id="zone_code"
                  v-model="form.zone_code"
                  :error="errors.has('zone_code')"
                  name="zone_code"
                  type="text"
                  mask="##"
                  @input="clean"
                />
              </div>
              <div class="w-1/4 px-1">
                <v-text-field
                  v-validate="'required'"
                  id="sector_code"
                  v-model="form.sector_code"
                  :error="errors.has('sector_code')"
                  name="sector_code"
                  type="text"
                  mask="##"
                  @input="clean"
                />
              </div>
              <div class="w-1/4 pl-1">
                <v-text-field
                  v-validate="'required'"
                  id="cell_code"
                  v-model="form.cell_code"
                  :error="errors.has('cell_code')"
                  name="cell_code"
                  type="text"
                  mask="##"
                  @input="clean"
                />
              </div>
            </div>
            <div class="flex w-full">
              <small
                v-if="['zone', 'sector', 'cell', 'assigned_cell'].includes(error.type)"
                class="text-red text-center"
              >{{ error.message }}</small>
            </div>
          </div>
          <div class="mb-6">
            <div class="flex w-full">
              <div class="w-full px-1">
                <label class="text-grey text-sm">Nombre</label>
                <v-text-field
                  v-validate="'required'"
                  id="name"
                  v-model="form.name"
                  :error-messages="errors.collect('name')"
                  name="name"
                  data-vv-as="nombre"
                  class="m-0 p-0"
                />
              </div>
            </div>
          </div>
          <div class="mb-6">
            <div class="flex w-full">
              <div class="w-full px-1">
                <label class="text-grey text-sm">Apellido</label>
                <v-text-field
                  v-validate="'required'"
                  v-model="form.last_name"
                  :error-messages="errors.collect('last_name')"
                  name="last_name"
                  data-vv-as="apellido"
                  class="m-0 p-0"
                />
              </div>
            </div>
          </div>
          <div class="mb-6">
            <div class="flex w-full">
              <div class="w-full px-1">
                <label class="text-grey text-sm">Correo Electrnico</label>
                <v-text-field
                  v-validate="'required|email'"
                  id="email"
                  v-model="form.email"
                  :error-messages="errors.collect('email')"
                  name="email"
                  class="m-0 p-0"
                  data-vv-as="correo"
                  @input="clean"
                />
                <small v-if="error.type == 'mail_exist'" class="text-red">{{ error.message }}</small>
              </div>
            </div>
          </div>
          <div class="flex justify-center w-full my-6">
            <button
              :class="[ errors.any() || error.status ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any() || error.status"
              type="submit"
              class="w-36 rounded py-3 text-white heebo mx-2"
            >
              <template v-if="!loading">
                <span class="font-medium text-base heebo tracking-wide">Invitar</span>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                >Cargando...</p>
              </template>
            </button>
          </div>
        </div>
      </form>

      <v-dialog v-model="dialog" max-width="365">
        <div class="bg-white px-6 pt-10 pb-6 rounded-lg text-sm">
          <div>
            <p class="text-center">Se envi la invitacin a</p>
            <p v-if="info" class="text-center">
              <strong>{{ info.name }} {{ info.last_name }}</strong> con la Clula
              <strong>D0 Z{{ info.zone_code }} S{{ info.sector_code }} C{{ info.cell_code }}</strong>
            </p>
            <div class="text-center mt-4">
              <v-icon class="bg-green p-3 rounded-full text-white font-bold">check</v-icon>
            </div>
          </div>
        </div>
      </v-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "LeaderInvitation",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Invitacin Lider",
    htmlAttrs: {
      lang: "es"
    }
  },
  data() {
    return {
      dialog: false,
      error: {
        type: "",
        message: "",
        status: null
      },
      loading: false,
      form: {
        zone_code: null,
        sector_code: null,
        cell_code: null,
        name: null,
        last_name: null,
        email: null,
        church_id: 1
      },
      info: null
    };
  },
  watch: {
    dialog: function(value) {
      if (value) {
        this.closeDialog();
      }
    }
  },
  methods: {
    async submit() {
      this.loading = true;
      try {
        const isValid = await this.$validator.validateAll();
        if (isValid) {
          const { data } = await window.axios.post(
            "general/temporary-invitation-leader",
            this.form
          );
          this.info = data.response;
          this.dialog = true;
        }
      } catch (e) {
        const { data } = e.response;
        this.error = data.error;
        return e;
      } finally {
        this.loading = false;
      }
    },
    clean() {
      if (this.error.type !== "") {
        this.error = "";
      }
    },
    closeDialog() {
      setTimeout(async () => {
        this.dialog = false;
        this.form.zone_code = null;
        this.form.sector_code = null;
        this.form.cell_code = null;
        this.form.name = null;
        this.form.last_name = null;
        this.form.email = null;
        await this.$validator.pause();
        await this.$validator.reset();
        await this.$validator.resume();
      }, 5000);
    }
  }
};
</script>
