<template>
  <auth-card>
    <template slot="form">
      <form autocomplete="off" class="mb-8" @submit.prevent="submit">
        <p
          class="text-grey text-base tracking-wide mb-4"
        >Welcome to C+ The Easy Way to Expand. Sign in entering your churchs URL.</p>
        <div class="mb-2">
          <div class="flex items-baseline">
            <input
              v-validate="'required'"
              :class="errors.has('url') ? 'has-error' : 'focus:border-blue'"
              v-model="subdomain"
              type="text"
              class="form-input w-2/3 mt-2 text-blue-darker mr-2 text-right text-xl mb-1 py-2 focus:outline-none"
              name="url"
              data-vv-as="church URL"
              data-vv-delay="500"
              placeholder="your-church"
              readonly
            >
            <p class="text-xl text-blue-darker heebo">.cplusapp.com</p>
          </div>
          <small class="text-error flex">{{ errors.first('url') }}</small>
        </div>
        <button
          :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
          :disabled="errors.any()"
          type="submit"
          class="w-full rounded py-3 text-white flex items-center justify-center no-outline"
        >
          <template v-if="!loading">
            <span class="mr-6 font-medium text-base heebo tracking-wide">Continue</span>
            <span class="icon-arrow-right text-sm"/>
          </template>
          <template v-else>
            <p
              class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
            >Loading...</p>
          </template>
        </button>
      </form>
    </template>
    <template slot="footer">
      <p class="text-sm text-grey heebo mb-6">Need to get your Church started on C+?
        <router-link
          :to="{ name: 'sign-up'}"
          class="no-underline text-blue hover:underline font-normal"
        >Sign Up</router-link>
      </p>
    </template>
  </auth-card>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapMutations } from "vuex";
export default {
  name: "SignInUrl",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - SignIn Subdomain",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      loading: false,
      subdomain: "rest-1"
    };
  },
  methods: {
    ...mapMutations({
      saveDomain: "auth/SAVE_DOMAIN",
      saveChurch: "auth/SAVE_CHURCH"
    }),
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          const { data } = await window.axios.post("/validate-subdomain", {
            subdomain: this.subdomain
          });
          await this.saveDomain(this.subdomain);

          delete data.response.message;
          delete data.response.valid;
          await this.saveChurch(data.response);

          this.$router.replace({
            name: "signin"
          });
        }
      } catch (e) {
        return e;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
