<template>
  <v-dialog v-model="show" max-width="471px" width="471px">
    <div class="bg-white h-auto pb-6 shadow px-0 relative rounded-lg text-center py-6">
      <h1 class="text-blue-darker font-medium text-3xl mb-4">Agregar Miembro</h1>
      <div class="flex w-full justify-center">
        <div class="mx-2">
          <button
            class="rounded py-3 w-48 text-blue-darker border-2 border-solid border-blue-darker no-outline bg-white hover:bg-blue-darker hover:text-white"
            type="button"
            @click.prevent="addMember"
          >
            <span class="font-semibold tracking-wide">Miembro</span>
          </button>
        </div>
        <div class="mx-2">
          <button
            class="rounded py-3 w-48 text-blue-darker border-2 border-solid border-blue-darker no-outline bg-white hover:bg-blue-darker hover:text-white"
            type="button"
            @click.prevent="addGuest"
          >
            <span class="font-semibold tracking-wide">Invitado</span>
          </button>
        </div>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
export default {
  name: "AddMembers",
  data() {
    return {
      show: false,
      user: {}
    };
  },
  mounted() {
    EventBus.$on("add-members-dialog", user => {
      this.show = true;
      this.user = user;
    });
  },
  methods: {
    addMember() {
      EventBus.$emit("add-cell-member", this.user);
    },
    addGuest() {
      EventBus.$emit("add-guest", this.user);
    }
  }
};
</script>
