<script>
import { mapGetters, mapActions } from "vuex";
import { ContentLoader } from "vue-content-loader";
export default {
  name: "ChurchProfile",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Profile",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    ContentLoader
  },
  data() {
    return {
      church: null,
      loading: false,
      dialog: false,
      dialogMap: false,
      users: [],
      map: {
        center: { lat: 45.508, lng: -73.587 },
        marker: { lat: 45.508, lng: -73.587 },
        place: [],
        currentPlace: null
      },
      form: {
        church_name: null,
        country: null,
        state: null,
        city: null,
        address: null,
        phone_number: "",
        latitude: null,
        longitude: null,
        pastor: {}
      },
      loader: true
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user",
      info: "auth/info"
    })
  },
  mounted() {
    this.getUsers();
    this.getChurch();
  },
  methods: {
    ...mapActions({
      updateChurch: "auth/updateChurchInfo"
    }),
    async getChurch() {
      try {
        const { data } = await window.axios.post("church/profile", {
          church_id: this.user.church_id,
          user_id: this.user.user_id,
          role_id: this.user.role
        });

        this.church = data.response;
        this.assignForm(data.response);
      } catch (e) {
        return e;
      } finally {
        this.loader = false;
      }
    },
    async editChurch() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          let data = new FormData();
          data.append("church_id", this.user.church_id);
          data.append("church_name", this.form.church_name);
          data.append("senior_pastor_id", this.form.pastor);
          data.append("state", this.form.state);
          data.append("city", this.form.city);
          data.append("address", this.form.address);
          data.append("phone_number", this.form.phone_number);
          data.append("latitude", this.form.latitude);
          data.append("longitude", this.form.longitude);

          await this.updateChurch({
            body: data,
            type: "edit"
          });
          this.dialog = false;
        }
      } catch (e) {
        console.error(e, "profile error");
      } finally {
        this.loading = false;
      }
    },
    setPlace(place) {
      this.form.address = place.formatted_address;
      this.map.currentPlace = place;
    },
    addMarker() {
      if (this.map.currentPlace) {
        this.form.latitude = this.map.currentPlace.geometry.location.lat();
        this.form.longitude = this.map.currentPlace.geometry.location.lng();
        const marker = {
          lat: this.form.latitude,
          lng: this.form.longitude
        };
        this.map.marker = marker;
        this.map.place = this.map.currentPlace;
        this.map.center = marker;
        this.map.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.map.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    assignForm(data) {
      this.form.church_name = data.church_name;
      this.form.state = data.state;
      this.form.city = data.city;
      this.form.address = data.address;
      this.form.latitude = data.latitude;
      this.form.longitude = data.longitude;
      this.form.phone_number = data.phone_number;
      this.form.pastor = this.users.find(u => {
        return u.user_id == data.senior_pastor_id;
      });
    },
    async getUsers() {
      try {
        const { data } = await window.axios.post("users/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role
        });

        this.users = data.response;
      } catch (error) {
        return error;
      }
    }
  }
};
</script>
