<template>
  <v-dialog v-model="show" max-width="577px">
    <div class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 relative">
      <div class="flex items-center justify-center flex-col">
        <div
          :class="form.profile_picture ? '' : 'bg-grey-lightest'"
          class="w-32 h-32 rounded-full -mt-100 pin-r text-center cursor-pointer relative mb-6"
        >
          <div v-if="form.profile_picture">
            <img
              :src="form.profile_picture"
              alt="Profile Image"
              class="rounded-full w-32 h-32 flex object-cover items-center"
            >
          </div>
          <p
            v-else
            class="text-white text-5xl uppercase flex items-center justify-center h-full"
          >{{ initials }}</p>
          <v-btn
            v-if="form.profile_picture"
            flat
            icon
            color="primary"
            class="absolute pin-t pin-r -m-4"
            @click="cleanImage"
          >
            <v-icon color="white" class>close</v-icon>
          </v-btn>
          <v-icon color="white" class="absolute pin-b pin-x mb-2">camera_alt</v-icon>
          <input
            v-validate="'size:1000'"
            id="profile_picture"
            type="file"
            name="profile_picture"
            class="absolute pin-x h-full w-full pin-t cursor-pointer rounded-full opacity-0"
            @change="previewImage($event)"
          >
        </div>
        <v-btn icon class="absolute pin-t pin-r -m-12" @click="show = false">
          <v-icon class="text-grey">close</v-icon>
        </v-btn>
      </div>
      <form data-vv-scope="profile-form" @submit.prevent="saveProfile('profile-form')">
        <div class="w-full">
          <div class="px-6 mb-4">
            <div class="flex">
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label for="first_name" class="w-full text-grey text-sm">Nombre</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('first_name')"
                  v-model="form.first_name"
                  name="first_name"
                  data-vv-as="nombre"
                  class="m-0 p-0"
                />
              </div>
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Apellido</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('last_name')"
                  v-model="form.last_name"
                  name="last_name"
                  data-vv-as="apellido"
                  class="m-0 p-0"
                />
              </div>
            </div>
            <div class="flex mb-2">
              <div class="w-full flex px-2 items-center w-1/2 text-sm">
                <p-radio v-model="form.gender" name="radio1" color="primary-o" value="f">Femenino</p-radio>
                <p-radio v-model="form.gender" name="radio1" color="primary-o" value="m">Masculino</p-radio>
              </div>
              <div class="w-full flex flex-col w-1/2 px-2">
                <label class="w-full text-grey text-sm">Fecha de nacimiento</label>
                <v-menu
                  ref="menu"
                  :close-on-content-click="false"
                  v-model="menu"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    v-validate="'required'"
                    slot="activator"
                    :error-messages="errors.collect('birth_date')"
                    :value="form.birth_date ? formatDate(form.birth_date) : ''"
                    class="text-sm m-0 p-0"
                    name="birth_date"
                    data-vv-as="fecha de nacimiento"
                    readonly
                  />
                  <v-date-picker
                    ref="birth_date"
                    v-model="form.birth_date"
                    :max="new Date().toISOString().substr(0, 10)"
                    min="1950-01-01"
                    locale="es"
                    no-title
                    @input="menu = false"
                  />
                </v-menu>
              </div>
            </div>
            <div class="flex">
              <div class="flex mb-2 w-1/2">
                <div class="flex flex-col px-2">
                  <label class="text-grey text-sm">Estado civil</label>
                  <v-select
                    v-validate="'required'"
                    :error-messages="errors.collect('marital_status')"
                    :items="maritalStatuses"
                    v-model="form.marital_status"
                    item-text="name"
                    item-value="id"
                    name="marital_status"
                    data-vv-as="estado civil"
                    class="m-0 p-0"
                  />
                </div>
              </div>
              <div v-if="form.marital_status == 'M'" class="flex flex-col w-1/2 px-2">
                <label class="w-full text-grey text-sm">Aniversario</label>
                <v-menu
                  ref="menu"
                  :close-on-content-click="false"
                  v-model="menu_married"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    :error-messages="errors.collect('married_since')"
                    v-model="form.married_since"
                    class="text-sm m-0 p-0"
                    name="married_since"
                    data-vv-as="aniversario"
                    readonly
                  />
                  <v-date-picker
                    ref="picker"
                    v-model="form.married_since"
                    :max="new Date().toISOString().substr(0, 10)"
                    min="1950-01-01"
                    locale="es"
                    no-title
                    @input="menu_married = false"
                  />
                </v-menu>
              </div>
            </div>
            <div class="flex mb-2">
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Direccin</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('address')"
                  v-model="form.address"
                  name="address"
                  data-vv-as="direccin"
                  class="m-0 p-0"
                />
              </div>
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Cdigo Postal</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('postal_code')"
                  v-model="form.postal_code"
                  name="postal_code"
                  data-vv-as="cdigo postal"
                  class="m-0 p-0"
                />
              </div>
            </div>
            <div class="flex mb-2">
              <div class="flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Ciudad</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('city')"
                  v-model="form.city"
                  name="city"
                  data-vv-as="ciudad"
                  class="m-0 p-0"
                />
              </div>
              <div class="flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Telfono</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('phone')"
                  v-model="form.phone"
                  name="phone"
                  data-vv-as="telfono"
                  mask="### ### ####"
                  class="m-0 p-0"
                />
              </div>
            </div>
            <div class="flex">
              <div class="flex flex-col w-1/2 px-2">
                <label class="w-full text-grey text-sm">Invitado desde</label>
                <v-menu
                  ref="menu"
                  :close-on-content-click="false"
                  v-model="menu_since"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    v-validate="'required'"
                    slot="activator"
                    :error-messages="errors.collect('guest_since')"
                    :value="form.guest_since ? formatDate(form.guest_since) : ''"
                    class="text-sm m-0 p-0"
                    name="guest_since"
                    data-vv-as="invitado desde"
                    readonly
                  />
                  <v-date-picker
                    ref="picker"
                    v-model="form.guest_since"
                    :max="new Date().toISOString().substr(0, 10)"
                    min="1950-01-01"
                    locale="es"
                    no-title
                    @input="menu = false"
                  />
                </v-menu>
              </div>
              <div class="flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Correo</label>
                <v-text-field
                  v-validate="'required'"
                  :error-messages="errors.collect('email')"
                  v-model="form.email"
                  name="email"
                  data-vv-as="correo"
                  class="m-0 p-0"
                />
              </div>
            </div>
          </div>
          <div class="border-t opacity-50 mb-4 border-grey-light"/>
          <div class="flex justify-end px-6 py-3">
            <div>
              <button
                class="rounded py-3 w-40 text-grey font-bold flex items-center justify-center no-outline border border-solid border-grey mx-1"
                @click.prevent="show = false"
              >Cancelar</button>
            </div>
            <div>
              <button
                :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                :disabled="errors.any()"
                class="rounded py-3 w-40 text-white heebo flex items-center justify-center no-outline"
                type="submit"
              >
                <template v-if="!loading">
                  <span class="font-semibold tracking-wide">Guardar</span>
                </template>
                <template v-else>
                  <p
                    class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                  >Cargando...</p>
                </template>
              </button>
            </div>
          </div>
        </div>
      </form>
    </div>
  </v-dialog>
</template>

<script>
import firebase from "@/plugins/firebase";
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "EditProfileGuest",
  data() {
    return {
      file: {},
      show: false,
      loading: false,
      menu_member: false,
      menu: false,
      menu_since: false,
      menu_married: false,
      menu_guest: false,
      form: {
        first_name: "",
        last_name: "",
        email: null,
        phone: null,
        birth_date: null,
        gender: "f",
        maritalStatus: null,
        marital_status: null,
        married_since: null,
        guest_since: null,
        member_since: null,
        exelerate_id: null,
        parent_user_id: null,
        parent_role_id: null,
        postal_code: null,
        church_id: null,
        user_id: null,
        profile_picture: null,
        cell_id: null,
        role_id: null
      },
      maritalStatuses: [
        { id: "M", name: "Casado" },
        { id: "S", name: "Soltero" }
      ]
    };
  },
  computed: {
    ...mapState({
      auth_user: state => state.auth.user
    }),
    initials() {
      if (this.form.first_name) {
        return this.form.first_name.charAt(0) + this.form.last_name.charAt(0);
      }
      return "";
    }
  },
  watch: {
    menu(val) {
      val &&
        this.$nextTick(() => (this.$refs.birth_date.activePicker = "YEAR"));
    }
  },
  mounted() {
    EventBus.$on("edit-profile-guest", value => {
      this.form = value;
      this.form.user_id = this.auth_user.user_id;
      this.form.church_id = this.auth_user.church_id;
      this.form.role_id = this.auth_user.role;
      this.show = true;
    });
  },
  methods: {
    async saveProfile(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          await this.uploadImage();
          await window.axios.put("members/guests/edit", this.form);
          EventBus.$emit("save-guest-member");
          this.show = false;
        }
      } catch (e) {
        const error = e.response;
        this.message = error.data.error.message;
      } finally {
        this.loading = false;
      }
    },
    previewImage(event) {
      let reader = new FileReader();
      this.file = event.target.files[0];
      reader.onload = () => {
        this.form.profile_picture = reader.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    },
    async uploadImage() {
      if (this.file.name) {
        const ref = firebase
          .storage()
          .ref()
          .child(this.file.name);
        ref.put(this.file);
        const url = await ref.getDownloadURL();
        this.form.profile_picture = url;
      }
    },
    cleanImage() {
      this.file = {};
      this.form.profile_picture = null;
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    }
  }
};
</script>
