<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions } from "vuex";
import firebase from "@/plugins/firebase";
export default {
  name: "ProfileEditAuthUser",
  components: {
    AuthCard
  },
  data() {
    return {
      file: {},
      edit: false,
      show: false,
      loading: false,
      tab: 0,
      user: [],
      menu: false,
      menu_married: false,
      menu_since: false,
      menu_guest: false,
      menu_member: false,
      message: null,
      form: {
        first_name: "",
        last_name: "",
        email: null,
        phone: null,
        birth_date: null,
        gender: "f",
        maritalStatus: null,
        marital_status: null,
        married_since: null,
        guest_since: null,
        member_since: null,
        exelerate_id: null,
        parent_user_id: null,
        parent_role_id: null,
        postal_code: null,
        church_id: null,
        user_id: null,
        profile_picture: null
      },
      maritalStatuses: [
        { id: "M", name: "Casado" },
        { id: "S", name: "Soltero" }
      ]
    };
  },
  computed: {
    initials() {
      if (this.form.first_name) {
        return this.form.first_name.charAt(0) + this.form.last_name.charAt(0);
      }
      return "";
    }
  },
  watch: {
    menu(val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  mounted() {
    EventBus.$on("show-profile-edit-auth-user", value => {
      this.form = value;
      this.form.birth_date = value.birth_date;
      this.form.parent_user_id = value.user_id;
      this.form.parent_role_id = value.role_id;
      this.form.church_id = 1;
      this.show = !this.show;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins",
      updateUser: "auth/updateUser"
    }),
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    save(date) {
      this.$refs.menu.save(date);
    },
    async saveProfile() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          await this.uploadImage();
          await window.axios.put("users/edit-profile", this.form);
          await this.updateUser(this.form);
          this.show = false;
        }
      } catch (e) {
        console.log(e);
        const error = e.response;
        this.message = error.data.error.message;
      } finally {
        this.loading = false;
      }
    },
    previewImage(event) {
      let reader = new FileReader();
      this.file = event.target.files[0];
      reader.onload = () => {
        this.form.profile_picture = reader.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    },
    async uploadImage() {
      if (this.file.name) {
        const ref = firebase
          .storage()
          .ref()
          .child(this.file.name);
        ref.put(this.file);
        const url = await ref.getDownloadURL();
        this.form.profile_picture = url;
      }
    },
    cleanImage() {
      this.file = {};
      this.form.profile_picture = null;
    }
  }
};
</script>
