<template>
  <v-dialog v-model="show" max-width="365px">
    <div class="bg-white rounded p-6 relative">
      <v-btn icon class="absolute pin-t pin-r -m-12" @click="show = false">
        <v-icon class="text-grey">close</v-icon>
      </v-btn>
      <div>
        <p class="text-3xl font-medium text-blue-darker mb-6 text-center">Promover</p>
        <div class="flex items-center justify-center mb-6">
          <div>
            <img
              :src="guest.profile_picture ? guest.profile_picture : require('@/assets/images/avatar.png')"
              alt="avatar"
              class="rounded-full w-24 h-24 mr-6 object-cover"
            >
          </div>
          <div class="text-left">
            <p
              class="font-medium text-grey-dark font-bold heebo text-sm"
            >D0 Z{{guest.zone_code}} S{{guest.sector_code}} C{{guest.cell_code}}</p>
            <p class="text-lg text-blue-darker font-bold">{{guest.full_name}}</p>
          </div>
        </div>
        <div class="mb-6">
          <p
            class="text-sm text-blue-darker"
          >Ests seguro de que quieres promover a {{guest.first_name}} {{guest.last_name}} como Miembro?</p>
          <p v-if="message" class="text-red">{{ message }}</p>
        </div>
        <div class="flex justify-center">
          <div>
            <button
              class="rounded py-3 w-48 text-white heebo flex items-center justify-center no-outline bg-blue-darker hover:bg-blue-dark"
              @click="promove"
            >
              <template v-if="!loading">
                <span class="font-semibold tracking-wide">Promover</span>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                >Cargando...</p>
              </template>
            </button>
          </div>
        </div>
      </div>
    </div>
    <v-dialog v-model="confirm" max-width="365">
      <div class="bg-white px-6 pt-10 pb-6 rounded-lg text-sm">
        <div>
          <p class="text-center text-blue-darker">
            <strong>{{ guest.full_name }}</strong> fue promovido exitosamente,
            ahora lo encontraras en la lista de miembros.
          </p>
          <div class="text-center mt-4">
            <v-icon class="bg-green p-3 rounded-full text-white font-bold">check</v-icon>
          </div>
        </div>
      </div>
    </v-dialog>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "PromoveGuest",
  data() {
    return {
      show: false,
      confirm: false,
      guest: {},
      message: null,
      loading: false
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    EventBus.$on("promove-guest", value => {
      this.message = null;
      this.guest = value;
      this.show = true;
    });
  },
  methods: {
    async promove() {
      this.loading = true;
      try {
        await window.axios.post("members/guests/promote", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.user.church_id,
          guest_id: this.guest.member_id
        });
        this.confirm = true;
        EventBus.$emit("save-guest-member");
        setTimeout(() => {
          this.confirm = false;
          this.show = false;
        }, 5000);
      } catch (error) {
        const { data } = error.response;
        this.message = data.error.message;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
