import store from "@/store";

export default (to, from, next) => {
  if (store.getters["auth/check"] && store.getters["auth/info"]) {
    next({ name: "dashboard" });
  } else if (to.name == "invitation") {
    store.dispatch("auth/saveToken", { token: to.query.token });
    next({ name: "register-invitation" });
  } else {
    next();
  }
};
