<template>
  <div>
    <auth-card :width="612" :padding="0" style="margin-top: 100px" class="relative">
      <template slot="header">
        <div class="flex flex-col items-center justify-center relative mb-4">
          <div
            class="rounded-full -mt-100 h-36 w-36 bg-blue text-white flex items-center justify-center text-5xl mb-3"
          >C{{ groups.cell_code }}</div>
          <div class="flex">
            <h1 class="font-normal heebo text-3xl text-blue-darker mx-1">D0</h1>
            <h1 class="font-normal heebo text-3xl text-blue-darker mx-1">Z{{ groups.zone_code }}</h1>
            <h1 class="font-normal heebo text-3xl text-blue-darker mx-1">S{{ groups.sector_code }}</h1>
            <h1 class="font-normal heebo text-3xl text-blue-darker mx-1">C{{ groups.cell_code }}</h1>
          </div>
          <p class="text-sm">Registra t Clula</p>
        </div>
      </template>
      <template slot="form">
        <ValidationObserver ref="observer">
          <form slot-scope="{ invalid }" class="mb-6" autocomplete="off" @submit.prevent="submit">
            <div class="absolute pin-t">
              <v-btn icon @click="$router.go(-1)">
                <v-icon class="text-grey-light" medium>keyboard_arrow_left</v-icon>
              </v-btn>
            </div>
            <div class="px-6 w-full mb-10">
              <div class="flex w-full mb-3">
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="w-full text-grey text-sm">Da de reunin</label>
                  <ValidationProvider name="da de reunin" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-select
                        :error-messages="errors"
                        :success="valid"
                        :items="meets"
                        v-model="form.meets"
                        name="meets"
                        class="m-0 p-0 text-sm"
                      />
                    </template>
                  </ValidationProvider>
                </div>
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="w-full text-grey text-sm">Hora de reunin</label>
                  <ValidationProvider name="hora de reunin" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-select
                        :error-messages="errors"
                        :success="valid"
                        :items="hours"
                        v-model="form.meets_time"
                        name="meets_time"
                        class="m-0 p-0 text-sm"
                      />
                    </template>
                  </ValidationProvider>
                </div>
              </div>
              <div class="flex w-full mb-3">
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="w-full text-grey text-sm">Direccin</label>
                  <ValidationProvider name="direccin" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        :error-messages="errors"
                        :success="valid"
                        v-model="form.address"
                        name="address"
                        class="m-0 p-0"
                      />
                    </template>
                  </ValidationProvider>
                </div>
                <div class="hebbo text-left w-1/2 px-2">
                  <label for="zip_code" class="w-full text-grey text-sm">Cdigo Postal</label>
                  <ValidationProvider name="cdigo postal" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        :error-messages="errors"
                        :success="valid"
                        v-model="form.zip_code"
                        placeholder="00000"
                        name="zip_code"
                        class="m-0 p-0"
                      />
                    </template>
                  </ValidationProvider>
                </div>
              </div>
              <div class="flex w-full mb-3">
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="text-grey text-sm">Ciudad</label>
                  <ValidationProvider name="ciudad" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        v-model="form.city"
                        :error-messages="errors"
                        :success="valid"
                        name="city"
                        class="m-0 p-0"
                      />
                    </template>
                  </ValidationProvider>
                </div>
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="w-full text-grey text-sm">Telfono</label>
                  <ValidationProvider name="telfono" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        v-model="form.phone_number"
                        :error-messages="errors"
                        :success="valid"
                        name="number"
                        mask="### ### ####"
                        class="m-0 p-0"
                      />
                    </template>
                  </ValidationProvider>
                </div>
              </div>
              <div class="flex w-full mb-4">
                <div class="hebbo text-left w-1/2 px-2">
                  <label class="text-sm text-grey tracking-wide">Fecha de Inicio</label>
                  <ValidationProvider name="fecha de inicio" rules="required">
                    <template slot-scope="{ errors, valid }">
                      <v-menu
                        ref="menu"
                        :close-on-content-click="true"
                        v-model="since"
                        :nudge-right="40"
                        lazy
                        transition="scale-transition"
                        offset-y
                        full-width
                        class="m-0 p-0"
                        min-width="290px"
                      >
                        <v-text-field
                          v-validate="'required'"
                          slot="activator"
                          :value="formatDate"
                          :error-messages="errors"
                          :success="valid"
                          name="since"
                          readonly
                          class="h-12 text-sm m-0 p-0"
                        >
                          <v-icon slot="prepend-inner" class="text-xl text-grey">calendar_today</v-icon>
                        </v-text-field>
                        <v-date-picker
                          ref="picker"
                          v-model="form.since"
                          :max="new Date().toISOString().substr(0, 10)"
                          locale="es"
                          min="1950-01-01"
                          no-title
                          @input="since = false"
                        />
                      </v-menu>
                    </template>
                  </ValidationProvider>
                </div>
              </div>
              <div class="flex w-full">
                <div class="hebbo text-left w-1/2 px-2">
                  <p class="text-sm text-grey">Todos los campos son requeridos.</p>
                </div>
              </div>
            </div>
            <div class="px-16 flex justify-center">
              <div>
                <button
                  :class="[ invalid ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                  :disabled="invalid"
                  class="w-36 rounded py-3 text-white heebo flex items-center justify-center no-outline"
                >
                  <template v-if="!loading">
                    <span class="font-medium text-base heebo tracking-wide">Continuar</span>
                  </template>
                  <template v-else>
                    <p
                      class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                    >Cargando...</p>
                  </template>
                </button>
              </div>
            </div>
          </form>
        </ValidationObserver>
      </template>
    </auth-card>
    <v-dialog v-model="terms" persistent max-width="500px" class="rounded-lg">
      <div class="flex flex-col items-center px-10 bg-white py-12 rounded-lg">
        <div class="text-center font-normal text-4xl heebo flex flex-col items-center mb-3">
          <img src="@/assets/images/Logo.png" alt="Logo C+" class="w-24 h-24 mb-4"> Trminos y Condiciones
        </div>
        <div class="text-justify mb-8">
          Al hacer click en
          "
          <strong>Acepto</strong>, usted comprende y acepta el Contrato en nombre del Cliente (como se describe en los
          <span
            class="underline"
          >Terminos de Servicio del Cliente</span>), la
          <span class="underline">Poltica de Privacidad</span>, y la
          <span class="underline">Poltica de Cookies.</span>
        </div>
        <div>
          <button
            :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
            :disabled="errors.any()"
            class="w-full rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
            @click="agree"
          >
            <template v-if="!loading">
              <span class="font-semibold tracking-wide">Acepto</span>
            </template>
            <template v-else>
              <p
                class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
              >Cargando...</p>
            </template>
          </button>
        </div>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import firebase from "@/plugins/firebase";
export default {
  name: "CellGroupInfo",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Cell Group Info",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      loading: false,
      terms: false,
      since: null,
      groups: {
        cell_code: 0,
        sector_code: 0,
        zone_code: 0
      },
      form: {
        city: null,
        address: null,
        phone_number: null,
        meets: null,
        meets_time: null,
        since: null,
        email: null,
        password: null,
        token: null,
        image: null,
        church_id: null,
        cell_id: null,
        zip_code: null
      },
      meets: ["Jueves", "Viernes", "Sbado"],
      hours: [
        "4:00 PM",
        "5:00 PM",
        "6:00 PM",
        "7:00 PM",
        "8:00 PM",
        "9:00 PM",
        "10:00 PM"
      ]
    };
  },
  computed: {
    ...mapState({
      info: state => state.auth.info
    }),
    formatDate() {
      return new Date(this.form.since).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    }
  },
  mounted() {
    this.setup();
  },
  methods: {
    ...mapActions({
      saveChurch: "auth/saveChurchInfo",
      updateChurch: "auth/updateChurchInfo",
      login: "auth/login"
    }),
    async submit() {
      this.loading = true;
      try {
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          this.terms = true;
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    setup() {
      const { info, user } = this.info;
      this.form.cell_id = info.cell_id;
      this.form.church_id = info.church_id;
      this.form.token = info.token;

      this.form.password = user.password;
      this.form.email = user.email;

      this.groups.cell_code = info.cell_code;
      this.groups.zone_code = info.zone_code;
      this.groups.sector_code = info.sector_code;
    },
    async agree() {
      this.loading = true;
      try {
        await this.uploadImage();
        await window.axios.post("/general/leader-register", this.form);
        await this.login(this.form);
        this.$router.replace({
          name: "dashboard"
        });
      } catch (e) {
        return e;
      } finally {
        this.loading = false;
      }
    },
    async uploadImage() {
      if (this.info.user.file.name) {
        const ref = firebase
          .storage()
          .ref()
          .child(this.info.user.file.name);
        ref.put(this.info.user.file);
        const url = await ref.getDownloadURL();
        this.form.image = url;
      }
    }
  }
};
</script>
