  <div class="flex w-full">
    <div class="bg-white h-full min-h-screen w-12 px-8">
      <div class="flex flex-col items-center py-6">
        <router-link :to="{name: 'cell-groups-profile', params: {id: $route.params.id}}"
                     :class="$route.name == 'cell-groups-profile' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-account-box text-xl" />
        </router-link>
        <router-link :to="{name: 'cell-groups-members', params: {id: $route.params.id}}"
                     :class="$route.name == 'cell-groups-members' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-people text-xl" />
        </router-link>
        <router-link :to="{name: 'cell-groups-reports', params: {id: $route.params.id}}"
                     :class="$route.name == 'cell-groups-reports' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline  hover:text-blue">
          <span class="icon-list text-xl" />
        </router-link>
        <router-link :to="{name: 'cell-groups-timeline', params: {id: $route.params.id}}"
                     :class="$route.name == 'cell-groups-timeline' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline  hover:text-blue">
          <v-icon class="text-grey font-medium">list</v-icon>
        </router-link>
      </div>
    </div>
    <div class="w-full">
      <div class="flex flex-col justify-between mb-8 p-8">
        <div>
          <h1 class="font-normal text-blue-darkest">Members & Guests</h1>
          <div class="text-blue-darker text-base">Showing
            <span class="px-2 bg-blue-lighter rounded text-blue mx-1">8</span> out of 8
            <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
               @click.prevent="get(active ? false : true, guests)">{{ active ? 'active': 'inactive' }}</a>
            <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
               @click.prevent="get(active, guests ? false : true)">{{ guests ? 'guests': 'members' }}</a>
          </div>
        </div>
      </div>
      <div class="px-8 w-full">
        <v-data-table :headers="headers"
                      :items="members"
                      :loading="loading"
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <td class="text-sm flex items-center my-2">
              <img :src=" props.item.profile_picture ? props.item.profile_picture : require('@/assets/images/avatar.png')"
                   class="h-10 w-10 rounded-full bg-grey mr-3">
              {{ props.item.name }}
            </td>
            <td class="text-sm text-blue">{{ props.item.email }}</td>
            <td class="text-sm">{{ props.item.member_role }}</td>
            <td class="text-sm">{{ props.item.phone }}</td>
            <td class="text-center">
              <v-menu :nudge-bottom="10"
                      origin="center center"
                      transition="scale-transition"
                      offset-y
                      bottom>
                <v-btn slot="activator"
                       flat
                       icon
                       class="no-outline">
                  <v-icon class="text-grey">info_outline</v-icon>
                </v-btn>
                <div>
                  <ul class="p-3 list-reset heebo bg-white w-48">
                    <!-- <li class="my-2 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                         @click="profile(props.item)">
                        <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                        Profile
                      </a>
                    </li>
                    <li class="my-2 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                        <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                        Edit
                      </a>
                    </li>
                    <li class="my-2 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                        <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                        Delete
                      </a>
                    </li> -->
                  </ul>
                </div>
              </v-menu>
            </td>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "CellGroupsMembers",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Cell Group Members & Guests",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Member", value: "name" },
        { text: "Email", value: "email" },
        { text: "Role", value: "member_role" },
        { text: "Phone", value: "phone" },
        { text: "", value: "details", align: "center" }
      ],
      members: [],
      active: true,
      guests: false,
      loading: true
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get(true, false);
  },
  methods: {
    async get(active, guests) {
      this.loading = true;
      try {
        const { data } = await window.axios.post("cells/members-list", {
          rol_id: this.user.role,
          cell_id: this.$route.params.id,
          active: active ? "1" : "0",
          filter_members: guests ? "0" : "1",
          user_id: this.user.user_id
        });
        data.response ? (this.members = data.response) : (this.members = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.guests = guests;
        this.loading = false;
      }
    }
  }
};
</script>
