<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex flex-col mb-8">
        <div class="flex justify-between">
          <div class="flex items-center">
            <h1 class="font-normal text-blue-darkest">Districts</h1>
            <p class="mx-3 flex items-center text-sm">
              <v-icon small
                      class="text-blue-darker mx-2">home</v-icon> -
              <span class="mx-2">Groups</span> -
              <span class="mx-2">Districts</span>
            </p>
          </div>
          <div>
            <button class="rounded py-2 px-4 text-white flex items-center justify-center no-outline bg-gradient hover:bg-blue-dark">
              <v-icon color="white"
                      small
                      class="mr-4">add</v-icon>
              <span class="font-medium text-base tracking-wide sahdow">Invite</span>
            </button>
          </div>
        </div>
        <div class="text-blue-darker text-base">Showing
          <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ districts.length }}</span> out of {{ districts.length }}
          <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
             @click.prevent="get(active ? false : true)">{{ active ? 'active': 'inactive' }}</a>
          <span class="mx-1">districts</span>
        </div>
      </div>
      <div>
        <div class="mb-3">
          <div class="flex justify-between">
            <div class="flex w-full justify-end">
              <input v-model="search"
                     type="text"
                     class="form-input bg-white w-1/4"
                     placeholder="Search by Keywords">
            </div>
          </div>
        </div>
        <v-data-table :headers="headers"
                      :items="districts"
                      :loading="loading"
                      :search="search"
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <tr class="group">
              <td :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                  class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px">
                <span class="-ml-2 w-8">
                  <v-tooltip right
                             color="red">
                    <v-icon v-if="props.item.is_complete == 0"
                            slot="activator"
                            color="red"
                            class="m-0 p-0 cursor-pointer">info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div class="mx-1 bg-purple text-white h-8 w-8 flex items-center justify-center rounded-full">D{{ props.item.district_code }}</div>
                <span class="mx-1">D{{ props.item.district_code }}</span>
              </td>
              <td class="text-base text-blue">{{ props.item.district_pastor ? props.item.district_pastor : '--' }}</td>
              <td class="text-center text-base text-blue">{{ props.item.zones.padStart(2, '0') }}</td>
              <td class="text-center text-base text-blue">{{ props.item.sectors.padStart(2, '0') }}</td>
              <td class="text-center text-base text-blue">{{ props.item.cells.padStart(2, '0') }}</td>
              <td class="text-center text-base text-blue">{{ props.item.members.padStart(2, '0') }}</td>
              <td class="text-center">
                <v-menu :nudge-bottom="10"
                        origin="center center"
                        transition="scale-transition"
                        offset-y
                        bottom>
                  <v-btn slot="activator"
                         flat
                         icon
                         color="grey"
                         class="no-outline">
                    <v-icon>info_outline</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                           @click="profile(props.item)">
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                          Profile
                        </a>
                      </li>
                      <li class="my-2 group">
                        <router-link :to="{ name: 'districts-edit', params: { id: props.item.district_id } }"
                                     class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </router-link>
                      </li>
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li>
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Districts",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Districts",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "District Id", value: "zone_code" },
        { text: "District Pastor", value: "district_pastor" },
        { text: "Zones", value: "zones", align: "center" },
        { text: "Sectors", value: "sectors", align: "center" },
        { text: "Cells", value: "cells", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      districts: [],
      active: true,
      loading: true,
      search: ""
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get(true);
  },
  methods: {
    async get(active) {
      try {
        const { data } = await window.axios.post("districts/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: active ? "1" : "0"
        });
        if (data.response) {
          this.districts = data.response;
        } else {
          this.districts = [];
        }
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    profile(group) {
      this.$router.push({
        name: "districts-profile",
        params: { id: group.district_id }
      });
    }
  }
};
</script>
