<template>
  <auth-card :width="430">
    <template slot="header">
      <div class="text-center">
        <img
          src="@/assets/images/irest.png"
          alt="Church Logo"
          class="rounded-full -mt-100 h-32 w-32 mb-3"
        >
        <h1 class="font-normal heebo text-2xl text-blue-darker leading-none">Nueva contrasea</h1>
        <p class="text-sm text-grey heebo">Ingresa tu nueva contrasea</p>
      </div>
    </template>
    <template slot="form">
      <ValidationObserver ref="observer">
        <form
          slot-scope="{ invalid }"
          autocomplete="off"
          class="px-4 mb-1"
          @submit.prevent="submit"
        >
          <div class="mb-4">
            <label for="password" class="text-grey text-sm">Nueva Contrasea</label>
            <ValidationProvider
              name="contrasea"
              vid="password"
              rules="required|min:8|verify_password"
            >
              <template slot-scope="{ errors, valid }">
                <v-text-field
                  id="password"
                  :error-messages="errors"
                  :success="valid"
                  :type="show_password ? 'text' : 'password'"
                  v-model="form.password"
                  name="password"
                  class="m-0 p-0"
                  data-vv-name="password"
                  required
                >
                  <template slot="append">
                    <div class="flex items-center m-0 p-0">
                      <v-icon v-if="errors.length" color="error" class="m-0 p-0">clear</v-icon>
                      <v-icon v-else-if="valid" color="success" class="m-0 p-0">check</v-icon>
                      <v-btn flat icon class="m-0 p-0" @click="show_password = !show_password">
                        <v-icon
                          color="blue-grey lighten-3"
                        >{{ show_password ? 'visibility_off' : 'visibility' }}</v-icon>
                      </v-btn>
                    </div>
                  </template>
                </v-text-field>
              </template>
            </ValidationProvider>
          </div>
          <div class="mb-8">
            <label for="confirm_password" class="text-grey text-sm">Verificar contrasea</label>
            <ValidationProvider
              name="verificar contrasea"
              rules="required|min:8|confirmed:password"
            >
              <template slot-scope="{ errors, valid }">
                <v-text-field
                  id="password_confirmation"
                  :error-messages="errors"
                  :success="valid"
                  :type="show_confirm_password ? 'text' : 'password'"
                  v-model="form.password_confirmation"
                  name="password_confirmation"
                  class="m-0 p-0"
                  required
                >
                  <template slot="append">
                    <div class="flex items-center m-0 p-0">
                      <v-icon v-if="errors.length" color="error" class="m-0 p-0">clear</v-icon>
                      <v-icon v-else-if="valid" color="success" class="m-0 p-0">check</v-icon>
                      <v-btn
                        flat
                        icon
                        class="m-0 p-0"
                        @click="show_confirm_password = !show_confirm_password"
                      >
                        <v-icon
                          color="blue-grey lighten-3"
                        >{{ show_confirm_password ? 'visibility_off' : 'visibility' }}</v-icon>
                      </v-btn>
                    </div>
                  </template>
                </v-text-field>
              </template>
            </ValidationProvider>
            <p
              class="text-grey leading-normal text-sm"
            >La contrasea debe llevar por lo menos una letra mayscula, un nmero y mnimo 8 caracteres.</p>
          </div>
          <div class="flex justify-center">
            <div>
              <button
                :class="[ invalid ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                :disabled="invalid"
                type="submit"
                class="w-48 rounded py-3 text-white flex items-center justify-center no-outline"
              >
                <template v-if="!loading">
                  <span class="font-medium text-base heebo tracking-wide">Aceptar</span>
                </template>
                <template v-else>
                  <p
                    class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                  >Cargando...</p>
                </template>
              </button>
            </div>
          </div>
        </form>
      </ValidationObserver>
      <v-dialog v-model="dialog" max-width="500">
        <div class="bg-white px-6 pt-10 pb-6 rounded-lg text-sm">
          <div>
            <p class="text-center text-3xl font-bold mb-4 text-blue-darker">Contrasea Actualizada!</p>
            <p class="text-center">Se ha actualizado correctamente tu contrasea</p>
            <div class="text-center mt-4">
              <v-icon class="bg-green p-3 rounded-full text-white font-bold">check</v-icon>
            </div>
          </div>
        </div>
      </v-dialog>
    </template>
  </auth-card>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Cookies from "js-cookie";
export default {
  name: "NewPassword",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Nueva Contrasea",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      dialog: false,
      loading: false,
      show_password: false,
      show_confirm_password: false,
      form: {
        email: null,
        password: null,
        password_confirmation: null
      },
      error: {}
    };
  },
  watch: {
    dialog: function(value) {
      if (value) {
        this.closeDialog();
      }
    }
  },
  mounted() {
    this.setEmail();
  },
  methods: {
    async submit() {
      this.loading = true;
      try {
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          await window.axios.post("add-new-password", this.form);
          this.dialog = true;
        }
      } catch (e) {
        this.error = e;
      } finally {
        this.loading = false;
      }
    },
    closeDialog() {
      setTimeout(() => this.$router.replace({ name: "signin" }), 5000);
    },
    setEmail() {
      if ((this.form.email = this.$route.params.email)) {
        Cookies.set("new-password-email", this.form.email);
      } else {
        this.form.email = Cookies.get("new-password-email");
      }
    }
  }
};
</script>
