<template>
  <div class="flex w-full">
    <div class="items-center p-4">
      <img
        :src="timeline.profile_picture ? timeline.profile_picture : require('@/assets/images/avatar.png')"
        class="rounded-full h-12 w-12 mr-3 object-cover"
      >
    </div>
    <div class="w-full">
      <div class="flex items-center justify-between">
        <p class="text-base text-grey">
          <span class="text-blue">{{ timeline.full_name }}</span> comento
        </p>
        <v-menu
          :nudge-bottom="10"
          :nudge-left="50"
          origin="center center"
          transition="scale-transition"
          offset-y
          bottom
        >
          <v-btn
            v-if="user.user_id == timeline.id_user_commented"
            slot="activator"
            flat
            icon
            color="grey"
            class="no-outline"
          >
            <v-icon>more_vert</v-icon>
          </v-btn>
          <div>
            <ul class="p-3 list-reset heebo bg-white w-36">
              <li class="mb-3 group">
                <a
                  class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                  @click.prevent="edit = true"
                >
                  <span class="mx-2">Editar</span>
                </a>
              </li>
              <li class="group">
                <a
                  class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                  @click.prevent="destroy"
                >
                  <span class="mx-2">Eliminar</span>
                </a>
              </li>
            </ul>
          </div>
        </v-menu>
      </div>
      <div class="h-auto flex flex-col">
        <p
          v-if="!edit"
          class="text-blue-darker leading-normal tracking-normal text-base"
        >{{ timeline.comment }}</p>
        <div v-else class="w-full pr-8 mb-3">
          <textarea
            v-model="edit_comment"
            name="comment"
            class="form-input w-full border-grey border-solid"
          />
          <div class="flex">
            <div>
              <button
                :disabled="edit_comment == ''"
                :class="[ edit_comment == '' ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                class="rounded py-2 w-32 text-white heebo flex items-center justify-center no-outline"
                @click.prevent="update"
              >
                <template v-if="!loading_update">
                  <span class="font-semibold tracking-wide text-sm">Actualizar</span>
                </template>
                <template v-else>
                  <p
                    class="text-white text-sm font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                  >Actualizando...</p>
                </template>
              </button>
            </div>
            <div>
              <button
                class="rounded py-2 w-32 text-grey font-bold flex items-center justify-center no-outline border border-solid border-grey mx-1"
                @click="edit = false"
              >
                <span class="font-semibold tracking-wide text-sm">Cancelar</span>
              </button>
            </div>
          </div>
        </div>
        <p class="text-grey-light text-sm">{{ formatDate(timeline.created_at) }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import EventBus from "@/bus";
export default {
  name: "TimelineComment",
  props: {
    timeline: {
      type: Object,
      required: true
    },
    user: {
      type: Object,
      required: true
    },
    type: {
      type: String,
      default: "member"
    }
  },
  data() {
    return {
      edit: false,
      loading_update: false,
      edit_comment: null
    };
  },
  mounted() {
    this.edit_comment = this.timeline.comment;
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    },
    async destroy() {
      try {
        await window.axios.post("general/timeline/delete-comments", {
          owner_id: this.user.member_id
            ? this.user.member_id
            : this.user.user_id,
          church_id: 1,
          role_id: this.timeline.role_user_commented,
          type: this.type == "member" ? "user" : this.type,
          commented_id:
            this.type == "member"
              ? this.timeline.id_user_commented
              : this.$route.params.id,
          comment_id:
            this.type == "member"
              ? this.timeline.id_comment_user_timeline
              : this.timeline.id_comment_group_timeline
        });
        EventBus.$emit("delete-comment");
      } catch (error) {
        return error;
      }
    },
    async update() {
      this.loading_update = true;
      try {
        await window.axios.post("general/timeline/edit-comments", {
          owner_id: this.user.user_id,
          role_id: this.type == "member" ? this.user.role_id : this.user.role,
          church_id: this.user.church_id,
          type: this.type == "member" ? "user" : this.type,
          commented_id:
            this.type == "member"
              ? this.timeline.id_user_commented
              : this.$route.params.id,
          comment_id:
            this.type == "member"
              ? this.timeline.id_comment_user_timeline
              : this.timeline.id_comment_group_timeline,
          comment: this.edit_comment
        });
        this.timeline.comment = this.edit_comment;
        this.edit = false;
        EventBus.$emit("update-comment");
      } catch (error) {
        console.log(error);
        return error;
      } finally {
        this.loading_update = false;
      }
    }
  }
};
</script>
