<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex justify-between items-center mb-8">
        <div>
          <div class="flex items-center">
            <h1 class="font-normal text-blue-darkest">Reporte</h1>
          </div>
          <div class="text-blue-darker text-base">
            Mostrando
            <span class="px-2 bg-blue-lightest rounded text-blue mx-1">
              <select v-model="pagination.rowsPerPage" name="show_items" class="outline-none">
                <option value="5">5</option>
                <option value="10">10</option>
                <option value="20">20</option>
              </select>
            </span>
            de {{ reports.length }} reportes
          </div>
        </div>
        <div>
          <button
            class="rounded py-1 px-3 text-white flex items-center justify-center no-outline bg-gradient hover:bg-blue-dark"
            @click.prevent="createReport()"
          >
            <v-icon color="white" class="mr-2" medium>add</v-icon>
            <span class="font-medium text-base">Nuevo Reporte</span>
          </button>
        </div>
      </div>
      <div class="flex justify-end mb-4">
        <button
          class="bg-white p-2 rounded border border-solid border-grey-light hover:opacity-75 mx-4"
          @click="download"
        >
          <v-icon color="blue">get_app</v-icon>
        </button>
        <v-menu
          ref="menu"
          :close-on-content-click="false"
          v-model="menu"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
          <input
            slot="activator"
            v-model="months"
            type="text"
            class="bg-white rounded border border-solid border-grey-light w-64 px-3 h-12 outline-none"
            placeholder="Jan, 2017 - Dec, 2017"
          >
          <v-date-picker
            v-model="months"
            :readonly="disabledPicker"
            :show-current="false"
            locale="es"
            type="month"
            multiple
            no-title
          >
            <v-btn flat color="primary" @click="reset()">Reiniciar</v-btn>
            <v-btn flat color="primary" @click="getReports()">Buscar</v-btn>
          </v-date-picker>
        </v-menu>
      </div>
      <div>
        <v-data-table
          :headers="headers"
          :items="reports"
          :loading="loading"
          :search="search"
          :pagination.sync="pagination"
          hide-actions
          no-data-text="No hay resultados"
          class="elevation-0 border border-grey-lighter rounded"
        >
          <template slot="headerCell" slot-scope="props">
            <span class="uppercase tracking-wide text-grey">{{ props.header.text }}</span>
          </template>
          <template v-if="props.item.is_complete != 0" slot="items" slot-scope="props">
            <tr class="group">
              <td class="flex items-center text-blue-darker text-base">{{ props.item.name }}</td>
              <td class="text-base text-blue.darker">{{ formatDate(props.item.date) }}</td>
              <td class="text-base text-blue-darker flex items-center">
                <img
                  :src="props.item.profile_picture ? props.item.profile_picture : require('@/assets/images/avatar.png')"
                  class="rounded-full w-8 h-8 mr-2"
                  style="object-fit: cover"
                >
                {{ props.item.created_by }}
              </td>
              <td class="text-center">
                <v-menu
                  :nudge-bottom="10"
                  :nudge-right="75"
                  origin="center center"
                  transition="scale-transition"
                  offset-y
                  bottom
                >
                  <v-btn slot="activator" flat icon class="text-grey">
                    <v-icon>{{ props.item.principal ? 'more_vert' : 'info_outline' }}</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li v-if="props.item.principal" class="mb-3 group">
                        <a
                          class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                          @click.prevent="createReport(props.item)"
                        >
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          <span class="mx-3">Editar</span>
                        </a>
                      </li>
                      <li class="group">
                        <a
                          class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                          @click.prevent="detailReport(props.item)"
                        >
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                          <span class="mx-3">Detalle</span>
                        </a>
                      </li>
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
        <div v-if="pages != 0" class="text-right pt-2">
          <v-pagination v-model="pagination.page" :length="pages"/>
        </div>
      </div>
    </div>
    <dialog-detail-report/>
  </div>
</template>

<script>
import { mapState } from "vuex";
import EventBus from "@/bus";
export default {
  name: "ReportsLeaders",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Reportes",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "ID", value: "id" },
        { text: "Fecha", value: "date" },
        { text: "Creado Por", value: "leader" },
        { text: "", value: "details", align: "center" }
      ],
      reports: [],
      loading: true,
      search: "",
      last_report: {
        is_complete: 0
      },
      menu: null,
      months: [],
      pagination: {
        totalItems: 0,
        rowsPerPage: 5
      }
    };
  },
  computed: {
    ...mapState({
      church: state => state.auth.church.info,
      user: state => state.auth.user
    }),
    canCreateReport() {
      const last_report = this.reports.find(r => r.principal == 1);
      if (last_report.principal) {
        return false;
      }
      return true;
    },
    disabledPicker() {
      return this.months.length == 2;
    },
    pages() {
      if (
        this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) {
        return 0;
      }

      return Math.ceil(
        this.pagination.totalItems / this.pagination.rowsPerPage
      );
    }
  },
  mounted() {
    this.getReports();
    EventBus.$on("saved-report", () => {
      this.getReports();
    });
  },
  methods: {
    report(value, report_id) {
      EventBus.$emit("show-dialog-report", {
        value: value,
        report_id: report_id
      });
    },
    editReport(report) {
      EventBus.$emit("show-dialog-report-edit", report);
    },
    async getReports() {
      try {
        this.menu = false;
        let request = {};
        if (this.months) {
          request = {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            parent_id: this.user.group.parent_id,
            start_date: this.months[0],
            end_date: this.months[1]
          };
        } else {
          request = {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            parent_id: this.user.group.parent_id
          };
        }
        const { data } = await window.axios.get("reports/list", {
          params: request
        });
        this.reports = data.response ? data.response : [];
        this.last_report = this.reports.find(r => r.principal == 1);
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
      }
    },
    async createReport(report = null) {
      try {
        const { data } = await window.axios.get("reports/detail", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            report_id: report ? report.report_id : this.last_report.report_id
          }
        });
        this.report(data.response, this.last_report.report_id);
      } catch (error) {
        console.log(error);
      }
    },
    detailReport(report) {
      EventBus.$emit("show-detail-report", report);
    },
    async download() {
      const { data } = await window.axios.post(
        "download/report-list",
        {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.user.church_id,
          parent_id: this.user.group.parent_id
        },
        {
          responseType: "blob",
          headers: {
            Accept: "application/pdf"
          }
        }
      );
      //Build a URL from the file
      const file = new Blob([data], { type: "application/pdf" });
      const fileURL = URL.createObjectURL(file);
      // //Open the URL on new Window
      window.open(fileURL);
    },
    reset() {
      this.months = [];
      this.getReports();
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    }
  }
};
</script>
