import Vue from "vue";

Vue.directive(
  "senior-pastor",
  (el, bind) =>
    bind.value.user_role !== "Senior Pastor" ? el.classList.add("hidden") : ""
);

Vue.directive(
  "district-pastor",
  (el, bind) =>
    bind.value.user_role !== "District Pastor" ? el.classList.add("hidden") : ""
);

Vue.directive(
  "zone-pastor",
  (el, bind) =>
    bind.value.user_role !== "Zone Pastor" ? el.classList.add("hidden") : ""
);

Vue.directive(
  "supervisor",
  (el, bind) =>
    bind.value.user_role !== "Supervisor" ? el.classList.add("hidden") : ""
);

Vue.directive(
  "leader",
  (el, bind) =>
    bind.value.user_role !== "Leader" ? el.classList.add("hidden") : ""
);
