<script>
import { mapState } from "vuex";
export default {
  name: "SectorsProfile",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Sectors Profile",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      tab: null,
      sector: [],
      map: {
        center: { lat: 45.508, lng: -73.587 },
        marker: { lat: 45.508, lng: -73.587 },
        place: [],
        currentPlace: null
      }
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("sectors/profile", {
          sector_id: this.$route.params.id,
          user_id: this.user.user_id,
          role_id: this.user.role,
          type_user: this.user.type_user
        });
        this.sector = data.response;
      } catch (error) {
        return error;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    }
  }
};
</script>
