<template>
  <auth-card :width="430" class="relative">
    <template slot="header">
      <div class="absolute pin-t pin-l">
        <v-btn :to="{ name: 'welcome' }" flat icon exact>
          <v-icon class="text-grey-light" medium>keyboard_arrow_left</v-icon>
        </v-btn>
      </div>
      <div class="text-center">
        <img
          src="@/assets/images/irest.png"
          alt="Church Logo"
          class="rounded-full -mt-100 h-32 w-32"
        >
        <h1 class="font-normal heebo text-3.5xl text-blue-darker">IRest Reseda</h1>
        <p class="text-base text-grey heebo">{{ hostname }}</p>
      </div>
    </template>
    <template slot="form" class="relative">
      <ValidationObserver ref="observer">
        <form
          slot-scope="{ invalid }"
          autocomplete="off"
          class="mb-3 px-4"
          @submit.prevent="submit"
        >
          <div class="mb-2">
            <label for="email" class="text-left text-sm text-grey">Correo Electrnico</label>
            <ValidationProvider name="correo" rules="required|email">
              <template slot-scope="{ errors, valid }">
                <v-text-field
                  id="email"
                  :error-messages="customError('wrong_mail') || errors"
                  :success="valid"
                  v-model="form.email"
                  name="email"
                  placeholder="juan@email.com"
                  class="m-0 p-0"
                  @keyup="clean"
                >
                  <v-fade-transition slot="append">
                    <v-icon v-if="errors.length || customError('wrong_mail')" color="error">clear</v-icon>
                    <v-icon v-else-if="valid" color="success">check</v-icon>
                  </v-fade-transition>
                </v-text-field>
              </template>
            </ValidationProvider>
          </div>
          <div class="mb-4">
            <label for="password" class="text-left text-sm text-grey">Contrasea</label>
            <ValidationProvider name="contrasea" rules="required">
              <template slot-scope="{ errors, valid }">
                <v-text-field
                  id="password"
                  :error-messages="customError('wrong_password') || errors"
                  :success="valid"
                  :type="show_password ? 'text' : 'password'"
                  v-model="form.password"
                  name="password"
                  class="m-0 p-0"
                  @keyup="clean"
                >
                  <template slot="append">
                    <div class="flex items-center m-0 p-0">
                      <v-icon
                        v-if="errors.length || customError('wrong_password')"
                        color="error"
                        class="m-0 p-0"
                      >clear</v-icon>
                      <v-icon v-else-if="valid" color="success" class="m-0 p-0">check</v-icon>
                      <v-btn flat icon class="m-0 p-0" @click="show_password = !show_password">
                        <v-icon
                          color="blue-grey lighten-3"
                        >{{ show_password ? 'visibility_off' : 'visibility' }}</v-icon>
                      </v-btn>
                    </div>
                  </template>
                </v-text-field>
              </template>
            </ValidationProvider>
          </div>
          <button
            :class="[ invalid ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
            :disabled="invalid"
            type="submit"
            class="w-full rounded py-3 text-white flex items-center justify-center no-outline"
          >
            <template v-if="!loading">
              <span class="font-medium text-base heebo tracking-wide">Iniciar sesin</span>
            </template>
            <template v-else>
              <p
                class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
              >Cargando...</p>
            </template>
          </button>
          <div class="mt-3 flex items-center justify-between">
            <div>
              <p-check v-model="form.remember" class="p-svg p-curve p-bigger" color="primary">
                <svg slot="extra" class="svg svg-icon p-1" viewBox="0 0 20 20">
                  <path
                    d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                    style="stroke: white;fill:white"
                  />
                </svg>
                <span class="text-grey text-sm">Recuerdame.</span>
              </p-check>
            </div>
            <div>
              <router-link
                :to="{ name: 'reset' }"
                class="no-underline text-blue text-sm hover:underline"
              >Se te olvid tu contrasea?</router-link>
            </div>
          </div>
        </form>
      </ValidationObserver>
    </template>
  </auth-card>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions } from "vuex";
import { ValidationProvider, ValidationObserver } from "vee-validate";
export default {
  name: "SignIn",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Iniciar Sesin",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      show_password: false,
      show: false,
      loading: false,
      form: {
        email: null,
        password: null,
        remember: false
      },
      error: {},
      hostname: window.location.hostname
    };
  },
  methods: {
    ...mapActions({
      login: "auth/login"
    }),
    async submit() {
      this.loading = true;
      try {
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          await this.login(this.form);
          this.$router.replace({
            name: "dashboard"
          });
        }
      } catch (e) {
        this.error = e;
        if (this.error.type == "not_assigned") {
          return this.$router.replace({ name: "not-assign-errors" });
        }
      } finally {
        this.loading = false;
      }
    },
    clean() {
      this.error = {};
    },
    customError(type) {
      if (typeof this.error.type !== "undefined") {
        if (this.error.type === type) {
          return this.error.message;
        }
      }
    }
  }
};
</script>
