<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex flex-col mb-8">
        <div class="flex items-center">
          <h1 class="font-normal text-blue-darkest">Sectors</h1>
          <p class="mx-3 flex items-center text-sm">
            <v-icon small
                    class="text-blue-darker">home</v-icon> - Groups - Sectors</p>
        </div>
        <div class="text-blue-darker text-base">Showing
          <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ sectors.length }}</span> out of {{ sectors.length }}
          <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
             @click.prevent="get(active ? false : true)">{{ active ? 'active': 'inactive' }}</a>
          <span class="mx-1">sectors</span>
        </div>
      </div>
      <div>
        <div class="mb-3">
          <p class="text-grey tracking-wide mb-1">Filter by</p>
          <div class="flex justify-between">
            <div v-if="groups" class="flex">
              <v-select :options="groups.districts"
                        :searchable="false"
                        :show-labels="false"
                        v-model="district"
                        track-by="district_code"
                        label="district_code"
                        placeholder="District"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white"
                        name="district">
                <template slot="singleLabel" slot-scope="props">
                  <span>D{{ props.option.district_code }}</span>
                </template>
                <template slot="option" slot-scope="props">
                  <span>D{{ props.option.district_code }}</span>
                </template>
              </v-select>
              <v-select :options="groups.zones"
                        :searchable="false"
                        :show-labels="false"
                        v-model="zone"
                        track-by="zone_code"
                        label="zone_code"
                        placeholder="Zone"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white mx-3"
                        name="zone">
                <template slot="singleLabel" slot-scope="props">
                  <span>Z{{ props.option.zone_code }}</span>
                </template>
                <template slot="option" slot-scope="props">
                  <span>Z{{ props.option.zone_code }}</span>
                </template>
              </v-select>
              <button type="button"
                      class="mx-2 bg-blue hover:bg-blue-dark text-white px-4 text-xl rounded outline-none"
                      @click="filteredSectors">
                <v-tooltip top>
                  <span slot="activator" class="icon-search"/>
                  <span>Search</span>
                </v-tooltip>
              </button>
              <button type="button"
                      class="mx-2 bg-blue hover:bg-blue-dark text-white px-4 text-xl rounded outline-none"
                      @click="get(true)">
                <v-tooltip top>
                  <span slot="activator" class="icon-trash"/>
                  <span>Reset</span>
                </v-tooltip>
              </button>
            </div>
            <div class="flex w-full justify-end">
              <input v-model="search"
                     type="text"
                     class="form-input bg-white w-1/3"
                     placeholder="Search by Keywords">
            </div>
          </div>
        </div>
        <v-data-table :headers="headers"
                      :items="sectors"
                      :loading="loading"
                      :search="search"
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <tr class="group">
              <td :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                  class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px">
                <span class="-ml-2 w-8">
                  <v-tooltip right
                             color="red">
                    <v-icon v-if="props.item.is_complete == 0"
                            slot="activator"
                            color="red"
                            class="m-0 p-0 cursor-pointer">info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div class="mx-1 bg-blue text-white h-8 w-8 flex items-center justify-center rounded-full">S{{ props.item.sector_code }}</div>
                <span class="mx-1">D{{ props.item.district_code }}</span>
                <span class="mx-1">Z{{ props.item.zone_code }}</span>
                <span class="mx-1">S{{ props.item.sector_code }}</span>
              </td>
              <td class="text-base text-blue">{{ props.item.supervisor ? props.item.supervisor : '--' }}</td>
              <td class="text-center text-base text-blue">{{ props.item.cells.padStart(2, "0") }}</td>
              <td class="text-center text-base text-blue">{{ props.item.members.padStart(2, "0") }}</td>
              <td class="text-center">
                <v-menu :nudge-bottom="10"
                        origin="center center"
                        transition="scale-transition"
                        offset-y
                        bottom>
                  <v-btn slot="activator"
                         flat
                         icon
                         color="grey"
                         class="no-outline">
                    <v-icon>info_outline</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                           @click="profile(props.item)">
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                          Profile
                        </a>
                      </li>
                      <li class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </a>
                      </li>
                      <li class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li>
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Sectors",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Sectors",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Sector Id", value: "sector_code" },
        { text: "Supervisor", value: "supervisor" },
        { text: "Cells", value: "cells", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      sectors: [],
      district: null,
      zone: null,
      active: true,
      loading: true,
      search: "",
      groups: {
        districts: [],
        zones: []
      }
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user"
    })
  },
  mounted() {
    this.get(true);
    this.getGroups();
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.post("sectors/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: active ? "1" : "0"
        });
        data.response ? (this.sectors = data.response) : (this.sectors = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    profile(group) {
      this.$router.push({
        name: "sectors-profile",
        params: { id: group.id }
      });
    },
    async getGroups() {
      try {
        const { data } = await window.axios.post("general/full-list-groups", {
          user_id: this.user.user_id,
          role_id: this.user.role
        });
        this.groups = data.response;
      } catch (error) {
        return error;
      }
    },
    async filteredSectors() {
      this.loading = true;
      try {
        await this.get(true);
        this.sectors = this.sectors.filter(
          s =>
            (this.district
              ? s.district_code == this.district.district_code
              : true) && (this.zone ? s.zone_code == this.zone.zone_code : true)
        );
      } catch (error) {
        return error;
      }
    }
  }
};
</script>
