<script>
import { mapState } from "vuex";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  name: "CellGroupsProfile",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Cell Group Profile",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      cell: {
        since: null
      },
      tab: null,
      map: {
        center: { lat: 45.508, lng: -73.587 }
      }
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("cells/profile", {
          cell_id: this.$route.params.id,
          user_id: this.user.user_id,
          church_id: 1,
          role_id: this.user.role
        });
        this.cell = data.response;
      } catch (error) {
        return error;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    download() {
      const element = document.getElementById("cell-profile");
      html2canvas(element).then(canvas => {
        const imgData = canvas.toDataURL("image/png");
        const pdf = new jsPDF("p", "pt", "a4", true);
        var width = pdf.internal.pageSize.getWidth();
        // var height = pdf.internal.pageSize.getHeight();
        pdf.addImage(imgData, "PNG", 0, 0, width, 600);
        pdf.save("perfil-celula.pdf");
      });
    },
    setRole(role) {
      switch (role) {
        case "Leader":
          return "Lder";
        case "Host":
          return "Anfitrin";
        default:
          return "";
      }
    }
  }
};
</script>
