<template>
  <auth-card>
    <template slot="title">
      <p class="text-3.5xl tracking-normal">Check your email</p>
      <p class="text-base text-grey leading-normal">Weve sent a six-digit confirmation code to
      <strong>you@example.com</strong>. It will expire shortly, so enter your code soon.</p>
    </template>
    <template slot="form">
      <p class="text-grey text-left mb-1">Your
        <strong>confirmation code</strong>
      </p>
      <form action=""
            class="flex flex-col mb-6"
            autocomplete="off">
        <div class="flex fe items-center justify-between mb-6">
          <input id="code1"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center text-grey font-normal text-2xl rounded"
                 maxlength="1"
                 @keyup="key($event, 0)">
          <input id="code2"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center font-normal text-2xl text-grey rounded"
                 maxlength="1"
                 @keyup="key($event, 1)">
          <input id="code3"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center font-normal text-2xl text-grey rounded"
                 maxlength="1"
                 @keyup="key($event, 2)">
          <span class="border w-4 border-grey" />
          <input id="code4"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center font-normal text-2xl text-grey rounded"
                 maxlength="1"
                 @keyup="key($event, 3)">
          <input id="code5"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center font-normal text-2xl text-grey rounded"
                 maxlength="1"
                 @keyup="key($event, 4)">
          <input id="code6"
                 type="text"
                 class="border border-grey border-solid h-10 w-10 text-center font-normal text-2xl text-grey rounded"
                 maxlength="1"
                 @keyup="key($event, 5)">
        </div>
        <template v-if="show">
          <transition v-if="!error"
                      name="fade">
            <p class="text-purple text-sm font-normal heebo flex items-center justify-center animate-loading">
              <svg class="fill-current text-purple rounded-full h-4 w-4 p-px mr-2"
                   width="8px"
                   height="6px"
                   viewBox="0 0 8 6"
                   version="1.1"
                   xmlns="http://www.w3.org/2000/svg"
                   xmlns:xlink="http://www.w3.org/1999/xlink">
                <g id="C+-Login"
                   stroke="none"
                   stroke-width="1"
                   fill="none"
                   fill-rule="evenodd">
                  <g id="1.2.2-Sign-Up-(1st)---Code---Verifying"
                     transform="translate(-684.000000, -626.000000)"
                     fill="#AB7DF6"
                     stroke="#AB7DF6">
                    <g id="Icon-Copy-2"
                       transform="translate(680.000000, 620.000000)">
                      <g id="Group-2"
                         transform="translate(0.000000, 1.000000)">
                        <polygon id="Icon"
                                 points="10.6515152 5.77777778 7.17171717 9.29353234 5.79292929 7.90049751 5.33333333 8.36484245 7.17171717 10.2222222 11.1111111 6.24212272" />
                      </g>
                    </g>
                  </g>
                </g>
              </svg>
              Verifyng...
            </p>
          </transition>
          <small v-else
                 class="text-error flex justify-center">{{ error }}</small>
        </template>
      </form>
    </template>
    <template slot="footer">
      <p class="text-sm text-grey heebo mb-6">Your church already have C+?
        <router-link :to="{ name: 'signin-url' }"
                     class="no-underline text-blue hover:underline font-normal">
          Sign In
        </router-link>
      </p>
    </template>
  </auth-card>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Code",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Code",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      code_user: [0, 0, 0, 0, 0, 0],
      confirmation_code: null,
      error: null
    };
  },
  computed: {
    ...mapGetters({
      code: "auth/code"
    })
  },
  methods: {
    ...mapActions({
      confirmation: "auth/confirmCode"
    }),
    key(e, index) {
      this.show = false;
      const target = e.target;
      if (e.code == "Backspace") {
        delete this.code_user[index];
        return;
      }
      if (target.value.length === target.maxLength) {
        this.code_user[index] = target.value;
        if (target.id == "code3") {
          document.getElementById("code4").focus();
          return;
        }
        if (target.id == "code6") {
          this.confirmation_code = null;
          this.confirmation_code = this.code_user.join("");
          this.validateCode();
          this.show = true;
          return;
        }
        target.nextSibling.focus();
      }
    },
    async validateCode() {
      try {
        if (this.code == this.confirmation_code) {
          await this.confirmation();
          this.$router.replace({
            name: "profile"
          });
        } else {
          this.error = "the code does not match";
        }
      } catch (error) {
        this.show = false;
        this.error = error.message;
      }
    }
  }
};
</script>
