import * as types from "../mutation-types";

// state
export const state = {
  temporary: false,
  mini: true
};

export const mutations = {
  [types.UPDATE_MINI_STATE](state, payload) {
    state.mini = payload;
  },
  [types.UPDATE_TEMPORARY_STATE](state, payload) {
    state.temporary = payload;
  },
  [types.TOGGLE_MINI](state) {
    state.mini = !state.mini;
  },
  [types.TOGGLE_TEMPORARY](state) {
    state.temporary = !state.temporary;
    state.mini = true;
  }
};

export const actions = {
  updateMiniState({ commit }, payload) {
    try {
      commit(types.UPDATE_MINI_STATE, payload);
    } catch (e) {
      console.log(e);
      // throw error;
    }
  },
  updateTemporaryState({ commit }, payload) {
    try {
      commit(types.UPDATE_TEMPORARY_STATE, payload);
    } catch (e) {
      console.log(e);
      // throw error;
    }
  },
  toggleMini({ commit }) {
    try {
      commit(types.TOGGLE_MINI);
    } catch (e) {
      console.log(e);
      // throw error;
    }
  },
  toggleTemporary({ commit }) {
    try {
      commit(types.TOGGLE_TEMPORARY);
    } catch (e) {
      console.log(e);
      // throw error;
    }
  }
};
