<template>
  <div>
    <auth-card :width="430" class="relative">
      <template slot="header">
        <div class="absolute pin-t pin-l">
          <v-btn :to="{ name: 'signin' }" flat icon exact>
            <v-icon class="text-grey-light" medium>keyboard_arrow_left</v-icon>
          </v-btn>
        </div>
        <div class="text-center">
          <img src="@/assets/images/irest.png" alt class="rounded-full -mt-100 h-32 w-32 mb-3">
          <h1 class="font-normal heebo text-2xl text-blue-darker">Reiniciar Contrasea</h1>
          <p
            class="text-sm text-grey heebo"
          >Para reiniciar su contrasea, ingrese la direccin de correo electrnico que usa para iniciar sesin.</p>
        </div>
      </template>
      <template slot="form">
        <ValidationObserver ref="observer">
          <form slot-scope="{ invalid }" autocomplete="off" class="px-4" @submit.prevent="submit">
            <div class="mb-4">
              <label for="email" class="text-grey">Correo Electrnico</label>
              <ValidationProvider name="correo" rules="required|email">
                <template slot-scope="{ errors, valid }">
                  <v-text-field
                    id="email"
                    :error-messages="customError('wrong_mail') || errors"
                    :success="valid"
                    v-model="email"
                    name="email"
                    type="email"
                    class="m-0 p-0"
                    placeholder="juan@correo.com"
                    @keyup="clean"
                  >
                    <v-fade-transition slot="append">
                      <v-icon v-if="errors.length || customError('wrong_mail')" color="error">clear</v-icon>
                      <v-icon v-else-if="valid" color="success">check</v-icon>
                    </v-fade-transition>
                  </v-text-field>
                </template>
              </ValidationProvider>
            </div>
            <button
              :class="[ invalid || error.type ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="invalid || error.type"
              type="submit"
              class="w-full rounded py-3 text-white flex items-center justify-center no-outline"
            >
              <template v-if="!loading">
                <span class="font-medium text-base heebo tracking-wide">Enviar</span>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                >Cargando...</p>
              </template>
            </button>
            <div class="mt-6 text-center">
              <router-link
                :to="{ name: 'signin'}"
                class="text-blue text-sm flex flex-col"
              >Volver a Iniciar Sesin</router-link>
            </div>
          </form>
        </ValidationObserver>
      </template>
    </auth-card>
    <v-dialog v-model="dialog" width="488">
      <div class="bg-white px-6 pt-10 pb-6 rounded-lg text-sm">
        <div>
          <p class="text-center text-3xl font-bold text-blue-darker mb-4">El correo fue enviado!</p>
          <p class="text-center">
            Revisa tu correo
            <strong>{{email}}</strong>
          </p>
          <p class="text-center">para ver las instrucciones de como reinicar tu contrasea.</p>
          <div class="text-center mt-4">
            <v-icon class="bg-green p-3 rounded-full text-white font-bold">check</v-icon>
          </div>
        </div>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
import { ValidationProvider, ValidationObserver } from "vee-validate";
export default {
  name: "Reset",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Reiniciar Contrasea",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      dialog: false,
      loading: false,
      email: null,
      error: {}
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  watch: {
    dialog: function(value) {
      if (value) {
        this.closeDialog();
      }
    }
  },
  methods: {
    ...mapActions({
      reset: "auth/reset"
    }),
    async submit() {
      this.loading = true;
      try {
        const isValid = await this.$refs.observer.validate();
        if (isValid) {
          await this.reset({
            user_email: this.email
          });
          this.dialog = true;
        }
      } catch (e) {
        this.error = e;
      } finally {
        this.loading = false;
      }
    },
    closeDialog() {
      setTimeout(() => this.$router.replace({ name: "signin" }), 5000);
    },
    customError(type) {
      if (typeof this.error.type !== "undefined") {
        if (this.error.type === type) {
          console.log(this.error);
          return this.error.message;
        }
      }
    },
    clean() {
      this.error = {};
    }
  }
};
</script>
