<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "TransferCells",
  data() {
    return {
      show: false,
      showCellGroups: false,
      showLeaders: false,
      transfer_cell: {
        cell: null,
        target_district: null,
        target_zone: null,
        target_sector: null
      },
      transfer_leader: {
        leader: null,
        target_district: null,
        target_zone: null,
        target_sector: null
      },
      cells: [],
      districts: [],
      zones: [],
      sectors: [],
      users_transfer: []
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    EventBus.$on("show-transfer-cells", () => {
      this.show = !this.show;
    });
    this.getCells();
    this.getGroupsTransfer();
    this.getUsersTransfer();
  },
  methods: {
    async getCells() {
      const { data } = await window.axios.post("sectors/cell-groups-list", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        sector_id: this.user.group.parent_id,
        active: "1"
      });

      this.cells = data.response;
    },
    async getGroupsTransfer() {
      const { data } = await window.axios.post("groups/list-groups-target", {
        user_id: this.user.user_id,
        role_id: this.user.role
      });

      this.districts = data.response.districts;
      this.zones = data.response.zones;
      this.sectors = data.response.sectors;
    },
    async getUsersTransfer() {
      const { data } = await window.axios.post("groups/list-user-transfer", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        church_id: this.church.church_id,
        parent_id: this.user.group.parent_id
      });

      this.users_transfer = data.response ? data.response : [];
    },
    async transferCell() {
      await window.axios.post("sectors/transfer-cell", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        cell_id: this.transfer_cell.cell,
        sector_id: this.transfer_cell.target_sector.sector_id,
        sector_code: this.transfer_cell.target_sector.sector_code,
        district_id: this.transfer_cell.target_district.district_id,
        district_code: this.transfer_cell.target_district.district_code,
        zone_id: this.transfer_cell.target_zone.zone_id,
        zone_code: this.transfer_cell.target_zone.zone_code
      });

      this.showCellGroups = false;
    },
    async transferLeader() {
      await window.axios.post("sectors/transfer-leader", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        leader_id: this.transfer_leader.supervisor.user_id,
        district_id: this.transfer_leader.target_district.district_id,
        zone_id: this.transfer_leader.target_zone.zone_id,
        sector_id: this.transfer_leader.target_sector.sector_id
      });

      this.showLeaders = false;
    }
  }
};
</script>
