<script>
import { mapState, mapActions } from "vuex";
import EventBus from "@/bus";
export default {
  name: "Toolbar",
  data() {
    return {
      popover: false,
      popover_notifications: false,
      add: false,
      addUser: false,
      church: null,
      notifications: [],
      reports: {},
      last_report: {
        is_complete: 0
      }
    };
  },
  computed: mapState({
    user: state => state.auth.user,
    info: state => state.auth.info
  }),
  mounted() {
    this.getChurch();
    this.getNotifcations();
    this.getReports();
  },
  methods: {
    ...mapActions({
      toggleMini: "general/toggleMini"
    }),
    menu() {
      EventBus.$emit("permanent", null);
    },
    showDialogAddUser() {
      EventBus.$emit("show", true);
    },
    showSendInvitation() {
      EventBus.$emit("show-send-invitation", true);
    },
    showAssign() {
      EventBus.$emit("show-assign", true);
    },
    showSettings() {
      EventBus.$emit("show-settings", true);
    },
    showTransferDistricts() {
      EventBus.$emit("show-transfer-districts", true);
    },
    showTransferZones() {
      EventBus.$emit("show-transfer-zones", true);
    },
    showTransferCells() {
      EventBus.$emit("show-transfer-cells", true);
    },
    showTransferMembers() {
      EventBus.$emit("show-transfer-members", true);
    },
    async getChurch() {
      const { data } = await window.axios.post("church/get", {
        church_id: this.user.church_id
      });

      this.church = data.response;
    },
    async getNotifcations() {
      const { data } = await window.axios.get("notifications/list", {
        params: {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: 1,
          language: "es"
        }
      });
      this.notifications = data.response ? data.response : [];
    },
    showProfileCell() {
      this.menu();
      this.$router.push({
        name: "cell-groups-profile",
        params: { id: this.user.group.parent_id }
      });
    },
    async getReports() {
      try {
        this.menu = false;
        let request = {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.user.church_id,
          parent_id: this.user.group.parent_id
        };
        const { data } = await window.axios.get("reports/list", {
          params: request
        });
        this.reports = data.response ? data.response : [];
        this.last_report = this.reports.slice(-1)[0];
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
      }
    },
    async createReport() {
      try {
        const { data } = await window.axios.get("reports/detail", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            report_id: this.last_report.report_id
          }
        });
        this.report(data.response, this.last_report.report_id);
      } catch (error) {
        console.log(error);
      }
    },
    report(value, report_id) {
      EventBus.$emit("show-dialog-report", {
        value: value,
        report_id: report_id
      });
    },
    addMembers() {
      EventBus.$emit("add-members-dialog", this.user);
    }
  }
};
</script>
