                </template>
                <template v-else>
                  <p
                    class="text-grey text-sm font-bold tracking-wide"
                  >D{{ user.group.district_code }} Z{{ user.group.zone_code }} S{{ user.group.sector_code }} C{{ user.group.cell_code }}</p>
                </template>
                <p class="text-2xl font-medium">{{ user.full_name }}</p>
                <div class="flex justify-center w-full">
                  <div v-for="(step, index) in steps" :key="step.name" class="mx-auto">
                    <v-menu
