                          scrollable
                          @input="selectDate($event, step, index)"
                        />
                      </div>
                    </v-menu>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="border-t-2"/>
          <div class="flex h-full">
            <div class="w-1/3 border-r-2">
              <div class="px-6 py-3">
                <div class="mb-2">
                  <p class="text-grey text-sm">Telfono</p>
                  <p class="text-blue-darker text-sm">{{ user.phone }}</p>
                </div>
                <div class="mb-2">
                  <p class="text-grey text-sm">Direccin</p>
                  <p class="text-blue-darker text-sm">{{ user.address }}</p>
                </div>
                <div class="mb-2">
                  <p class="text-grey text-sm">Ciudad</p>
                  <p class="text-blue-darker text-sm">{{ user.city }}</p>
                </div>
              </div>
              <gmap-map
                :center="center"
                :zoom="12"
                :options="{ fullscreenControl: false, mapTypeControl: false }"
                style="width: 100%; height: 175px"
              />
            </div>
            <div class="w-2/3">
              <div class="border-b-2 p-4">
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Correo</p>
                  <p class="w-36">{{ user.email }}</p>
                </div>
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Gnero</p>
                  <p class="w-36">{{ user.gender == 'm' ? 'Masculino' : 'Femenino' }}</p>
                </div>
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Fecha de nacimiento</p>
                  <p class="w-36">{{ formatDate(user.birth_date) }}</p>
                </div>
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Estado civil</p>
                  <p class="w-36">{{ user.marital_status == 'm' ? 'Casado' : 'Soltero' }}</p>
                </div>
                <div
                  v-if="user.marital_status == 'm'"
                  class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap"
                >
                  <p class="w-48 text-sm text-grey">Aniversario</p>
                  <p class="w-36">{{ user.married_since ? formatDate(user.married_since) : '' }}</p>
                </div>
              </div>
              <div class="py-5 px-4">
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Exelerate ID</p>
                  <p class="w-36">{{user.exelerate_id ? user.exelerate_id : ''}}</p>
                </div>
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Supervisor</p>
                  <p class="w-36">No asignado</p>
                </div>
                <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Invitado desde</p>
                  <p class="w-36">{{ user.guest_since ? formatDate(user.guest_since) : '' }}</p>
                </div>
                <div class="w-full flex text-sm text-blue-darkest flex-wrap">
                  <p class="w-48 text-sm text-grey">Miembro desde</p>
                  <p class="w-36">{{ user.member_since ? formatDate(user.member_since) : '' }}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="border-t-2"/>
        </div>
        <div class="flex justify-end px-6 mt-4">
          <div>
            <button
              class="rounded py-3 w-48 text-white heebo flex items-center justify-center no-outline bg-blue-darker hover:bg-blue-dark"
              @click="edit()"
            >
              <span class="font-semibold tracking-wide">Editar</span>
            </button>
          </div>
        </div>
      </div>
      <div
        v-if="active == 'timeline'"
        class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 min-h-32"
      >
        <div class="relative mb-4 pb-6 flex items-center justify-between bg-grey-light">
          <h1 class="font-normal heebo text-2xl text-blue-darker px-4">Timeline</h1>
          <div>
            <v-btn icon class="absolute pin-t pin-r -mt-16 -mr-12" @click="show = false">
              <v-icon class="text-grey-lighter">close</v-icon>
            </v-btn>
          </div>
        </div>
        <div class="mt-4 flex flex-col w-full h-auto">
          <div class="px-12">
            <v-textarea v-model="comment" outline name="input-7-4" color="primary"/>
            <div class="flex justify-end">
              <div>
                <button
                  class="rounded py-3 w-36 text-white heebo flex items-center justify-center no-outline bg-blue-darker hover:bg-blue-dark"
                  @click="addCommentTimeline"
                >
                  <template v-if="!loading_comment">
                    <span class="font-semibold tracking-wide">Comentar</span>
                  </template>
                  <template v-else>
                    <p
                      class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                    >Cargando...</p>
                  </template>
                </button>
              </div>
            </div>
          </div>
          <div class="px-6 w-full">
            <div v-if="timeline != null" class="h-80 overflow-auto">
              <div v-for="(item, index) in timeline" :key="index">
                <timeline-comment v-if="item.is_comment == 1" :timeline="item" :user="user"/>
                <timeline-activity v-else :timeline="item"/>
              </div>
            </div>
            <div v-else class="px-6 w-full text-center">
              <p class="text-black">Sin informacin</p>
            </div>
          </div>
        </div>
      </div>
    </v-dialog>
    <leader-report v-if="Object.keys(user).length" :user="user"/>
  </div>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapGetters } from "vuex";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  name: "ProfileAuthUser",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: true,
      loading_comment: false,
      user: {},
      active: "profile",
      timeline: [],
      comment: null,
      menu: [],
      date: null,
      center: {
        lat: 13.713513,
        lng: -89.247708
      },
      steps: [
        {
          id: 1,
          name: "Conversin",
          unlock: require("@/assets/images/steps/salvacion.png"),
          next: require("@/assets/images/steps/salvacion-proximo.png"),
          warning: require("@/assets/images/steps/salvacion-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 2,
          name: "Bautismo en Agua",
          unlock: require("@/assets/images/steps/bautismo.png"),
          next: require("@/assets/images/steps/bautismo-proximo.png"),
          warning: require("@/assets/images/steps/bautismo-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 3,
          name: "Espritu Santo",
          unlock: require("@/assets/images/steps/espiritu-santo.png"),
          next: require("@/assets/images/steps/espiritu-santo-proximo.png"),
          warning: require("@/assets/images/steps/espiritu-santo-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 4,
          name: "Servicio",
          unlock: require("@/assets/images/steps/servicio.png"),
          next: require("@/assets/images/steps/servicio-proximo.png"),
          warning: require("@/assets/images/steps/servicio-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 5,
          name: "Lider",
          unlock: require("@/assets/images/steps/lider.png"),
          next: require("@/assets/images/steps/lider-proximo.png"),
          warning: require("@/assets/images/steps/lider-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        }
      ]
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info",
      info: "auth/info",
      auth_user: "auth/user"
    })
  },
  mounted() {
    EventBus.$on("show-profile-auth-user", value => {
      this.user = value;
      this.show = !this.show;
      this.loading = false;
      this.getTimeline();
      if (value.steps) {
        this.steps.forEach((s, i) => {
          value.steps.forEach(s1 => {
            if (s.id == s1.step_id) {
              s.step_date = s1.step_date;
              if (value.steps.length == i + 1) {
                return;
              }
              this.steps[i + 1].disabled = false;
            }
          });
        });
      }
    });
    EventBus.$on("delete-comment", () => {
      this.getTimeline();
    });
    EventBus.$on("update-comment", () => {
      this.getTimeline();
    });
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    edit() {
      this.show = false;
      EventBus.$emit("show-profile-edit-auth-user", this.user);
    },
    setSteps(steps) {
      let _steps = [];
      steps.forEach(_s => {
        _steps.push(_s.name);
      });
      this.steps.forEach(s => {
        _steps.includes(s.name) ? (s.active = true) : (s.active = false);
      });
    },
    async getTimeline() {
      try {
        const { data } = await window.axios.post(
          "general/timeline/list-timeline",
          {
            user_id: this.auth_user.user_id,
            role_id: this.auth_user.role,
            church_id: 1,
            type: "user",
            id_timeline_owner: this.auth_user.user_id,
            language: "es",
            start_date: null,
            end_date: null
          }
        );
        this.timeline = data.response;
      } catch (error) {
        console.error(error);
        return error;
      }
    },
    async addCommentTimeline() {
      this.loading_comment = true;
      try {
        await window.axios.post("general/timeline/add-comments", {
          owner_id: this.auth_user.user_id,
          role_id: this.auth_user.role,
          church_id: 1,
          type: "user",
          commented_id: this.user.user_id,
          comment: this.comment
        });
        this.comment = null;
        this.getTimeline();
      } catch (error) {
        return error;
      } finally {
        this.loading_comment = false;
      }
    },
    checkImageStep(step, index) {
      if (step.step_date) {
        this.steps[index].disabled = false;
        return step.unlock;
      }
      if (index > 0) {
        if (this.steps[index - 1].step_date) {
          return step.next;
        } else {
          return step.warning;
        }
      }
      this.steps[index].disabled = false;
      return step.next;
    },
    selectDate(date, step, index) {
      window.axios.post("users/assign-steps", {
        user_id: this.auth_user.user_id,
        role_id: this.auth_user.role,
        church_id: 1,
        step_id: step.id,
        step_name: step.name,
        step_date: date
      });
      this.steps[index].step_date = date;
      this.steps[index + 1].disabled = false;
    },
    download() {
      const element = document.getElementById("leader-report");
      html2canvas(element).then(canvas => {
        const imgData = canvas.toDataURL("image/png");
        const pdf = new jsPDF("p", "pt", "a4", true);
        var width = pdf.internal.pageSize.getWidth();
        var height = pdf.internal.pageSize.getHeight();
        console.log(height);
        pdf.addImage(imgData, "PNG", 0, 0, width, height);
        pdf.save("perfil-lider.pdf");
      });
    }
  }
};
</script>
