<template>
  <auth-card :padding="12">
    <template slot="title">
      <p class="text-3.5xl tracking-normal">Create a new church</p>
      <p class="text-base text-grey">Please enter your
        <strong>email address</strong>
      </p>
    </template>
    <template slot="form">
      <form autocomplete="off"
            class="mb-12"
            @submit.prevent="submit">
        <div class="mb-2">
          <input v-validate="'required|email'"
                 :class="{ 'has-error' : errors.has('email') }"
                 v-model="form.email"
                 type="email"
                 class="w-full form-input mb-1 focus:text-black"
                 placeholder="you@email.com"
                 name="email"
                 autocomplete="off"
                 @keypress="error = null">
          <small class="text-error flex">{{ errors.first('email') || error }}</small>
        </div>
        <button :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                :disabled="errors.any()"
                class="w-full rounded py-3 text-white heebo flex items-center justify-center no-outline"
                type="submit">
          <template v-if="!loading">
            <span class="mr-6 font-medium text-base heebo tracking-wide">Next</span>
            <span class="icon-arrow-right text-sm" />
          </template>
          <template v-else>
            <p class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide">
              Loading...
            </p>
          </template>
        </button>
        <div class="mt-8 flex items-center justify-center">
          <p-check v-model="form.newsletter"
                   class="p-svg p-curve p-bigger"
                   color="primary">
            <v-icon slot="extra"
                    class="svg svg-icon"
                    small>
              check
            </v-icon>
            <span class="text-grey text-13">It's ok to send me email about the C+ service.</span>
          </p-check>
        </div>
      </form>
    </template>
    <template slot="footer">
      <p class="text-13 text-grey heebo mb-2">Your church already have C+?
        <router-link :to="{ name: 'signin-url' }"
                     class="no-underline text-blue hover:underline font-normal">Sign In</router-link>
      </p>
    </template>
  </auth-card>
</template>

<script>
import AuthCard from "@/components/AuthCard";
export default {
  name: "SignUp",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Name",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      form: {
        email: null,
        newsletter: 0
      },
      error: null,
      loading: false
    };
  },
  methods: {
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          await this.$store.dispatch("auth/sendCode", this.form);
          this.$router.replace({
            name: "code"
          });
        }
      } catch (e) {
        this.error = e.message;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
