<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
import { ValidationProvider } from "vee-validate";
export default {
  name: "Settings",
  components: {
    AuthCard,
    ValidationProvider
  },
  data() {
    return {
      show: false,
      show_password: false,
      show_confirm_password: false,
      loading: {
        email: false,
        contact: false,
        password: false,
        language: false
      },
      tab: "tab-1",
      success: {
        email: "",
        contact: ""
      },
      notifications: {
        reports_email: false,
        reports_mobile: false,
        members_email: false,
        members_mobile: false,
        news_email: false,
        news_mobile: false
      },
      contact: {
        subject: null,
        message: null
      },
      form: {
        password: null,
        password_confirmation: null
      },
      cellStructures: [
        { key: "5x5", name: "5 X 5 (District, Zone, Sector, etc)" },
        { key: "G-12", name: "G-12 (Networks, 12, 144, etc)" },
        { key: "other", name: "Other (Custom)" }
      ],
      languages: [
        {
          id: "en",
          name: "English"
        },
        {
          id: "es",
          name: "Spanish"
        }
      ],
      email: null
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info",
      user: "auth/user"
    })
  },
  mounted() {
    EventBus.$on("show-settings", value => {
      this.show = value;
    });
    this.get();
    this.email = this.user.email;
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    async get() {
      try {
        const { data } = await window.axios.post("settings/get", {
          church_id: this.church.church_id
        });
        this.settings = data.response;
        this.general = this.settings.notifications;
        this.general.language = this.languages.find(l => {
          return l.id == this.settings.notifications.language;
        });
        this.assignChurchSettings(data.response);
      } catch (error) {
        return error;
      }
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          this.invitations.map(i => {
            i.group_id = i.district.id;
            i.user_id = this.user.user_id;
            i.role_id = this.user.role;
            i.church_id = this.church.church_id;
          });
          await window.axios.post("general/send-invitations", {
            invitations: this.invitations
          });
          this.show = false;
          this.invitations = [
            {
              email: "",
              district: "",
              group_id: "",
              user_id: "",
              church_id: "",
              role_id: ""
            }
          ];
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    selectDistrict() {
      if (this.church_settings.has_districts) {
        this.church_settings.has_zones = true;
        this.church_settings.has_sectors = true;
      }
    },
    async saveGeneral(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          this.general.language = this.general.language.id;
          this.general.church_id = this.church.church_id;
          await window.axios.post("settings/general", this.general);
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
        this.show = false;
      }
    },
    assignChurchSettings(data) {
      this.church_settings.church_id = this.church.church_id;
      this.church_settings.church_name = data.church_name;
      this.church_settings.subdomain = data.subdomain;
      this.church_settings.cell_structure = this.cellStructures.find(
        cs => cs.key == data.cell_structure
      );
      this.church_settings.label_structure = this.labels.find(
        l => l.id == data.label_structure
      );
      this.church_settings.has_districts = data.has_districts;
      this.church_settings.has_zones = data.has_zones;
      this.church_settings.has_sectors = data.has_sectors;
    },
    async contactUs(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          await window.axios.post("settings/contact-us", this.contact);
          this.show = false;
        }
      } catch (error) {
        return error.responses;
      } finally {
        this.contact = {
          name: null,
          email: null,
          message: null
        };
        this.loading = false;
      }
    },
    async saveSettings(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          const data = this.church_settings;
          await window.axios.post("settings/church", {
            church_id: data.church_id,
            church_name: data.church_name,
            subdomain: data.subdomain,
            cell_structure: data.cell_structure.key,
            label_structure: data.label_structure.id,
            has_districts: data.has_districts ? 1 : 0,
            has_zones: data.has_zones ? 1 : 0,
            has_sectors: data.has_sectors ? 1 : 0
          });
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
        this.show = false;
      }
    },
    async changeEmail(scope) {
      this.loading.email = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          const { data } = await window.axios.post("settings/change-email", {
            user_id: this.user.user_id,
            church_id: 1,
            email: this.email
          });
          this.success.email = data.response.message;
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading.email = false;
      }
    },
    async changeNotification(type, value) {
      await window.axios.post("settings/notifications", {
        user_id: this.user.user_id,
        church_id: 1,
        active: value,
        notification_type: type
      });
    },
    async submitContact(scope) {
      this.loading.contact = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          const { data } = await window.axios.post("settings/contact-us", {
            user_id: this.user.user_id,
            church_id: 1,
            subject: this.contact.subject,
            message: this.contact.message
          });
          this.contact.subject = null;
          this.contact.message = null;
          await this.$validator.pause();
          await this.$validator.reset();
          await this.$validator.resume();
          this.success.contact = data.response.message;
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading.contact = false;
      }
    },
    async changePassword(scope) {
      this.loading.password = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          this.form.email = this.email;
          await window.axios.post("/add-new-password", this.form);
        }
      } catch (e) {
        this.message = e.message;
      } finally {
        this.loading.password = false;
      }
    }
  }
};
</script>
