<template>
  <div class="flex w-full font-sans">
    <div class="bg-white h-full min-h-screen w-12 px-8">
      <div class="flex flex-col items-center py-6">
        <router-link :to="{name: 'church-profile'}"
                     :class="$router.name == 'church-profile' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-account-box text-xl" />
        </router-link>
        <router-link :to="{name: 'church-admins'}"
                     :class="$route.name == 'church-admins' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline  hover:text-blue">
          <span class="icon-bookmark text-xl" />
        </router-link>
        <router-link :to="{name: 'church-districts'}"
                     :class="$route.name == 'church-districts' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-people text-xl" />
        </router-link>
      </div>
    </div>
    <div class="p-8 w-full">
      <div class="flex justify-between items-end mb-8">
        <div>
          <h1 class="font-normal text-blue-darkest">Administrators</h1>
          Showing <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ admins.length }}</span>out of {{admins.length}} administrators
        </div>
        <div>
          <button class="rounded py-2 px-4 text-white flex items-center justify-center no-outline bg-gradient hover:bg-blue-dark">
            <v-icon color="white"
                    small
                    class="mr-4">add</v-icon>
            <span class="font-medium text-base tracking-wide sahdow">Invite</span>
          </button>
        </div>
      </div>
      <div>
        <v-data-table :headers="headers"
                      :items="admins"
                      :loading="loading"
                      hide-actions
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>

          <template slot="items"
                    slot-scope="props">
            <tr class="group">
              <td :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                  class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px">
                <img :src="props.item.profile_picture ? props.item.profile_picture : require('@/assets/images/avatar.png')"
                     alt=""
                     class="h-12 w-12 rounded-full">
                <span class="mx-2">{{ props.item.full_name }}</span>
              </td>
              <td class="text-blue tracking-wide">{{ props.item.email }}</td>
              <td>{{ props.item.role }}</td>
              <td class="tracking-wide">{{ props.item.phone ? props.item.phone : '--' }}</td>
              <td>
                <v-menu :nudge-bottom="10"
                        origin="center center"
                        transition="scale-transition"
                        offset-y
                        bottom>
                  <v-btn slot="activator"
                         flat
                         icon
                         color="grey"
                         class="no-outline">
                    <v-icon>more_vert</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                           @click="profile(props.item)">
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                          Profile
                        </a>
                      </li>
                      <li v-if="show(props.item)" class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                           @click="edit(props.item)">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </a>
                      </li>
                      <li class="mb-3 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li>
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
    <profile-senior/>
    <profile-edit-senior/>
    <profile-user/>
    <profile-edit-user/>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import EventBus from "@/bus";

export default {
  name: "ChurchAdmins",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Administrators",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        {
          text: "Full Name",
          value: "full_name"
        },
        { text: "Email", value: "email" },
        { text: "Role", value: "role" },
        { text: "Phone", value: "phone" },
        { text: "Details", value: "details" }
      ],
      admins: [],
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user"
    })
  },
  mounted() {
    this.get();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("users/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role
        });
        this.admins = data.response;
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
      }
    },
    async profile(user) {
      const { data } = await window.axios.post("users/profile", {
        user_id: user.user_id,
        type_user: user.type_user
      });
      if (user.role == "Senior Pastor") {
        EventBus.$emit("show-profile-senior", data.response);
        return;
      }
      EventBus.$emit("show-profile-user", data.response);
    },
    async edit(user) {
      const { data } = await window.axios.post("users/profile", {
        user_id: user.user_id,
        type_user: user.type_user
      });
      if (user.role == "Senior Pastor") {
        EventBus.$emit("show-profile-edit-senior", data.response);
        return;
      }
      EventBus.$emit("show-profile-edit-user", data.response);
    },
    show(data) {
      let user_role = this.user.user_role;
      if (data.role == user_role) {
        return true;
      } else if (user_role == "Senior Pastor") {
        return true;
      }
      return false;
    }
  }
};
</script>
