<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ProfileEditSenior",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      tab: 0,
      user: [],
      menu: false,
      menu_guest: false,
      menu_member: false,
      message: null,
      form: {
        first_name: null,
        last_name: null,
        email: null,
        phone: null,
        birth_date: null,
        gender: "f",
        maritalStatus: null,
        marital_status: null,
        guest_since: null,
        member_since: null,
        current_password: null,
        password: null
      },
      maritalStatuses: [
        { id: "married", name: "Married" },
        { id: "single", name: "Single" }
      ]
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info"
    })
  },
  watch: {
    menu(val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  mounted() {
    EventBus.$on("show-profile-edit-senior", value => {
      this.user = value;
      this.user.maritalStatus = this.maritalStatuses.find(ms => {
        return ms.name == value.marital_status;
      });
      this.user.marital_status = this.user.maritalStatus.name;
      this.form.birth_date = value.birth_date;
      this.show = !this.show;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    },
    save(date) {
      this.$refs.menu.save(date);
    },
    async savePassword(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          await window.axios.post("users/edit-password", {
            user_id: this.user.user_id,
            role_id: this.user.role,
            type_user: this.user.type_user,
            current_password: this.form.current_password,
            password: this.form.password
          });

          this.show = false;
        }
      } catch (e) {
        const error = e.response;
        this.message = error.data.error.message;
      } finally {
        this.loading = false;
      }
    },
    async saveProfile(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          this.user.marital_status = this.user.maritalStatus.name;
          await window.axios.post("users/edit-profile", this.user);
          this.show = false;
        }
      } catch (e) {
        const error = e.response;
        this.message = error.data.error.message;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
