<template>
  <div>
    <div class="absolute pin-l pin-t p-6 flex items-start">
      <img src="@/assets/images/Logo-c-white.png" alt class="h-14 mr-4">
      <img src="@/assets/images/Logo-white.png" alt class="h-12">
    </div>
    <auth-card :width="470" :padding="0" style="margin-top: 100px" class="relative">
      <template slot="header">
        <div class="flex flex-col items-center justify-center">
          <croppa
            v-model="croppa"
            :height="144"
            :width="144"
            :placeholder-font-size="14"
            :initial-image="image"
            placeholder="Upload an Image"
            placeholder-color="#000"
            prevent-white-space
            @init="onInit"
          >
            <img slot="initial" :src="image" crossorigin="anonymous">
          </croppa>
          <h1 class="font-normal heebo text-4xl text-blue-darker">Church Info</h1>
        </div>
      </template>
      <template slot="form">
        <form class="-mt-4 mb-12" autocomplete="off" @submit.prevent="submit">
          <div class="absolute pin-t">
            <v-btn :to="{ name: 'profile' }" icon>
              <v-icon class="text-grey-light">keyboard_arrow_left</v-icon>
            </v-btn>
          </div>
          <div class="px-16">
            <div class="hebbo text-left mb-2">
              <label for="church_name" class="w-full text-grey text-sm">Church Name</label>
              <input
                v-validate="churchNameValidation()"
                id="church_name"
                :class="{ 'has-error' : errors.has('church_name') }"
                v-model="form.name"
                type="text"
                class="form-input w-full mt-2 text-blue-darker mb-1"
                name="church_name"
                data-vv-as="Church Name"
                data-vv-delay="500"
              >
              <small class="text-error flex">{{ errors.first('church_name') }}</small>
            </div>
            <div class="hebbo text-left mb-2">
              <label for="last_name" class="w-full text-grey text-sm">Country</label>
              <v-select
                v-validate="'required'"
                v-model="form.country"
                :options="options"
                :searchable="true"
                :show-labels="false"
                :class="{ 'has-error' : errors.has('country') }"
                track-by="name"
                label="name"
                placeholder="Select a Country"
                class="border border-solid border-grey-light rounded py-px mb-1"
                name="country"
                data-vv-as="Country"
              >
                <template slot="singleLabel" slot-scope="props">
                  <div class="flex">
                    <img :src="props.option.flag" class="h-4 w-6 mr-4">
                    <span class="option__desc">
                      <span class="option__title">{{ props.option.name }}</span>
                    </span>
                  </div>
                </template>
                <template slot="option" slot-scope="props">
                  <div class="flex">
                    <img :src="props.option.flag" class="option__image h-4 w-6 mr-4" alt>
                    <div class="option__desc">
                      <span class="option__title">{{ props.option.name }}</span>
                    </div>
                  </div>
                </template>
              </v-select>
              <small class="text-error flex">{{ errors.first('country') }}</small>
            </div>
            <div class="flex">
              <div class="hebbo text-left mb-2 flex flex-col w-1/2 pr-2">
                <label for="last_name" class="text-grey text-sm">State</label>
                <input
                  v-validate="'required'"
                  id="state"
                  :class="{ 'has-error' : errors.has('state') }"
                  v-model="form.state"
                  type="text"
                  class="form-input w-full mt-2 text-blue-darker mb-1"
                  name="state"
                >
                <small class="text-error flex">{{ errors.first('state') }}</small>
              </div>
              <div class="hebbo text-left mb-2 flex flex-col w-1/2 pl-2">
                <label for="last_name" class="text-grey text-sm">City</label>
                <input
                  v-validate="'required'"
                  :class="{ 'has-error' : errors.has('city') }"
                  v-model="form.city"
                  type="text"
                  class="form-input w-full mt-2 text-blue-darker mb-1"
                  name="city"
                >
                <small class="text-error flex">{{ errors.first('city') }}</small>
              </div>
            </div>
            <div class="hebbo text-left mb-2">
              <label for="address" class="w-full text-grey text-sm">Address</label>
              <input
                v-validate="'required'"
                :class="{ 'has-error' : errors.has('address') }"
                v-model="form.address"
                type="text"
                class="form-input w-full mt-2 text-blue-darker mb-1"
                name="address"
                placeholder="Street name, number and zip code"
                @focus="dialog = true"
              >
              <small class="text-error flex">{{ errors.first('address') }}</small>
            </div>
            <div class="hebbo text-left mb-2">
              <label for="address" class="w-full text-grey text-sm">Phone Number</label>
              <div class="flex flex-col">
                <div class="flex mb-1">
                  <!-- <input type="text"
                         class="form-input w-1/4 mt-2 text-blue-darker mr-4"
                         :class="{ 'has-error' : errors.has('code') }"
                         name="code"
                         v-model="form.codePhone"
                  v-validate="'required'">-->
                  <input
                    v-validate="'required'"
                    :class="{ 'has-error' : errors.has('number') }"
                    v-model="form.numberPhone"
                    type="text"
                    class="form-input w-full mt-2 text-blue-darker"
                    name="number"
                  >
                </div>
                <small class="text-error flex">{{ errors.first('code') || errors.first('number') }}</small>
              </div>
            </div>
          </div>

          <div class="border-t my-6 border-grey-light"/>

          <div class="px-16">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              class="w-full rounded py-3 text-white heebo flex items-center justify-center no-outline"
            >
              <template v-if="!loading">
                <span
                  class="mr-6 font-medium text-base heebo tracking-wide"
                >Continue to Church Address</span>
                <span class="icon-arrow-right text-sm"/>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                >Loading...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
      <template slot="footer">
        <p class="text-sm text-grey heebo mb-2">Your church already have C+?
          <router-link
            :to="{ name: 'signin-url' }"
            class="no-underline text-blue hover:underline font-normal"
          >Sign In</router-link>
        </p>
      </template>
    </auth-card>
    <v-dialog v-model="dialog" max-width="800px">
      <div class="bg-white p-4">
        <div class="mb-4">
          <h2 class="text-medium heebo text-2xl mb-2">Search Location</h2>
          <div class="flex justify-between">
            <gmap-autocomplete class="form-input w-full" @place_changed="setPlace"/>
            <button
              class="w-32 rounded py-3 text-white flex items-center justify-center no-outline bg-blue-darker hover:bg-blue-darkest"
              @click="addMarker"
            >Search</button>
          </div>
        </div>
        <gmap-map :center="map.center" :zoom="12" style="width:100%;  height: 400px;">
          <gmap-marker :position="map.marker" @click="map.center=map.marker"/>
        </gmap-map>
        <div class="mt-4">
          <a
            :disabled="!map.places"
            class="rounded py-2 px-4 text-white no-outline text-sm bg-blue-darker hover:bg-blue-darkest mr-4 uppercase font-medium tracking-wide"
            @click.prevent="dialog = false"
          >Save</a>
          <a
            class="no-underline uppercase font-medium text-sm text-base tracking-wide text-blue hover:underline"
            @click.prevent="dialog = false"
          >Close</a>
        </div>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ChurchInfo",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Info",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      croppa: {},
      dialog: false,
      loading: false,
      church: null,
      image: null,
      map: {
        center: { lat: 45.508, lng: -73.587 },
        marker: { lat: 45.508, lng: -73.587 },
        place: [],
        currentPlace: null
      },
      form: {
        name: null,
        country: null,
        state: null,
        city: null,
        address: null,
        codePhone: "",
        numberPhone: "",
        latitude: null,
        longitude: null
      },
      options: []
    };
  },
  computed: {
    ...mapGetters({
      token: "auth/token",
      churchInfo: "auth/church_info"
    }),
    phone() {
      return `${this.form.codePhone}${this.form.numberPhone}`;
    }
  },
  beforeMount() {
    this.countries();
    this.initChurch();
  },
  mounted() {
    this.geolocate();
  },
  methods: {
    ...mapActions({
      saveChurch: "auth/saveChurchInfo",
      updateChurch: "auth/updateChurchInfo"
    }),
    countries() {
      fetch("https://restcountries.eu/rest/v2/all").then(response => {
        response.json().then(data => {
          this.options = data;
        });
      });
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          let data = new FormData();
          data.append("image", this.croppa.getChosenFile());
          data.append("church_name", this.form.name);
          data.append("country", this.form.country.name);
          data.append("state", this.form.state);
          data.append("city", this.form.city);
          data.append("address", this.form.address);
          data.append("phone", this.form.numberPhone);
          data.append("latitude", this.form.latitude);
          data.append("longitude", this.form.longitude);
          data.append("token", this.token);

          if (this.churchInfo) {
            data.append("church_id", this.churchInfo.church_id);
            await this.updateChurch({ body: data });
          } else {
            await this.saveChurch(data);
          }
          this.$router.replace({
            name: "church-settings"
          });
        }
      } catch (e) {
        console.error("Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    onInit() {
      this.croppa.addClipPlugin(function(ctx, x, y, w, h) {
        /*
         * ctx: canvas context
         * x: start point (top-left corner) x coordination
         * y: start point (top-left corner) y coordination
         * w: croppa width
         * h: croppa height
         */
        ctx.beginPath();
        ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true);
        ctx.closePath();
      });
    },
    setPlace(place) {
      this.form.address = place.formatted_address;
      this.map.currentPlace = place;
    },
    addMarker() {
      if (this.map.currentPlace) {
        this.form.latitude = this.map.currentPlace.geometry.location.lat();
        this.form.longitude = this.map.currentPlace.geometry.location.lng();
        const marker = {
          lat: this.form.latitude,
          lng: this.form.longitude
        };
        this.map.marker = marker;
        this.map.place = this.map.currentPlace;
        this.map.center = marker;
        this.map.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.map.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    initChurch() {
      if (this.churchInfo) {
        this.church = this.churchInfo;
        this.form.name = this.church.church_name;
        this.form.state = this.church.state;
        this.form.city = this.church.city;
        this.form.address = this.church.address;
        this.form.latitude = this.church.latitude;
        this.form.longitude = this.church.longitude;
        this.form.numberPhone = this.church.phone;
        this.image = this.church.profile_picture;
        fetch(
          `https://restcountries.eu/rest/v2/name/${this.church.country}`
        ).then(response => {
          response.json().then(data => {
            this.form.country = data[0];
          });
        });
      }
    },
    churchNameValidation() {
      if (this.churchInfo) {
        return this.churchInfo.church_name == this.form.name
          ? "required"
          : "required|unique_church";
      }

      return "required|unique_church";
    }
  }
};
</script>
