<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex flex-col mb-8">
        <div class="flex items-center">
          <h1 class="font-normal text-blue-darkest">Cell Groups</h1>
          <p class="mx-3 flex items-center text-sm">
            <v-icon small class="text-blue-darker mx-2">home</v-icon>- Groups - Cell Groups
          </p>
        </div>
        <div class="text-blue-darker text-base">
          Showing
          <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ cells.length }}</span>
          out of {{ cells.length }}
          <a
            class="px-2 bg-blue-lighter rounded text-blue mx-1"
            @click.prevent="get(active ? false : true)"
          >{{ active ? 'active': 'inactive' }}</a>
          <span class="px-2 bg-blue-lighter rounded text-blue mx-1">cell groups</span>
        </div>
      </div>
      <div>
        <div class="mb-3">
          <p class="text-grey tracking-wide mb-1">Filter by</p>
          <div class="flex justify-between">
            <div v-if="groups" class="flex">
              <v-select
                :options="groups.districts"
                :searchable="false"
                :show-labels="false"
                v-model="district"
                track-by="district_code"
                label="district_code"
                placeholder="District"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white"
                name="district"
              >
                <template slot="singleLabel" slot-scope="props">
                  <span>D{{ props.option.district_code }}</span>
                </template>
                <template slot="option" slot-scope="props">
                  <span>D{{ props.option.district_code }}</span>
                </template>
              </v-select>
              <v-select
                :options="groups.zones"
                :searchable="true"
                :show-labels="false"
                v-model="zone"
                track-by="zone_code"
                label="zone_code"
                placeholder="Zone"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white mx-3"
                name="zone"
              >
                <template slot="singleLabel" slot-scope="props">
                  <span>Z{{ props.option.zone_code }}</span>
                </template>
                <template slot="option" slot-scope="props">
                  <span>Z{{ props.option.zone_code }}</span>
                </template>
              </v-select>
              <v-select
                :options="groups.sectors"
                :searchable="true"
                :show-labels="false"
                v-model="sector"
                track-by="sector_code"
                label="sector_code"
                placeholder="Sector"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white"
                name="sector"
              >
                <template slot="singleLabel" slot-scope="props">
                  <span>S{{ props.option.sector_code }}</span>
                </template>
                <template slot="option" slot-scope="props">
                  <span>S{{ props.option.sector_code }}</span>
                </template>
              </v-select>
              <button
                type="button"
                class="mx-2 bg-blue hover:bg-blue-dark text-white px-4 text-xl rounded outline-none"
                @click="filteredCells"
              >
                <v-tooltip top>
                  <span slot="activator" class="icon-search"/>
                  <span>Search</span>
                </v-tooltip>
              </button>
              <button
                type="button"
                class="mx-2 bg-blue hover:bg-blue-dark text-white px-4 text-xl rounded outline-none"
                @click="get(true)"
              >
                <v-tooltip top>
                  <span slot="activator" class="icon-trash"/>
                  <span>Reset</span>
                </v-tooltip>
              </button>
            </div>
            <div class="flex w-full justify-end">
              <input
                v-model="search"
                type="text"
                class="form-input bg-white w-1/3"
                placeholder="Search by Keywords"
              >
            </div>
          </div>
        </div>
        <v-data-table
          :headers="headers"
          :items="cells"
          :loading="loading"
          :search="search"
          hide-actions
          class="elevation-0 border border-grey-light rounded"
        >
          <template slot="headerCell" slot-scope="props">
            <span class="uppercase tracking-wide text-grey">{{ props.header.text }}</span>
          </template>
          <template slot="items" slot-scope="props">
            <tr class="group">
              <td
                :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px"
              >
                <span class="-ml-2 w-8">
                  <v-tooltip right color="red">
                    <v-icon
                      v-if="props.item.is_complete == 0"
                      slot="activator"
                      color="red"
                      class="m-0 p-0 cursor-pointer"
                    >info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div
                  class="mx-1 bg-green text-white h-8 w-8 flex items-center justify-center rounded-full"
                >C{{ props.item.cell_code }}</div>
                <span class="mx-1">D{{ props.item.district_code }}</span>
                <span class="mx-1">Z{{ props.item.zone_code }}</span>
                <span class="mx-1">S{{ props.item.sector_code }}</span>
                <span class="mx-1">C{{ props.item.cell_code }}</span>
              </td>
              <td
                class="text-sm text-blue tracking-wide"
              >{{ props.item.leader ? props.item.leader : '--' }}</td>
              <td class="text-sm">{{ formatDate(props.item.start_date) }}</td>
              <td
                class="text-center text-sm text-blue tracking-wide"
              >{{ props.item.city ? props.item.city : '--' }}</td>
              <td class="text-center text-base text-blue">{{ props.item.members }}</td>
              <td class="text-center">
                <v-menu
                  :nudge-bottom="10"
                  origin="center center"
                  transition="scale-transition"
                  offset-y
                  bottom
                >
                  <v-btn slot="activator" flat icon color="grey" class="no-outline">
                    <v-icon>info_outline</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="my-2 group">
                        <a
                          class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                          @click="profile(props.item)"
                        >
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>Profile
                        </a>
                      </li>
                      <!-- <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </a>
                      </li>
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li>-->
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "CellGroups",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Cell Groups",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Cell Id", value: "cell_code" },
        { text: "Leader", value: "leader" },
        { text: "Start Date", value: "start_date" },
        { text: "City", value: "city", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      cells: [],
      filterData: [],
      active: true,
      loading: true,
      search: "",
      district: null,
      zone: null,
      sector: null,
      groups: {
        districts: [],
        zones: [],
        sectors: []
      }
    };
  },
  computed: {
    ...mapState({
      user: state => state.auth.user
    })
  },
  mounted() {
    this.get(true);
    this.getGroups();
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.post("cells/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: active ? "1" : "0"
        });
        data.response ? (this.cells = data.response) : (this.cells = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    profile(group) {
      this.$router.push({
        name: "cell-groups-profile",
        params: { id: group.id }
      });
    },
    async getGroups() {
      try {
        const { data } = await window.axios.post("general/full-list-groups", {
          user_id: this.user.user_id,
          role_id: this.user.role
        });
        this.groups = data.response;
      } catch (error) {
        return error;
      }
    },
    async filteredCells() {
      this.loading = true;
      try {
        await this.get(true);
        this.cells = this.cells.filter(
          c =>
            (this.district
              ? c.district_code == this.district.district_code
              : true) &&
            (this.zone ? c.zone_code == this.zone.zone_code : true) &&
            (this.sector ? c.sector_code == this.sector.sector_code : true)
        );
      } catch (error) {
        return error;
      }
    }
  }
};
</script>
