<template>
  <div class="flex w-full">
    <div class="bg-white h-full min-h-screen w-12 px-8">
      <div class="flex flex-col items-center py-6">
        <router-link :to="{name: 'districts-profile', params: {id: $route.params.id}}"
                     :class="$route.name == 'districts-profile' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-account-box text-xl" />
        </router-link>
        <router-link :to="{name: 'districts-zones', params: {id: $route.params.id}}"
                     :class="$route.name == 'districts-zones' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline hover:text-blue">
          <span class="icon-people text-xl" />
        </router-link>
        <router-link :to="{name: 'districts-reports', params: {id: $route.params.id}}"
                     :class="$route.name == 'districts-reports' ? 'text-blue' : 'text-grey'"
                     class="my-3 no-underline  hover:text-blue">
          <span class="icon-list text-xl" />
        </router-link>
        <router-link :to="{name: 'districts-timeline', params: {id: $route.params.id}}"
                     class="my-3 no-underline  hover:text-blue">
          <v-icon :class="$route.name == 'districts-timeline' ? 'text-blue' : 'text-grey'"
                  class="font-medium">list</v-icon>
        </router-link>
      </div>
    </div>
    <div class="w-full">
      <div class="flex flex-col justify-between p-8">
        <div>
          <h1 class="font-normal text-blue-darkest">Zones</h1>
          <div class="text-blue-darker text-base">Showing
            <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ zones.length }}</span> out of {{ zones.length }}
            <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
               @click.prevent="get(active ? false : true)">{{ active ? 'active': 'inactive' }}</a>
            <span class="mx-1">zones</span>
          </div>
        </div>
      </div>
      <div class="p-8 w-full">
        <v-data-table :headers="headers"
                      :items="zones"
                      :loading="loading"
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <tr class="group">
              <td :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                  class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px">
                <span class="-ml-2 w-8">
                  <v-tooltip right
                             color="red">
                    <v-icon v-if="props.item.is_complete == 0"
                            slot="activator"
                            color="red"
                            class="m-0 p-0 cursor-pointer">info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div class="mx-1 bg-orange text-white h-8 w-8 flex items-center justify-center rounded-full">Z{{ props.item.zone_code }}</div>
                <span class="mx-1">D{{ props.item.district_code }}</span>
                <span class="mx-1">Z{{ props.item.zone_code }}</span>
              </td>
              <td class="text-sm text-blue">{{ props.item.zone_pastor ? props.item.zone_pastor : '--' }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.sectors.padStart(2, "0") }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.cells.padStart(2, "0") }}</td>
              <td class="text-center text-sm text-blue">{{ props.item.members.padStart(2, "0") }}</td>
              <td class="text-center">
                <v-menu :nudge-bottom="10"
                        origin="center center"
                        transition="scale-transition"
                        offset-y
                        bottom>
                  <v-btn slot="activator"
                         flat
                         icon
                         class="text-grey">
                    <v-icon>info_outline</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                           @click="profile(props.item)">
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                          Profile
                        </a>
                      </li>
                      <!-- <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </a>
                      </li>
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li> -->
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "DistrictsZones",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Districts Zones",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Zone Id", value: "zone_code" },
        { text: "Zone Pastor", value: "zone_pastor" },
        { text: "Sectors", value: "sectors", align: "center" },
        { text: "Cells", value: "cells", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      zones: [],
      active: true,
      loading: true
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get(true);
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.post(
          "districts/zones-groups-list",
          {
            user_id: this.user.user_id,
            role_id: this.user.role,
            district_id: this.$route.params.id,
            active: active ? "1" : "0"
          }
        );
        if (data.response) {
          this.zones = data.response;
        } else {
          this.zones = [];
        }
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
        this.active = active;
      }
    },
    profile(group) {
      this.$router.push({
        name: "sectors-profile",
        params: { id: group.sector_code }
      });
    }
  }
};
</script>
