<script>
import { mapState } from "vuex";
import EventBus from "@/bus";
export default {
  name: "DialogReport",
  data() {
    return {
      show: false,
      e1: 1,
      tab: "#tab-1",
      expand: {
        assistance: 0,
        performance: 0,
        donations: 0
      },
      members: [],
      guests: [],
      donations_offering: 0,
      donations_events: 0,
      donations_transport: 0,
      total_donations: 0,
      performance_baptism: [],
      performance_conversion: [],
      performance_visits: [],
      attendance: [],
      members_assistance: [],
      guests_assistance: [],
      members_doctrine: [],
      members_celebration: [],
      name: null,
      is_edit: false,
      report_id: null,
      report: []
    };
  },
  computed: {
    ...mapState({
      church: state => state.auth.church.info,
      user: state => state.auth.user
    }),
    sumDonations() {
      let sum =
        parseFloat(this.donations_offering) +
        parseFloat(this.donations_events) +
        parseFloat(this.donations_transport);
      return parseFloat(sum).toFixed(2);
    },
    totalMembersAssistance() {
      let count = 0;
      if (this.report.members) {
        this.report.members.forEach(a => {
          if (a.cell_group == "1" && a.role != 1) {
            count++;
          }
        });
      }
      return count;
    },
    totalGuestsAssistance() {
      let count = 0;
      if (this.report.guests) {
        this.report.guests.forEach(a => {
          if (a.cell_group == "1" && a.role == 1) {
            count++;
          }
        });
      }
      return count;
    },
    totalKidsAssistance() {
      let count = 0;

      return count;
    },
    totalDoctrine() {
      let count_members = 0;
      let count_guests = 0;
      if (this.report.members) {
        this.report.members.forEach(a => {
          if (a.doctrine == "1") {
            count_members++;
          }
        });
      }
      if (this.report.guests) {
        this.report.guests.forEach(a => {
          if (a.doctrine == "1") {
            count_guests++;
          }
        });
      }
      return count_members + count_guests;
    },
    totalCelebration() {
      let count_members = 0;
      let count_guests = 0;
      if (this.report.members) {
        this.report.members.forEach(a => {
          if (a.celebration == "1") {
            count_members++;
          }
        });
      }
      if (this.report.guests) {
        this.report.guests.forEach(a => {
          if (a.celebration == "1") {
            count_guests++;
          }
        });
      }
      return count_members + count_guests;
    },
    totalSalvation() {
      let count = 0;
      if (this.report.salvation) {
        this.report.salvation.forEach(a => {
          if (a.salvations == "1") {
            count++;
          }
        });
      }
      return count;
    },
    totalBaptized() {
      let count = 0;
      if (this.report.baptized) {
        this.report.baptized.forEach(a => {
          if (a.baptized == "1") {
            count++;
          }
        });
      }
      return count;
    },
    totalScheduleVisits() {
      let count = 0;
      if (this.report.visits) {
        this.report.visits.forEach(a => {
          if (a.scheduled_visits == "1") {
            count++;
          }
        });
      }
      return count;
    },
    sumCellAssistance() {
      let sum =
        parseFloat(this.report.total_member_assistance) +
        parseFloat(this.report.total_kids_assistance) +
        parseFloat(this.report.total_guest_assistance);
      return sum;
    },
    sumChurchAssistance() {
      let sum =
        parseFloat(this.report.total_doctrine) +
        parseFloat(this.report.total_celebration);
      return sum;
    },
    sumPerformance() {
      let sum =
        parseFloat(this.report.total_baptized) +
        parseFloat(this.report.total_salvation) +
        parseFloat(this.report.total_schedule_visits);
      return sum;
    }
  },
  mounted() {
    EventBus.$on("show-dialog-report", value => {
      this.e1 = 1;
      this.getReport(value.value);
      this.report_id = value.report_id;
      this.show = !this.show;
    });
    EventBus.$on("show-dialog-report-edit", value => {
      this.e1 = 1;
      this.getReport(value.value);
      this.report_id = value.report_id;
      this.show = !this.show;
    });
  },
  methods: {
    async createReport() {
      try {
        let url = "reports/edit";
        await window.axios.put(url, {
          user_id: this.user.user_id,
          role_id: this.user.role,
          cell_id: this.user.group.parent_id,
          church_id: this.user.church_id,
          donations_offering: this.donations_offering,
          donations_events: this.donations_events,
          donations_transport: this.donations_transport,
          total_member_assistance: this.totalMembersAssistance,
          total_kids_assistance: this.totalKidsAssistance,
          total_guest_assistance: this.totalGuestsAssistance,
          total_doctrine: this.totalDoctrine,
          total_celebration: this.totalCelebration,
          total_salvation: this.totalSalvation,
          total_baptized: this.totalBaptized,
          total_schedule_visits: this.totalScheduleVisits,
          attendance: this.attendance,
          report_id: this.report_id
        });
        this.show = false;
        this.e1 = 1;
        EventBus.$emit("saved-report");
      } catch (error) {
        console.error(error);
        return error;
      }
    },
    addToPerformanceBaptism(item) {
      let member = this.report.baptized.find(a => a.id == item.id);
      if (member.baptized == 1) {
        return (member.baptized = "0");
      }
      return (member.baptized = "1");
    },
    addToPerformanceConversion(item) {
      let member = this.report.salvation.find(a => a.id == item.id);
      if (member.salvations == 1) {
        return (member.salvations = "0");
      }
      return (member.salvations = "1");
    },
    addToPerformanceVisits(item) {
      let member = this.report.visits.find(a => a.id == item.id);
      if (member.scheduled_visits == 1) {
        return (member.scheduled_visits = "0");
      }
      return (member.scheduled_visits = "1");
    },
    addToAttendance(item) {
      let member = {
        member_id: null,
        cell_group: "1",
        doctrine: "0",
        celebration: "0",
        salvations: "0",
        baptized: "0",
        scheduled_visits: "0"
      };
      member["member_id"] = item.member_id;
      this.attendance.push(member);
    },
    removeFromAttendance(item) {
      var removeIndex = this.attendance
        .map(function(item) {
          return item.member_id;
        })
        .indexOf(item.member_id);

      this.attendance.splice(removeIndex, 1);
    },
    addMemberAttendace(item, type_member = "members", type = "cell_group") {
      let member = this.report[type_member].find(a => a.id == item.id);
      if (member[type] == 1) {
        return (member[type] = "0");
      }
      return (member[type] = "1");
    },
    setAttendance(member, guest = false) {
      this.attendance.push({
        member_id: member.member_id,
        first_name: member.first_name,
        last_name: member.last_name,
        profile_picture: member.profile_picture,
        role: guest ? 1 : 2,
        cell_group: "0",
        doctrine: "0",
        celebration: "0",
        salvations: "0",
        baptized: "0",
        scheduled_visits: "0"
      });
    },
    async getReport(report) {
      this.report = report;
      this.members = report.members;
      // this.members.forEach(m => this.setAttendance(m));
      this.guests = report.guests ? report.guests : [];
      // this.guests.forEach(g => this.setAttendance(g, true));
      this.donations_offering = report.donations_offering;
      this.donations_transport = report.donations_transport;
      this.donations_events = report.donations_events;
    },
    formatDate(date) {
      if (date) {
        return new Date(date).toLocaleString("es", {
          year: "numeric",
          month: "short",
          day: "numeric"
        });
      }
      return "";
    },
    expandChange(e) {
      console.log(e);
    }
  }
};
</script>
