<template>
  <div>
    <div class="absolute pin-l pin-t p-6 flex items-start">
      <img src="@/assets/images/Logo-c-white.png"
           alt=""
           class="h-14 mr-4">
      <img src="@/assets/images/Logo-white.png"
           alt=""
           class="h-12">
    </div>
    <auth-card :width="576"
               :padding="0"
               style="margin-top: 100px">
      <template slot="header">
        <img :src="church.profile_picture"
             alt="Church Profile Picture"
             class="rounded-full -mt-100 h-36 w-36">
        <h1 class="font-normal heebo text-4xl text-blue-darker">Add administrators</h1>
      </template>
      <template slot="form">
        <form autocomplete="off"
              @submit.prevent="submit">
          <div class="px-6 ml-3">
            <div class="flex text-left mb-3 text-xs">
              <div class="w-2/5 px-1">Name</div>
              <div class="w-2/5 px-4">Email</div>
              <div class="w-1/6" />
            </div>
            <div v-for="(invite, index) in invitations"
                 :key="index"
                 class="flex mb-4 items-end">
              <div class="flex flex-col w-2/5 mr-3 h-16 my-2">
                <input v-validate="'required'"
                       :class="{ 'has-error' : errors.has(`name${index}`) }"
                       :name="`name${index}`"
                       v-model="invite.name"
                       type="text"
                       class="form-input mb-1"
                       placeholder="Name"
                       data-vv-as="name">
                <small class="text-error flex text-xs">{{ errors.first(`name${index}`) }}</small>
              </div>
              <div class="flex flex-col w-2/5 h-16 my-2">
                <input v-validate="'required|email'"
                       :class="{ 'has-error' : errors.has(`email${index}`) }"
                       :name="`email${index}`"
                       v-model="invite.email"
                       type="email"
                       class="form-input mb-1"
                       placeholder="you@example.com"
                       data-vv-as="email">
                <small class="text-error flex text-xs">{{ errors.first(`email${index}`) }}</small>
              </div>
              <div class="w-1/6 flex flex-col items-center justify-center h-16 my-2">
                <p-check v-model="invite.owner"
                         class="p-svg p-bigger p-round mb-1 mx-0"
                         color="success">
                  <svg slot="extra"
                       class="svg svg-icon p-1"
                       viewBox="0 0 20 20">
                    <path d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                          style="stroke: white;fill:white" />
                  </svg>
                </p-check>
                <span class="text-grey text-13">Owner</span>
              </div>
            </div>
          </div>
          <div class="mt-8 mb-12">
            <a class="no-underline heebo text-blue text-sm hover:underline"
               @click.prevent="incrementFields()">Add more</a>
          </div>
          <div class="border-t opacity-50 mb-8 border-grey-light" />
          <div class="flex justify-center">
            <button type="button"
                    class="w-1/3 bg-blue-darker rounded py-3 text-white hover:bg-blue-darkest heebo mx-2"
                    @click.prevent="dialog = true">
              <span class="mr-6 font-medium text-base heebo tracking-wide">Skip</span>
              <span class="icon-arrow-right text-sm" />
            </button>
            <button :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                    :disabled="errors.any()"
                    class="w-1/3 rounded py-3 text-white heebo flex items-center justify-center no-outline">
              <template v-if="!loading">
                <span class="mr-6 font-medium text-base heebo tracking-wide">Send</span>
                <span class="icon-arrow-right text-sm" />
              </template>
              <template v-else>
                <p class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide">
                  Loading...
                </p>
              </template>
            </button>
          </div>
        </form>
      </template>
    </auth-card>
    <v-dialog v-model="dialog"
              persistent
              max-width="500px"
              class="rounded-lg">
      <div class="flex flex-col items-center px-6 bg-white p-12 rounded">
        <div class="text-center font-normal text-4xl heebo flex flex-col items-center mb-3">
          <span class="icon-complete text-4xl text-white bg-teal h-20 w-20 flex items-center justify-center rounded-lg mb-4" /> Review the Terms
        </div>
        <div class="text-justify mb-8">
          By clicking 
          <strong>I Agree</strong>, you understand and agree to the Contract on behalf of the Customer (as described in our
          <span class="underline">Customer Terms of Service</span>),
          <span class="underline">Privacy Policy</span>, and
          <span class="underline">Cookie Policy</span>.
        </div>
        <div>
          <button :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                  :disabled="errors.any()"
                  class="w-full rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
                  @click="agree">
            <template v-if="!loading">
              <span class="icon-checkmark mr-3" />
              <span class="font-semibold tracking-wide">I Agree</span>
            </template>
            <template v-else>
              <p class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide">
                Loading...
              </p>
            </template>
          </button>
        </div>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Invitations",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Add administrators",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      dialog: false,
      invitations: [{ name: "", email: "", owner: 0, church_id: "" }],
      email: null,
      loading: false
    };
  },
  computed: {
    ...mapGetters({
      token: "auth/token",
      church: "auth/church_info"
    })
  },
  methods: {
    ...mapActions({
      terms: "auth/terms",
      admins: "auth/admins"
    }),
    incrementFields() {
      return this.invitations.push({
        name: "",
        email: "",
        owner: 0,
        church_id: ""
      });
    },
    async agree() {
      this.loading = true;
      try {
        await this.terms();
        this.$router.replace({
          name: "dashboard"
        });
      } catch (error) {
        return error;
      } finally {
        this.loading = false;
      }
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          this.invitations.map(i => {
            i.church_id = this.church.church_id;
          });
          await this.admins(this.invitations);
          this.dialog = true;
        }
      } catch (e) {
        console.error("Correct them errors!");
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
