<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "DistrictsProfile",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Districts Profile",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      tab: null,
      map: {
        center: { lat: 45.508, lng: -73.587 },
        place: [],
        currentPlace: null
      },
      district: {}
    };
  },
  computed: mapState({
    user: state => state.auth.user
  }),
  mounted() {
    this.get();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("districts/profile", {
          district_id: this.$route.params.id,
          user_id: this.user.user_id,
          role_id: this.user.role,
          type_user: this.user.type_user
        });
        this.district = data.response;
      } catch (error) {
        return error;
      }
    },
    edit() {
      EventBus.$emit("edit-district");
    }
  }
};
</script>
