<template>
  <div class="w-full flex justify-center">
    <div
      v-if="info"
      class="bg-white rounded-lg h-auto pt-8 pb-6 shadow h-auto relative"
      style="width: 461px"
    >
      <div class="flex justify-center w-full h-auto absolute">
        <div
          :class="form.profile_picture ? '' : 'bg-blue'"
          class="w-32 h-32 rounded-full -mt-100 pin-r text-center cursor-pointer relative"
        >
          <div v-if="form.profile_picture">
            <img
              :src="form.profile_picture"
              alt="Profile Image"
              class="rounded-full w-32 h-32 object-cover"
            >
          </div>
          <p
            v-else
            class="text-white text-4xl uppercase flex items-center justify-center h-full"
          >{{ initials }}</p>
          <v-btn
            v-if="form.profile_picture"
            flat
            icon
            color="primary"
            class="absolute pin-t pin-r -m-4"
            @click="cleanImage"
          >
            <v-icon color="white" class>close</v-icon>
          </v-btn>
          <v-icon color="white" class="absolute pin-b pin-x mb-2">camera_alt</v-icon>
          <input
            id="profile_picture"
            type="file"
            name="profile_picture"
            class="absolute pin-x h-full w-full pin-t cursor-pointer rounded-full opacity-0"
            @change="previewImage($event)"
          >
        </div>
      </div>
      <div class="text-center my-12">
        <h1 class="font-normal text-3xl text-blue-darker">Bienvenido!</h1>
        <p class="text-sm">
          <strong>{{ info.name }} {{ info.last_name }}</strong>, elige tu contrasea.
        </p>
      </div>
      <ValidationObserver ref="observer">
        <form slot-scope="{ invalid }" @submit.prevent="submit">
          <div class="px-16">
            <div class="mb-6">
              <label class="text-grey text-sm">Correo Electrnico</label>
              <ValidationProvider name="contrasea" rules="required">
                <template slot-scope="{ errors, valid }">
                  <div class="flex w-full">
                    <div class="w-full px-1">
                      <v-text-field
                        id="email"
                        :value="info.email"
                        name="email"
                        class="text-center"
                        disabled
                      />
                    </div>
                  </div>
                </template>
              </ValidationProvider>
            </div>
            <div class="mb-6">
              <div class="flex w-full">
                <div class="w-full px-1">
                  <label class="text-grey text-sm">Contrasea</label>
                  <ValidationProvider
                    name="contrasea"
                    vid="password"
                    rules="required|min:8|verify_password"
                  >
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        id="password"
                        :error-messages="errors"
                        :success="valid"
                        :type="show_password ? 'text' : 'password'"
                        v-model="form.password"
                        name="password"
                        class="m-0 p-0"
                        data-vv-name="password"
                        required
                      >
                        <template slot="append">
                          <div class="flex items-center m-0 p-0">
                            <v-icon v-if="errors.length" color="error" class="m-0 p-0">clear</v-icon>
                            <v-icon v-else-if="valid" color="success" class="m-0 p-0">check</v-icon>
                            <v-btn
                              flat
                              icon
                              class="m-0 p-0"
                              @click="show_password = !show_password"
                            >
                              <v-icon
                                color="blue-grey lighten-3"
                              >{{ show_password ? 'visibility_off' : 'visibility' }}</v-icon>
                            </v-btn>
                          </div>
                        </template>
                      </v-text-field>
                    </template>
                  </ValidationProvider>
                </div>
              </div>
            </div>
            <div class="mb-6">
              <div class="flex w-full">
                <div class="w-full px-1">
                  <label class="text-grey text-sm">Verificar Contrasea</label>
                  <ValidationProvider
                    name="verificar contrasea"
                    rules="required|min:8|confirmed:password"
                  >
                    <template slot-scope="{ errors, valid }">
                      <v-text-field
                        id="password_confirmation"
                        :error-messages="errors"
                        :success="valid"
                        :type="show_confirm_password ? 'text' : 'password'"
                        v-model="form.password_confirmation"
                        name="password_confirmation"
                        class="m-0 p-0"
                        data-vv-name="password_confirmation"
                        required
                      >
                        <template slot="append">
                          <div class="flex items-center m-0 p-0">
                            <v-icon v-if="errors.length" color="error" class="m-0 p-0">clear</v-icon>
                            <v-icon v-else-if="valid" color="success" class="m-0 p-0">check</v-icon>
                            <v-btn
                              flat
                              icon
                              class="m-0 p-0"
                              @click="show_confirm_password = !show_confirm_password"
                            >
                              <v-icon
                                color="blue-grey lighten-3"
                              >{{ show_confirm_password ? 'visibility_off' : 'visibility' }}</v-icon>
                            </v-btn>
                          </div>
                        </template>
                      </v-text-field>
                    </template>
                  </ValidationProvider>
                  <p
                    class="text-grey text-sm"
                  >La contrasea debe llevar por lo menos una letra mayscula, un nmero y mnimo 8 caracteres</p>
                </div>
              </div>
            </div>
            <div class="flex justify-center w-full my-6">
              <div>
                <button
                  :class="[ invalid ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                  :disabled="invalid"
                  type="submit"
                  class="rounded w-36 py-3 text-white heebo flex items-center justify-center no-outline"
                >
                  <template v-if="!loading_form">
                    <span class="font-medium text-base heebo tracking-wide">Continuar</span>
                  </template>
                  <template v-else>
                    <p
                      class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                    >Cargando...</p>
                  </template>
                </button>
              </div>
            </div>
          </div>
        </form>
      </ValidationObserver>
    </div>
    <div v-else-if="loading" class="lds-ellipsis">
      <div/>
      <div/>
      <div/>
      <div/>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { ValidationProvider, ValidationObserver } from "vee-validate";
export default {
  name: "RegisterInvitation",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Registro de Lider",
    htmlAttrs: {
      lang: "es"
    }
  },
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      loading: true,
      file: {},
      loading_form: false,
      info: null,
      show_password: false,
      show_confirm_password: false,
      form: {
        profile_picture: null,
        email: null,
        password: null,
        password_confirmation: null
      }
    };
  },
  computed: {
    ...mapState({
      token: state => state.auth.token
    }),
    initials() {
      return this.info.name.charAt(0) + this.info.last_name.charAt(0);
    }
  },
  mounted() {
    this.checkInvitation();
  },
  methods: {
    ...mapActions({
      saveInfo: "auth/saveInfo"
    }),
    async checkInvitation() {
      this.loading = true;
      try {
        const { data } = await window.axios.post(
          "general/validate-invitation",
          {
            token: this.token
          }
        );
        if (data.response.success) {
          this.form.email = data.response.email;
          this.info = data.response;
        }
      } catch (error) {
        const { data } = error.response;
        if (data.error.type == "invitation_used") {
          this.$router.replace({ name: "used-errors" });
        } else if (data.error.type == "expire_invitation") {
          this.$router.replace({ name: "expire-errors" });
        }
        return error;
      } finally {
        this.loading = false;
      }
    },
    async submit() {
      this.loading_form = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          this.saveInfo({
            user: {
              image: this.form.profile_picture,
              file: this.file,
              email: this.form.email,
              password: this.form.password
            },
            info: this.info
          });
          this.$router.replace({
            name: "cell-group-info"
          });
        }
      } catch (e) {
        console.log(e);
      } finally {
        this.loading_form = false;
      }
    },
    previewImage(event) {
      let reader = new FileReader();
      this.file = event.target.files[0];
      reader.onload = () => {
        this.form.profile_picture = reader.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    },
    cleanImage() {
      this.file = {};
      this.form.profile_picture = null;
    }
  }
};
</script>
