<template>
  <v-navigation-drawer
    v-model="drawer"
    :mini-variant="mini"
    :temporary="temporary"
    app
    fixed
    class="bg-blue-darker z-50"
    width="250"
  >
    <div>
      <div :class="mini ? '': 'px-9'" class="flex py-4 items-center justify-center">
        <router-link :to="{ name: 'dashboard'}">
          <img
            :class="mini ? '' : 'mr-6'"
            src="@/assets/images/Logo.png"
            alt="Logo C+"
            class="h-10 w-10"
          >
          <img
            :class="mini ? 'hidden': ''"
            src="@/assets/images/Logo-white.png"
            alt="Logo C+"
            class="h-8 w-auto"
          >
        </router-link>
      </div>
      <div class="bg-blue-darkest h-32 flex items-center justify-center">
        <div
          :class="mini ? 'mx-auto' : 'px-4'"
          class="flex justify-center items-center cursor-pointer"
          @click="profile"
        >
          <div>
            <img
              :src="user_info.profile_picture ? user_info.profile_picture : require('@/assets/images/avatar.png')"
              :class="mini ? '' : 'mr-6'"
              alt="User avatar"
              class="rounded-full h-14 w-14"
              style="object-fit: cover"
            >
          </div>
          <div v-if="user && !mini" class="heebo flex flex-col">
            <p class="text-white text-base">{{ user_info.first_name }} {{ user_info.last_name }}</p>
            <p class="text-grey text-sm">{{ user.user_role == 'Leader' ? 'Lder':'' }}</p>
          </div>
        </div>
      </div>
    </div>
    <v-list :class="mini ? '' : 'px-4'" dense class="bg-blue-darker heebo pt-4" dark>
      <p
        v-if="mini ? '': 'hidden'"
        class="text-grey uppercase font-medium tracking-wide text-xs my-4 px-4"
      >Men</p>
      <template v-for="(item, i) in items">
        <v-list-group
          v-if="item.childs && (item.user_access ? checkItem(item.user_access) : true)"
          :key="i"
          :group="item.group"
          :append-icon="mini ? '' : 'keyboard_arrow_right'"
          no-action
          class="mb-3"
        >
          <v-list-tile
            slot="activator"
            :value="IsActive(item.group)"
            class="text-grey font-light"
            active-class="font-normal tracking-wide"
          >
            <v-list-tile-action class="-mr-4">
              <v-icon
                :class="{ 'opacity-25' : !IsActive(item.group) }"
                v-text="`icon-${item.icon}`"
              />
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title
                v-if="!mini"
                class="font-normal tracking-wide text-sm text-white"
              >{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile
            v-for="(subItem, i) in item.childs"
            v-if="subItem.user_access ? checkItem(subItem.user_access) : true"
            :key="i"
            :to="{ name: subItem.url }"
            active-class="text-indigo font-medium tracking-wide heebo"
            class="text-grey -mb-2 font-light"
            ripple
          >
            <v-list-tile-content class="-ml-4">
              <v-list-tile-title class="tracking-wide text-sm">{{ subItem.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list-group>
        <v-list-tile
          v-else-if="item.user_access ? checkItem(item.user_access) : true"
          :key="i"
          :to="{ name: item.url }"
          :disabled="item.disabled"
          exact
          class="text-grey mb-3 heebo font-light"
          active-class="font-medium"
        >
          <v-list-tile-action class="-mr-4">
            <v-icon :class="{ 'opacity-25': !IsActive(item.group) }" v-text="`icon-${item.icon}`"/>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title
              :class="item.disabled ? 'opacity-25' : 'text-white'"
              class="tracking-wide text-sm font-light"
            >{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
      <a
        class="no-underline flex items-center w-full absolute pin-b pin-l py-4 px-8 hover:text-grey text-grey-light hover:bg-blue-darkest"
        @click.prevent="signout"
      >
        <span :class="mini ? '': 'mr-3'" class="icon-logout"/>
        <p v-if="!mini" class="font-base tracking-wide">Cerrar Sesin</p>
      </a>
    </v-list>
    <profile-auth-user/>
    <profile-edit-auth-user/>
    <profile-member/>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapActions, mapState } from "vuex";
import EventBus from "../bus";
export default {
  name: "Navigation",
  middleware: "auth",
  data() {
    return {
      drawer: true,
      items: [
        {
          title: "Inicio",
          group: "dashboard",
          icon: "dashboard",
          url: "dashboard"
        },
        {
          title: "Reportes",
          group: "reports",
          url: "reports-leaders",
          icon: "reports"
        },
        {
          title: "Groups",
          icon: "home",
          group: "groups",
          user_access: [
            "Senior Pastor",
            "Owner",
            "Administrator",
            "District Pastor",
            "Supervisor"
          ],
          childs: [
            {
              title: "Cell Groups",
              url: "cell-groups",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor",
                "Supervisor"
              ]
            },
            {
              title: "Sectors",
              url: "sectors",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor",
                "Supervisors"
              ]
            },
            {
              title: "Zones",
              url: "zones",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor"
              ]
            },
            {
              title: "Districts",
              url: "districts",
              user_access: ["Senior Pastor", "Owner", "Administrator"]
            }
          ]
        },
        {
          title: "Miembros",
          icon: "people",
          group: "members",
          childs: [
            {
              title: "Miembros",
              url: "cell-members",
              user_access: ["all"]
            },
            { title: "Invitados", url: "guests", user_access: ["all"] },
            {
              title: "Leaders",
              url: "leaders",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor",
                "Zone Pastor",
                "Supervisor"
              ]
            },
            {
              title: "Supervisors",
              url: "supervisors",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor",
                "Zone Pastor"
              ]
            },
            {
              title: "Zone Pastors",
              url: "zone-pastors",
              user_access: [
                "Senior Pastor",
                "Owner",
                "Administrator",
                "District Pastor"
              ]
            },
            {
              title: "Districts Pastors",
              url: "district-pastors",
              user_access: ["Senior Pastor", "Owner", "Administrator"]
            }
          ]
        },
        {
          title: "Statistics",
          icon: "statistics",
          group: "statistics",
          user_access: [],
          childs: [
            // { title: "Global", url: "statistics" },
            // { title: "Cell Attendance", url: "statistics" },
            // { title: "Church Attendance", url: "statistics" },
            // { title: "Performance", url: "statistics" },
            // { title: "Donations", url: "statistics" },
            {
              title: "Reports",
              url: "reports-leaders",
              user_access: ["Leader"]
            }
          ]
        },
        {
          title: "News",
          group: "news",
          icon: "notifications",
          url: "news",
          user_access: ["Senior Pastor", "Owner", "Administrator"]
        },
        {
          title: "Guas",
          icon: "guide",
          group: "statistics",
          url: "guides",
          disabled: true
        },
        {
          title: "Estadisticas",
          icon: "statistics",
          group: "statistics",
          url: "statistics",
          disabled: true
        },
        {
          title: "Eventos",
          icon: "events",
          group: "statistics",
          url: "events",
          disabled: true
        }
      ]
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user",
      info: "auth/info"
    }),
    ...mapState({
      user_info: state => state.auth.user_info,
      temporary: state => state.general.temporary,
      mini: state => state.general.mini
    })
  },
  watch: {
    drawer: function(value) {
      if (!value) {
        this.drawer = true;
      }
    }
  },
  methods: {
    ...mapActions({
      logout: "auth/logout",
      updateMiniState: "general/updateMiniState",
      updateTemporaryState: "general/updateTemporaryState",
      toggleTemporary: "general/toggleTemporary"
    }),
    IsActive(group) {
      const str = this.$route.path.split("/");
      return str[1].includes(group);
    },
    async signout() {
      await this.logout();
      this.$router.replace({
        name: "signin"
      });
    },
    checkItem(access) {
      if (access.includes("all")) {
        return true;
      }
      return access.includes(this.user.user_role);
    },
    async profile() {
      const { data } = await window.axios.post("users/profile", {
        user_id: this.user.user_id,
        church_id: 1
      });

      EventBus.$emit("show-profile-auth-user", data.response);
    }
  }
};
</script>
