<template>
  <v-dialog v-model="show"
            max-width="800px">
    <div class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0">
      <div class="relative mb-4 flex items-center justify-between">
        <h1 class="font-normal heebo text-2xl text-blue-darker px-4">Member Profile</h1>
        <div>
          <v-btn icon
                 class="-mr-2"
                 @click="show = false">
            <v-icon class="text-grey">more_vert</v-icon>
          </v-btn>
          <v-btn icon
                 @click="show = false">
            <v-icon class="text-grey">close</v-icon>
          </v-btn>
        </div>
      </div>
      <div class="border-t border-grey-light" />
      <div class="mt-6">
        <div class="flex items-center justify-between p-6">
          <div class="flex items-center">
            <img :src="user.profile_picture"
                 class="rounded-full w-24 h-24 mr-6">
            <div class="text-blue-darkest">
              <p>{{ user.church }}</p>
              <p class="text-2xl font-medium">{{ user.full_name }}</p>
            </div>
          </div>
          <div class="mx-6">
            <img src="@/assets/images/star.svg"
                 alt="Star">
          </div>
          <div class="w-64">
            <div class="w-full flex mb-2 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Phone</p>
              <p class="w-36">{{ user.phone }}</p>
            </div>
            <div class="w-full flex mb-2 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Email</p>
              <p class="w-36">{{ user.email }}</p>
            </div>
            <div class="w-full flex mb-2 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Full address</p>
              <p class="w-36">{{ user.address }}</p>
            </div>
          </div>
        </div>
        <div class="border-t border-grey-light" />
        <div class="flex p-6">
          <div v-for="step in user.steps"
               :key="step.step_id"
               class="mx-3">
            <img src="@/assets/images/star.svg"
                 alt="Star">
          </div>
        </div>
        <div class="border-t border-grey-light" />
        <div class="flex">
          <div class="w-3/5">
            <gmap-map :center="center"
                      :zoom="15"
                      :options="{ mapTypeControl: false }"
                      map-type-id="terrain"
                      class="w-full h-full" />
          </div>
          <div class="p-4">
            <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Gender</p>
              <p class="w-36">{{ user.gender == 'm' ? 'Male' : 'Female' }}</p>
            </div>
            <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Birth Date</p>
              <p class="w-36">{{ formatDate(user.birth_date) }}</p>
            </div>
            <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Marital Status</p>
              <p class="w-36">{{ user.marital_status }}</p>
            </div>
            <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Guest since</p>
              <p class="w-36">{{ formatDate(user.guest_since) }}</p>
            </div>
            <div class="w-full flex mb-3 text-sm text-blue-darkest flex-wrap">
              <p class="w-24 text-sm text-grey">Member Since</p>
              <p class="w-36">{{ formatDate(user.member_since) }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Profile",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      user: [],
      center: {
        lat: 13.713513,
        lng: -89.247708
      }
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info"
    })
  },
  mounted() {
    EventBus.$on("show-profile-member", value => {
      this.user = value;
      this.show = !this.show;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    }
  }
};
</script>
