              </div>
              <div class="w-full flex flex-col mb-2 px-2">
                <label class="w-full text-grey text-sm">Last Name</label>
                <input
                  v-validate="'required'"
                  :class="{ 'has-error' : errors.has('profile-form.last_name') }"
                  v-model="form.last_name"
                  type="text"
                  class="form-input w-full mt-1 text-blue-darker mb-1"
                  name="last_name"
                  data-vv-as="Last Name"
                >
                <small
                  class="text-error flex text-xs tracking-wide"
                >{{ errors.first('profile-form.last_name') }}</small>
              </div>
              <div class="w-full flex flex-col mb-2 px-2">
                <label class="w-full text-grey text-sm">Role</label>
                <v-select
                  v-validate="'required'"
                  :options="roles"
                  :searchable="true"
                  :show-labels="false"
