                  v-model="form.email"
                  type="email"
                  class="form-input w-full mt-1 text-blue-darker mb-1"
                  name="email"
                  data-vv-as="Email"
                >
                <small
                  class="text-error flex text-xs tracking-wide"
                >{{ errors.first('profile-form.email') }}</small>
              </div>
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">Number Phone</label>
                <input
                  v-validate="'required'"
