                  v-model="form.phone"
                  type="text"
                  class="form-input w-full mt-1 text-blue-darker mb-1"
                  name="number_phone"
                  data-vv-as="Number Phone"
                >
                <small
                  class="text-error flex text-xs tracking-wide"
                >{{ errors.first('profile-form.number_phone') }}</small>
              </div>
              <div class="w-full flex flex-col mb-2 w-1/2 px-2">
                <label class="w-full text-grey text-sm">City</label>
                <input
                  v-validate="'required'"
                  :class="{ 'has-error' : errors.has('profile-form.city') }"
                  v-model="form.city"
                  type="text"
                  class="form-input w-full mt-1 text-blue-darker mb-1"
                  name="city"
