    </div>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
export default {
  name: "ProfileEditUser",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      tab: 0,
      menu: false,
      menu_guest: false,
      menu_member: false,
      message: null,
      form: [],
      roles: [
        { id: 1, name: "Senior Pastor" },
        { id: 2, name: "Administrator" },
        { id: 3, name: "Owner" }
      ]
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  watch: {
    menu(val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  mounted() {
    EventBus.$on("show-profile-edit-user", value => {
      this.form = value;
      // this.form.number_phone = value.phone;
      // this.form.first_name = value.first_name;
      // this.form.last_name = value.last_name;
      // this.form.phone = value.phone;
      this.form.role = this.roles.find(r => r.name == value.role);
      this.show = !this.show;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    async saveProfile(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          this.form.role_id = this.form.role.id;
          await window.axios.post("users/edit-profile", this.form);
          this.show = false;
        }
      } catch (e) {
        console.error("Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    async savePassword(scope) {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll(scope);
        if (validate) {
          await window.axios.post("users/edit-password", {
            user_id: this.user.user_id,
            role_id: this.user.role,
            type_user: this.user.type_user,
            current_password: this.form.current_password,
            password: this.form.password
          });

          this.show = false;
        }
      } catch (e) {
        const error = e.response;
        this.message = error.data.error.message;
      } finally {
        this.loading = false;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    },
    save(date) {
      this.$refs.menu.save(date);
    }
  }
};
</script>
