import Vue from "vue";
import VeeValidate, { Validator } from "vee-validate";
import axios from "axios";
import es from "vee-validate/dist/locale/es";

Vue.use(VeeValidate, {
  locale: "es",
  dictionary: {
    es
  }
});

const isUniqueChurch = value => {
  return axios
    .post("/church-name-validation", { church_name: value })
    .then(({ data }) => {
      return {
        valid: data.response.valid === "true" ? true : false,
        data: {
          message: data.response.message
        }
      };
    });
};
Validator.extend("verify_password", {
  getMessage: () =>
    "La contrasea debe contener al menos: 1 letra mayscula, 1 nmero",
  validate: value => {
    const strongRegex = new RegExp("^(?=.*[A-Z])(?=.*[0-9])");
    return strongRegex.test(value);
  }
});

