<template>
  <v-dialog v-model="show" max-width="350px">
    <auth-card :width="350" :padding="0">
      <template slot="header">
        <div class="relative heebo">
          <v-btn icon class="absolute pin-t pin-r -mt-6" @click="show = false">
            <v-icon class="text-grey">close</v-icon>
          </v-btn>
          <h1 class="font-normal text-4xl text-blue-darker">Assign</h1>
          <p class="text-base text-blue-darker">District Pastor</p>
        </div>
      </template>
      <template slot="form">
        <form autocomplete="off" @submit.prevent="submit">
          <div class="px-8 ml-3">
            <div class="mb-4 items-end">
              <div class="flex flex-col w-full mr-3 mb-4">
                <label class="text-left tracking-wide text-sm">Name</label>
                <v-select
                  v-validate="'required'"
                  :options="users"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('name') }"
                  v-model="form.leader"
                  track-by="full_name"
                  label="full_name"
                  placeholder
                  class="border border-solid border-grey-light rounded my-1"
                  name="name"
                  data-vv-as="name"
                />
                <small class="text-error flex">{{ errors.first(`name`) }}</small>
              </div>
              <div class="flex flex-col w-full">
                <label class="text-left tracking-wide text-sm">Disctrict</label>
                <v-select
                  v-validate="'required'"
                  :options="districts"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has('district') }"
                  v-model="form.district"
                  track-by="district_code"
                  label="district_code"
                  placeholder
                  class="border border-solid border-grey-light rounded py-px my-1"
                  name="district"
                  data-vv-as="district"
                />
                <small class="text-error flex">{{ errors.first('district') || error }}</small>
              </div>
            </div>
          </div>
          <div class="border-t opacity-50 mb-8 border-grey-light"/>
          <div class="flex justify-center">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              class="w-1/2 rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
              type="submit"
            >
              <template v-if="!loading">
                <span class="font-semibold tracking-wide">Assign</span>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                >Loading...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
    </auth-card>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
export default {
  name: "Assign",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      users: [],
      districts: [],
      error: null,
      form: {
        leader: null,
        district: null
      }
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  watch: {
    show(newValue) {
      if (newValue) {
        this.form.leader = null;
        this.form.district = null;
      }
    }
  },
  mounted() {
    EventBus.$on("show-assign", value => {
      this.show = value;
    });
    this.getUsers();
    this.getDistricts();
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    async getDistricts() {
      try {
        const { data } = await window.axios.post("general/list-groups", {
          church_id: this.church.church_id,
          user_id: this.user.user_id,
          role_id: this.user.role
        });
        this.districts = data.response;
      } catch (error) {
        return error;
      }
    },
    async getUsers() {
      try {
        const { data } = await window.axios.post("general/list-users", {
          church_id: this.church.church_id
        });
        this.users = data.response;
      } catch (error) {
        return error;
      }
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          await window.axios.post("general/assign", {
            group_id: this.form.district.id,
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.church.church_id,
            leader_id: this.form.leader.id
          });
          this.show = false;
        }
      } catch (e) {
        const { data } = e.response;
        this.error = data.error.message;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
