<template>
  <v-dialog v-model="show"
            max-width="400px">
    <div class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 relative">
      <div class="mb-4 flex items-center justify-center flex-col">
        <img :src="user.profile_picture ? user.profile_picture : 'https://placehold.it/144x144'"
             alt="Profile Picture"
             class="w-36 h-36 mb-2 -mt-100 bg-white rounded-full p-3">
        <p class="text-3xl font-bold text-blue-darkest">{{ user.full_name }}</p>
        <p class="text-grey text-2xl font-bold">{{ user.church }}</p>
        <v-btn icon
               class="absolute pin-t pin-r"
               @click="show = false">
          <v-icon class="text-grey">close</v-icon>
        </v-btn>
      </div>
      <div class="border-t opacity-50 mb-8 border-grey-light" />
      <div class="w-full px-6 mb-4">
        <div class="w-full flex mb-2 text-blue-darkest flex-wrap">
          <p class="w-24 text-grey">Role</p>
          <p class="w-36">{{ user.role }}</p>
        </div>
        <div class="w-full flex mb-2 text-blue-darkest flex-wrap">
          <p class="w-24 text-grey">City</p>
          <p class="w-36">{{ user.city ? user.city : 'Not Available' }}</p>
        </div>
        <div class="w-full flex mb-2 text-blue-darkest flex-wrap">
          <p class="w-24 text-grey">Phone</p>
          <p class="w-36">{{ user.phone ? user.phone : 'Not Available' }}</p>
        </div>
        <div class="w-full flex mb-2 text-blue-darkest flex-wrap">
          <p class="w-24 text-grey">Email</p>
          <p class="w-36">{{ user.email }}</p>
        </div>
      </div>
      <div class="border-t opacity-50 mb-8 border-grey-light" />
      <div class="flex justify-end px-6">
        <button class="rounded py-3 px-16 text-white heebo flex items-center justify-center no-outline bg-blue hover:bg-blue-dark"
                @click="edit()">
          <template v-if="!loading">
            <span class="font-semibold tracking-wide">Edit</span>
          </template>
          <template v-else>
            <p class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide">
              Loading...
            </p>
          </template>
        </button>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ProfileUser",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      user: [],
      center: {
        lat: 13.713513,
        lng: -89.247708
      }
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info"
    })
  },
  mounted() {
    EventBus.$on("show-profile-user", value => {
      this.user = value;
      this.show = !this.show;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    },
    edit() {
      this.show = false;
      EventBus.$emit("show-profile-edit-user", this.user);
    }
  }
};
</script>
