import store from "@/store";

export default async (to, from, next) => {
  if (to.name === "dashboard") {
    await store.dispatch("general/updateMiniState", true);
    await store.dispatch("general/updateTemporaryState", false);
    next();
    return;
  }
  await store.dispatch("general/updateMiniState", false);
  await store.dispatch("general/updateTemporaryState", false);
  next();
};
