<template>
  <v-dialog v-model="show"
            max-width="600px">
    <auth-card :width="600"
               :padding="0">
      <template slot="header">
        <div class="relative">
          <v-btn icon
                 class="absolute pin-t pin-r -mt-6"
                 @click="show = false">
            <v-icon class="text-grey">close</v-icon>
          </v-btn>
          <h1 class="font-normal text-4xl text-blue-darker font-serif">Add administrators</h1>
        </div>
      </template>
      <template slot="form">
        <form autocomplete="off"
              @submit.prevent="submit">
          <div class="px-6 ml-3">
            <div class="flex text-left mb-3 text-xs">
              <div class="w-2/5">Email</div>
              <div class="w-1/6" />
            </div>
            <div v-for="(invite, index) in invitations"
                 :key="index"
                 class="flex mb-4 items-end">
              <div class="flex flex-col w-2/5 h-16 mr-3">
                <input v-validate="'required'"
                       :class="{ 'has-error' : errors.has(`name${index}`) }"
                       :name="`name${index}`"
                       v-model="invite.name"
                       type="text"
                       class="form-input mb-1"
                       placeholder="Name"
                       data-vv-as="name">
                <small class="text-error flex">{{ errors.first(`name${index}`) }}</small>
              </div>
              <div class="flex flex-col w-2/5 h-16">
                <input v-validate="'required|email'"
                       :class="{ 'has-error' : errors.has(`email${index}`) }"
                       :name="`email${index}`"
                       v-model="invite.email"
                       type="email"
                       class="form-input mb-1"
                       placeholder="you@example.com"
                       data-vv-as="email">
                <small class="text-error flex">{{ errors.first(`email${index}`) }}</small>
              </div>
              <div class="w-1/6 flex flex-col items-center justify-center h-16 pb-3">
                <p-check v-model="invite.owner"
                         class="p-svg p-bigger p-round mb-1 mx-0"
                         color="success">
                  <svg slot="extra"
                       class="svg svg-icon p-1"
                       viewBox="0 0 20 20">
                    <path d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                          style="stroke: white;fill:white" />
                  </svg>
                </p-check>
                <span class="text-grey text-13">Owner</span>
              </div>
            </div>
          </div>
          <div class="mt-8 mb-12">
            <a v-if="invitations.length < 5"
               class="no-underline heebo text-blue text-sm hover:underline"
               @click.prevent="incrementFields()">Add more</a>
          </div>
          <div class="border-t opacity-50 mb-8 border-grey-light" />
          <div class="flex justify-end mx-12">
            <button :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                    :disabled="errors.any()"
                    class="w-1/3 rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
                    type="submit">
              <template v-if="!loading">
                <span class="font-semibold tracking-wide">Send</span>
              </template>
              <template v-else>
                <p class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide">
                  Loading...
                </p>
              </template>
            </button>
          </div>
        </form>
      </template>
    </auth-card>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
export default {
  name: "AddUser",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      invitations: [{ name: "", email: "", owner: 0, church_id: "" }]
    };
  },
  computed: mapState({
    church: state => state.auth.church_info
  }),
  watch: {
    show(newValue) {
      if (newValue) {
        this.invitations = [{ name: "", email: "", owner: 0, church_id: "" }];
      }
    }
  },
  mounted() {
    EventBus.$on("show", value => {
      this.show = value;
    });
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    incrementFields() {
      return this.invitations.push({
        name: "",
        email: "",
        owner: 0,
        church_id: ""
      });
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          this.invitations.map(i => {
            i.church_id = this.church.church_id;
          });
          await this.admins(this.invitations);
          this.show = false;
          this.invitations = [{ name: "", email: "", owner: 0, church_id: "" }];
        }
      } catch (e) {
        console.error("Correct them errors!");
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
