<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex flex-col mb-8">
        <div class="flex items-center">
          <h1 class="font-normal text-blue-darkest">District Pastors</h1>
          <p class="mx-3 flex items-center text-sm">
            <v-icon small
                    class="text-blue-darker mx-2">home</v-icon> - Members - District Pastors
          </p>
        </div>
        <div>
          Showing <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ districtPastors.length }}</span>out of {{ districtPastors.length }}
          <a class="px-2 bg-blue-lighter rounded text-blue mx-1"
             @click.prevent="get(active ? false : true)">{{ active ? 'active': 'inactive' }}</a>
          District Pastors
        </div>
      </div>
      <div>
        <div class="mb-3">
          <p class="text-grey tracking-wide mb-1">Filter by</p>
          <div class="flex justify-between">
            <div class="flex">
              <v-select :options="[]"
                        :searchable="true"
                        :show-labels="false"
                        track-by="name"
                        label="name"
                        placeholder="District"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                        name="district" />
              <v-select :options="[]"
                        :searchable="true"
                        :show-labels="false"
                        track-by="name"
                        label="name"
                        placeholder="Zone"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                        name="zone" />
              <v-select :options="[]"
                        :searchable="true"
                        :show-labels="false"
                        track-by="name"
                        label="name"
                        placeholder="Sector"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                        name="sector" />
              <v-select :options="[]"
                        :searchable="true"
                        :show-labels="false"
                        track-by="name"
                        label="name"
                        placeholder="Cell Group"
                        class="border border-solid border-grey-light rounded py-1 w-42 bg-white"
                        name="sector" />
            </div>
            <div class="flex w-full justify-end">
              <input v-model="search"
                     type="text"
                     class="form-input bg-white w-1/3"
                     placeholder="Search by Keywords">
            </div>
          </div>
        </div>
        <v-data-table :headers="headers"
                      :items="districtPastors"
                      :loading="loading"
                      :search="search"
                      class="elevation-0 border border-grey-light rounded">
          <template slot="headerCell"
                    slot-scope="props">
            <span class="uppercase tracking-wide text-grey">
              {{ props.header.text }}
            </span>
          </template>
          <template slot="items"
                    slot-scope="props">
            <td class="flex items-center text-sm text-blue tracking-wide">
              <img :src="props.item.profile_picture ? props.item.profile_picture : require('@/assets/images/avatar.png')"
                   alt="Profile Picture"
                   class="w-8 h-8 bg-white rounded-full mr-3">
              {{ props.item.first_name }} {{ props.item.last_name }}
            </td>
            <td class="text-grey">
              <span class="mx-1 tracking-wide">{{ props.item.district_code ? `D${props.item.district_code}` : '--' }}</span>
            </td>
            <td class="text-center text-sm text-blue tracking-wide">{{ props.item.city ? props.item.city : '--' }}</td>
            <td class="text-center text-base text-blue">{{ props.item.zone_pastors.padStart(2, '0') }}</td>
            <td class="text-center text-base text-blue">{{ props.item.supervisors.padStart(2, '0') }}</td>
            <td class="text-center text-base text-blue">{{ props.item.leaders.padStart(2, '0') }}</td>
            <td class="text-center text-base text-blue">{{ props.item.members.padStart(2, '0') }}</td>
            <td class="text-center">
              <v-menu :nudge-bottom="10"
                      origin="center center"
                      transition="scale-transition"
                      offset-y
                      bottom>
                <v-btn slot="activator"
                       flat
                       icon
                       color="grey"
                       class="no-outline">
                  <v-icon>info_outline</v-icon>
                </v-btn>
                <div>
                  <ul class="p-3 list-reset heebo bg-white w-48">
                    <li class="mb-3 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                         @click="profile(props.item)">
                        <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                        Profile
                      </a>
                    </li>
                    <li class="mb-3 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                        <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                        Edit Profile
                      </a>
                    </li>
                    <li class="mb-3 group">
                      <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                         @click="remove(props.item)">
                        <v-icon class="group-hover:text-blue text-grey">remove_circle_outline</v-icon>
                        Remove
                      </a>
                    </li>
                  </ul>
                </div>
              </v-menu>
            </td>
          </template>
        </v-data-table>
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      top
      color="green"
    >
      USER WAS SUCCESSFULLY REMOVED
      <v-btn
        color="white"
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
    <profile/>
  </div>
</template>

<script>
import { mapState } from "vuex";
import EventBus from "@/bus";

export default {
  name: "DistrictPastorsLeaders",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - District Pastors",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "District Pastor", value: "district_pastor" },
        { text: "District", value: "district_id" },
        { text: "City", value: "city", align: "center" },
        { text: "Zone Pastors", value: "zone_pastors", align: "center" },
        { text: "Supervisors", value: "Aupervisors", align: "center" },
        { text: "Leaders", value: "leaders", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      districtPastors: [],
      active: true,
      loading: true,
      search: "",
      snackbar: false
    };
  },
  computed: mapState({
    user: state => state.auth.user,
    church: state => state.auth.church.info
  }),
  mounted() {
    this.get(true);
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.post(
          "members/list-district-pastors",
          {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.church.church_id,
            parent_id: this.user.group ? this.user.group.parent_id : null,
            active: active ? "1" : "0"
          }
        );
        data.response
          ? (this.districtPastors = data.response)
          : (this.districtPastors = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    async remove(item) {
      try {
        await window.axios.post("members/remove", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.church.church_id,
          leader_id: item.user_id
        });

        this.snackbar = true;
      } catch (error) {
        return error;
      }
    },
    async profile(user) {
      const { data } = await window.axios.post("users/profile", {
        user_id: user.user_id,
        type_user: "member-group"
      });
      EventBus.$emit("show-profile", data.response);
    }
  }
};
</script>
