<template>
  <div>
    <div class="absolute pin-l pin-t p-6 flex items-start">
      <img src="@/assets/images/Logo-c-white.png" alt class="h-14 mr-4">
      <img src="@/assets/images/Logo-white.png" alt class="h-12">
    </div>
    <auth-card :width="470" :padding="0" style="margin-top: 100px" class="relative">
      <template slot="header">
        <div class="flex flex-col items-center justify-center">
          <croppa
            v-model="croppa"
            :height="144"
            :width="144"
            :placeholder-font-size="12"
            placeholder="Upload an Image"
            placeholder-color="#000"
            prevent-white-space
            @init="onInit"
          >
            <img slot="initial" :src="image" crossorigin="anonymous">
          </croppa>
          <h1 class="font-normal heebo text-4xl text-blue-darker">Church Settings</h1>
        </div>
      </template>
      <template slot="form">
        <form class="-mt-4 mb-12" autocomplete="off" @submit.prevent="submit">
          <div class="absolute pin-t">
            <v-btn :to="{ name: 'church-info' }" icon>
              <v-icon class="text-grey-light">keyboard_arrow_left</v-icon>
            </v-btn>
          </div>
          <div class="px-16">
            <div class="hebbo text-left mb-2">
              <label for="role" class="w-full text-grey text-sm">What is your role in this church?</label>
              <v-select
                v-validate="'required'"
                :options="roles"
                :searchable="true"
                :show-labels="false"
                :class="{ 'has-error' : errors.has('role') }"
                v-model="form.rol"
                track-by="name"
                label="name"
                placeholder="Select a Role"
                class="border border-solid border-grey-light rounded py-px my-1"
                name="role"
                data-vv-as="role"
              />
              <small class="text-error flex">{{ errors.first('role') }}</small>
            </div>
            <div class="hebbo text-left mb-3">
              <label
                for="role"
                class="w-full text-grey text-sm"
              >What is the cell structure in this Church?</label>
              <v-select
                v-validate="'required'"
                :options="cellStructures"
                :searchable="true"
                :show-labels="false"
                :class="{ 'has-error' : errors.has('structure') }"
                v-model="form.cell_structure"
                track-by="name"
                label="name"
                placeholder="Select an Option"
                class="border border-solid border-grey-light rounded py-px my-1"
                name="structure"
                data-vv-as="structure"
              />
              <small class="text-error flex">{{ errors.first('structure') }}</small>
            </div>
            <div class="hebbo text-left mb-2 flex justify-between items-end">
              <p class="w-1/4 text-grey text-sm leading-normal">Does your church have?</p>
              <div class="flex">
                <div class="flex flex-col items-center justify-center mx-2">
                  <p-check
                    v-model="form.has_district"
                    class="p-svg p-bigger p-round mb-2 mx-0"
                    color="success"
                    @change="selectDistrict()"
                  >
                    <svg slot="extra" class="svg svg-icon p-1" viewBox="0 0 20 20">
                      <path
                        d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                        style="stroke: white;fill:white"
                      />
                    </svg>
                  </p-check>
                  <span class="text-grey text-13">Districts</span>
                </div>
                <div class="flex flex-col items-center justify-center mx-2">
                  <p-check
                    v-model="form.has_zone"
                    :disabled="form.has_district"
                    class="p-svg p-bigger p-round mb-2 mx-0"
                    color="success"
                    @change="selectZone()"
                  >
                    <svg slot="extra" class="svg svg-icon p-1" viewBox="0 0 20 20">
                      <path
                        d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                        style="stroke: white;fill:white"
                      />
                    </svg>
                  </p-check>
                  <span class="text-grey text-13">Zones</span>
                </div>
                <div class="flex flex-col items-center justify-center mx-2">
                  <p-check
                    v-model="form.has_sector"
                    :disabled="form.has_district || form.has_zone"
                    class="p-svg p-bigger p-round mb-2 mx-0"
                    color="success"
                  >
                    <svg slot="extra" class="svg svg-icon p-1" viewBox="0 0 20 20">
                      <path
                        d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                        style="stroke: white;fill:white"
                      />
                    </svg>
                  </p-check>
                  <span class="text-grey text-13">Sectors</span>
                </div>
              </div>
            </div>
            <div class="hebbo text-left mb-2">
              <label for="role" class="w-full text-grey text-sm">How do you label your structure?</label>
              <v-select
                v-validate="'required'"
                :options="labels"
                :searchable="true"
                :show-labels="false"
                :class="{ 'has-error' : errors.has('labels') }"
                v-model="form.label_structure"
                track-by="name"
                label="name"
                placeholder="Select a Structure"
                class="border border-solid border-grey-light rounded py-px my-1"
                name="labels"
                data-vv-as="label"
              />
              <small class="text-error flex">{{ errors.first('labels') }}</small>
            </div>
            <div class="hebbo text-left mb-2">
              <label
                for="address"
                class="w-full text-grey text-sm"
              >What URL do you want for your Church?</label>
              <div class="flex items-baseline mb-1">
                <input
                  v-validate="churchSettings ? form.subdomain == churchSettings.subdomain ? 'required' :'required' : 'required'"
                  :class="{ 'has-error' : errors.has('url') }"
                  v-model="form.subdomain"
                  type="text"
                  class="form-input w-2/3 mt-2 text-blue-darker mr-4 text-right text-xl"
                  name="url"
                  data-vv-as="URL"
                  data-vv-delay="500"
                >
                <p class="text-xl text-blue-darker">.cplusapp.com</p>
              </div>
              <small class="text-error flex">{{ errors.first('url') }}</small>
            </div>
            <p
              class="text-sm text-grey text-justify leading-normal"
            >Choose the address youll use to sign in to C+.
              <br>Letters, numbers, and dashes only.
            </p>
          </div>
          <div class="border-t my-6 border-grey-light"/>
          <div class="px-16">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              class="w-full rounded py-3 text-white heebo flex items-center justify-center no-outline"
            >
              <template v-if="!loading">
                <span class="mr-6 font-medium text-base heebo tracking-wide">Continue to Goals</span>
                <span class="icon-arrow-right text-sm"/>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                >Loading...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
      <template slot="footer">
        <p class="text-sm text-grey heebo mb-2">Your church already have C+?
          <router-link
            :to="{ name: 'signin-url'}"
            class="no-underline text-blue hover:underline font-normal"
          >Sign In</router-link>
        </p>
      </template>
    </auth-card>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ChurchSettings",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Settings",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      croppa: {},
      loading: false,
      church_info: null,
      image: null,
      roles: [
        { id: 1, name: "Senior Pastor" },
        { id: 2, name: "District Pastor" },
        { id: 3, name: "Zone Pastor" },
        { id: 4, name: "Supervisor" },
        { id: 5, name: "Leader" }
      ],
      cellStructures: [
        { key: "5x5", name: "5 X 5 (District, Zone, Sector, etc)" },
        { key: "G-12", name: "G-12 (Networks, 12, 144, etc)" },
        { key: "other", name: "Other (Custom)" }
      ],
      labels: [
        { id: 1, name: "Letters (DA ZB SC CD)" },
        { id: 2, name: "Numbers (D1 Z2 S3 C4)" },
        { id: 3, name: "Letters+Numbers (DA Z1 SB C2)" },
        { id: 4, name: "Numbers+Letters (D1 ZA S2 CB)" }
      ],
      form: {
        rol: null,
        cell_structure: null,
        label_structure: null,
        subdomain: null,
        has_district: 0,
        has_sector: 0,
        has_zone: 0
      }
    };
  },
  computed: {
    ...mapGetters({
      token: "auth/token",
      churchInfo: "auth/church_info",
      churchSettings: "auth/church_settings"
    })
  },
  mounted() {
    this.initChurch();
  },
  methods: {
    ...mapActions({
      saveChurch: "auth/saveChurchSettings"
    }),
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          let data = new FormData();
          if (!this.croppa.hasImage()) {
            return;
          }
          data.append("image", this.croppa.getChosenFile());
          data.append("rol", this.form.rol.id);
          data.append("cell_structure", this.form.cell_structure.key);
          data.append("label_structure", this.form.label_structure.id);
          data.append("subdomain", this.form.subdomain);
          data.append("has_district", this.form.has_district == true ? 1 : 0);
          data.append("has_sector", this.form.has_sector == true ? 1 : 0);
          data.append("has_zone", this.form.has_zone == true ? 1 : 0);
          data.append("token", this.token);
          data.append("church_id", this.church_info.church_id);

          await this.saveChurch(data);
          this.$router.replace({
            name: "church-goals"
          });
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    onInit() {
      this.croppa.addClipPlugin(function(ctx, x, y, w, h) {
        ctx.beginPath();
        ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true);
        ctx.closePath();
      });
    },
    initChurch() {
      if (this.churchInfo) {
        this.church_info = this.churchInfo;
        this.image = this.churchInfo.profile_picture;
      }

      if (this.churchSettings) {
        this.form.has_district = this.churchSettings.has_district;
        this.form.subdomain = this.churchSettings.subdomain;
        this.form.rol = this.roles.find(
          item => item.id == this.churchSettings.user_role
        );
        this.form.cell_structure = this.cellStructures.find(
          item => item.key == this.churchSettings.structure
        );
        this.form.label_structure = this.labels.find(
          item => item.id == this.churchSettings.label_structure
        );
      }
    },
    selectDistrict() {
      if (this.form.has_district) {
        this.form.has_zone = true;
        this.form.has_sector = true;
      }
    },
    selectZone() {
      if (this.form.has_zone) {
        this.form.has_sector = true;
      }
    }
  }
};
</script>
