<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "TransferMembers",
  data() {
    return {
      show: false,
      showMembers: false,
      showGuests: false,
      transfer_member: {
        member: null,
        target_district: null,
        target_zone: null,
        target_sector: null,
        target_cell: null
      },
      transfer_guest: {
        guest: null,
        target_district: null,
        target_zone: null,
        target_sector: null,
        target_cell: null
      },
      districts: [],
      zones: [],
      sectors: [],
      cells: [],
      members: [],
      guests: []
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    EventBus.$on("show-transfer-members", () => {
      this.show = !this.show;
    });
    this.getMembers();
    this.getGuests();
    this.getGroupsTransfer();
  },
  methods: {
    async getMembers() {
      const { data } = await window.axios.post("cells/members-list", {
        user_id: this.user.user_id,
        rol_id: this.user.role,
        cell_id: this.user.group.parent_id,
        filter_members: "1",
        active: "1"
      });

      this.members = data.response ? data.response : [];
    },
    async getGuests() {
      const { data } = await window.axios.post("cells/members-list", {
        user_id: this.user.user_id,
        rol_id: this.user.role,
        cell_id: this.user.group.parent_id,
        filter_members: "0",
        active: "1"
      });

      this.guests = data.response ? data.response : [];
    },
    async getGroupsTransfer() {
      const { data } = await window.axios.post("groups/list-groups-target", {
        user_id: this.user.user_id,
        role_id: this.user.role
      });

      this.districts = data.response.districts;
      this.zones = data.response.zones;
      this.sectors = data.response.sectors;
      this.cells = data.response.cells;
    },
    async transferMember() {
      await window.axios.post("cells/transfer-member", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        member_id: this.transfer_member.member.id,
        cell_id: this.transfer_member.target_cell.cell_id
      });

      this.showMembers = false;
    },
    async transferGuest() {
      await window.axios.post("cells/transfer-member", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        member_id: this.transfer_member.guest.id,
        cell_id: this.transfer_member.target_cell.cell_id
      });

      this.showGuests = false;
    }
  }
};
</script>
