<template>
  <div>
    <div class="absolute pin-l pin-t p-6 flex items-start">
      <img src="@/assets/images/Logo-c-white.png" alt class="h-14 mr-4">
      <img src="@/assets/images/Logo-white.png" alt class="h-12">
    </div>
    <auth-card :width="470" :padding="0" style="margin-top: 100px">
      <template slot="header">
        <div class="flex flex-col items-center justify-center">
          <croppa
            v-model="croppa"
            :height="144"
            :width="144"
            :placeholder-font-size="14"
            placeholder="Upload an Image"
            placeholder-color="#000"
            prevent-white-space
            @init="onInit"
          >
            <img slot="initial" :src="image" crossorigin="anonymous">
          </croppa>
          <h1 class="font-normal heebo text-4xl text-blue-darker">Profile Info</h1>
        </div>
      </template>
      <template slot="form">
        <form class="-mt-8 mb-8" autocomplete="off" @submit.prevent="submit">
          <div class="px-16">
            <div class="hebbo text-left mb-3">
              <label class="w-full text-grey text-13">First Name</label>
              <input
                v-validate="'required'"
                :class="{ 'has-error' : errors.has('first_name') }"
                v-model="form.first_name"
                type="text"
                class="form-input w-full mt-1 text-blue-darker mb-1"
                name="first_name"
                data-vv-as="First Name"
              >
              <small class="text-error flex text-xs tracking-wide">{{ errors.first('first_name') }}</small>
            </div>
            <div class="hebbo text-left mb-3">
              <label for="last_name" class="w-full text-grey text-13">Last Name</label>
              <input
                v-validate="'required'"
                :class="{ 'has-error' : errors.has('last_name') }"
                v-model="form.last_name"
                type="text"
                class="form-input w-full mt-1 text-blue-darker mb-1"
                name="last_name"
                data-vv-as="Last Name"
              >
              <small class="text-error flex text-xs tracking-wide">{{ errors.first('last_name') }}</small>
            </div>
            <div class="hebbo text-left mb-3">
              <label for="password" class="w-full text-grey text-13">Your Password</label>
              <div class="relative flex items-center mb-1">
                <input
                  v-validate="'required|min:6|confirmed:passwordRef'"
                  id="password"
                  :type="show ? 'text' : 'password'"
                  :class="{ 'has-error' : errors.has('password') }"
                  v-model="form.password"
                  class="form-input w-full mt-1 text-blue-darker"
                  name="password"
                  data-vv-as="Password"
                >
                <span
                  :class="show ? 'icon-eye-off': 'icon-eye'"
                  class="absolute pin-r mr-8 mt-1 text-grey cursor-pointer"
                  @click="show = !show"
                />
              </div>
              <small class="text-error flex text-xs tracking-wide">{{ errors.first('password') }}</small>
            </div>
            <div class="hebbo text-left mb-2">
              <label for="confirm_password" class="w-full text-grey text-13">Confirm Password</label>
              <div class="relative flex items-center mb-1">
                <input
                  id="confirm_password"
                  ref="passwordRef"
                  :type="show_confirm ? 'text' : 'password'"
                  :class="{ 'has-error' : errors.has('confirm_password') }"
                  v-model="form.confirm_password"
                  class="form-input w-full mt-1 text-blue-darker"
                  name="confirm_password"
                  data-vv-as="Confirm Password"
                >
                <span
                  :class="show_confirm ? 'icon-eye-off': 'icon-eye'"
                  class="absolute pin-r mr-8 mt-1 text-grey cursor-pointer"
                  @click="show_confirm = !show_confirm"
                />
              </div>
              <small
                class="text-error flex text-xs tracking-wide"
              >{{ errors.first('confirm_password') }}</small>
            </div>
            <p
              class="mb-12 text-left text-grey text-13 heebo leading-normal"
            >Passwords must be at least 6 characters long, and cant be things like password, 123456 or abcdef.</p>
          </div>
          <div class="border-t border-grey opacity-50 border-solid mt-2 mb-6"/>
          <div class="px-16">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              type="submit"
              class="w-full rounded py-3 text-white flex items-center justify-center no-outline"
            >
              <template v-if="!loading">
                <span class="mr-6 font-medium text-base heebo tracking-wide">Continue to Info</span>
                <span class="icon-arrow-right text-sm"/>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                >Loading...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
      <template slot="footer">
        <p class="text-sm text-grey heebo">Your church already have C+?
          <router-link
            :to="{ name: 'signin-url' }"
            class="no-underline text-blue hover:underline font-normal"
          >Sign In</router-link>
        </p>
      </template>
    </auth-card>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Profile",
  layout: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Profile",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      croppa: {},
      image: null,
      show_confirm: false,
      loading: false,
      form: {
        first_name: "",
        last_name: "",
        password: null,
        confirm_password: null
      }
    };
  },
  computed: {
    ...mapGetters({
      token: "auth/token",
      user: "auth/user"
    })
  },
  mounted() {
    this.initUser();
  },
  methods: {
    ...mapActions({
      saveProfile: "auth/saveProfile"
    }),
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          let data = new FormData();
          data.append("image", this.croppa.getChosenFile());
          data.append("first_name", this.form.first_name);
          data.append("last_name", this.form.last_name);
          data.append("password", this.form.password);
          data.append("token", this.token);

          const _data = await this.saveProfile(data);
          console.log(this.setRoute(_data.response));
          this.$router.replace({
            name: this.setRoute(_data.response)
          });
        }
      } catch (e) {
        console.error(e, "profile error");
      } finally {
        this.loading = false;
      }
    },
    onInit() {
      this.croppa.addClipPlugin(function(ctx, x, y, w, h) {
        ctx.beginPath();
        ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true);
        ctx.closePath();
      });
    },
    initUser() {
      if (this.user) {
        const user = this.user;
        this.form.first_name = user.first_name;
        this.form.last_name = user.last_name;
        this.image = user.profile_picture;
      }
    },
    setRoute(data) {
      switch (data.role) {
        case "1":
          return "church-info";
        case "2":
          return "church-info";
        case "3":
          return "church-info";
        case "4":
          return "church-info";
        case "5":
          return "cell-group-info";
        case "6":
          return "church-info";
      }
    }
  }
};
</script>
