<template>
  <div>
    <div class="absolute pin-l pin-t p-6 flex items-start">
      <img src="@/assets/images/Logo-c-white.png" alt class="h-14 mr-4">
      <img src="@/assets/images/Logo-white.png" alt class="h-12">
    </div>
    <auth-card :width="576" :padding="0" style="margin-top: 100px" class="relative">
      <template slot="header">
        <img
          :src="church.profile_picture ? church.profile_picture: 'https://placehold.it/144x144'"
          alt
          class="rounded-full -mt-100 h-36 w-36"
        >
        <h1 class="font-normal heebo text-4xl text-blue-darker">Church Goals</h1>
        <p class="text-sm text-grey">Per Cell Group</p>
        <div class="flex justify-center pt-2">
          <small v-if="error" class="text-error flex tracking-wide font-semibold">{{ error }}</small>
        </div>
      </template>
      <template slot="form">
        <form autocomplete="off" class="mb-10" @submit.prevent="submit">
          <div class="absolute pin-t">
            <v-btn :to="{ name: 'church-settings' }" icon>
              <v-icon color="grey-light">keyboard_arrow_left</v-icon>
            </v-btn>
          </div>
          <div class="mb-4">
            <div
              class="flex justify-between pl-10 mb-2 text-grey text-xs uppercase font-semibold tracking-wide"
            >
              <p class="w-1/4 text-left">Goal</p>
              <p class="w-1/4">Frequency</p>
              <p class="w-1/4">
                Percentage
                <span
                  :class="{ 'text-red': fullPercentage != 100 }"
                >({{ fullPercentage }}%)</span>
              </p>
              <p class="w-1/4">Value</p>
            </div>
            <div class="border-t opacity-50 mb-2 border-grey-light"/>
            <div v-for="(goal, index) in goals" :key="index">
              <div class="flex justify-between pl-10 items-center mb-2">
                <div class="text-sm tracking-wide w-1/4 text-left text-blue-darker">{{ goal.name }}</div>
                <div class="text-sm tracking-wide w-1/4 flex flex-col items-center px-2">
                  <v-select
                    v-validate="'required'"
                    :options="frequencies"
                    v-model="goal.frequency"
                    :name="`frequency${index}`"
                    :show-labels="false"
                    :class="{ 'has-error' : errors.has(`frequency${index}`) }"
                    :allow-empty="false"
                    track-by="id"
                    label="name"
                    placeholder
                    class="rounded py-px my-1 text-sm"
                    data-vv-as="Frequency"
                  />
                  <small v-if="errors.first(`frequency${index}`)" class="text-error flex">Required</small>
                </div>
                <div class="w-1/4 text-blue text-xl flex justify-center items-center">
                  <a
                    :class="parseFloat(goal.percentage).toFixed(2) == 0.05 ? 'border-grey text-grey pointer-events-none' : 'border-blue-darker text-blue-darker'"
                    class="no-underline border-2 rounded-full w-6 h-6 flex items-center justify-center select-none"
                    @click.prevent="percentage(index, 'subs')"
                  >&#43;</a>
                  <span class="w-12 mx-1">{{ decimalPercentage(goal.percentage) }}%</span>
                  <a
                    :class="parseFloat(goal.percentage).toFixed(2) == 1 ? 'border-grey text-grey pointer-events-none' : 'border-blue-darker text-blue-darker'"
                    class="no-underline border-2 rounded-full w-6 h-6 flex items-center justify-center select-none"
                    @click.prevent="percentage(index, 'add')"
                  >&#43;</a>
                </div>
                <div class="w-1/4">
                  <input
                    v-validate="'required'"
                    :name="`value${index}`"
                    :class="{ 'has-error' : errors.has(`value${index}`) }"
                    v-model.number="goal.value"
                    type="text"
                    class="border border-solid border-grey-ligth h-10 w-12 text-center font-normal text-2xl text-grey rounded"
                    data-vv-as="value"
                  >
                </div>
              </div>
              <div
                v-if="goals.length - 1 != index"
                class="border-t opacity-50 mb-2 border-grey-light"
              />
            </div>
          </div>
          <div class="flex justify-center">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              class="w-3/5 rounded py-3 text-white heebo flex items-center justify-center no-outline"
            >
              <template v-if="!loading">
                <span class="mr-6 font-medium text-base heebo tracking-wide">Send Invitations</span>
                <span class="icon-arrow-right text-sm"/>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-normal heebo flex items-center justify-center animate-loading"
                >Loading...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
      <template slot="footer">
        <p class="text-sm text-grey heebo">Your church already have C+?
          <router-link
            :to="{ name: 'signin-url'}"
            class="no-underline text-blue hover:underline font-normal"
          >Sign In</router-link>
        </p>
      </template>
    </auth-card>
  </div>
</template>

<script>
import AuthCard from "@/components/AuthCard";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ChurchGoals",
  layout: "auth",
  middleware: "guest",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Church Goals",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    AuthCard
  },
  data() {
    return {
      loading: false,
      error: null,
      goals: [
        {
          goal: 1,
          name: "Multiply",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 2,
          name: "Adults attendance",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 3,
          name: "Kids attendance",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 4,
          name: "Church attendance",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 5,
          name: "Salvation",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 6,
          name: "Baptized",
          percentage: 0.05,
          frequency: null,
          value: 0
        },
        {
          goal: 7,
          name: "Scheduled visits",
          percentage: 0.05,
          frequency: null,
          value: 0
        }
      ],
      frequencies: [
        { id: 1, name: "Yearly" },
        { id: 2, name: "Monthly" },
        { id: 3, name: "Quarterly" }
      ]
    };
  },
  computed: {
    ...mapGetters({
      token: "auth/token",
      church: "auth/church_info",
      getGoals: "auth/goals"
    }),
    fullPercentage() {
      const value = this.goals.reduce((sum, item) => sum + item.percentage, 0);
      return (value * 100).toFixed(0);
    }
  },
  mounted() {
    this.initGoals();
  },
  methods: {
    ...mapActions({
      churchGoals: "auth/churchGoals"
    }),
    percentage(index, operation) {
      if (operation === "add") {
        const goal = this.goals[index].percentage;
        this.goals[index].percentage = parseFloat(goal) + 0.05;
        return;
      }
      if (operation === "subs") {
        const goal = this.goals[index].percentage;
        this.goals[index].percentage = parseFloat(goal) - 0.05;
        return;
      }
    },
    decimalPercentage(value) {
      const decimal = value * 100;
      return String(decimal.toFixed(0));
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        var validatePercentage = this.goals.reduce(
          (sum, item) => sum + item.percentage,
          0
        );
        if (validatePercentage.toFixed(1) != 1.0) {
          this.error = "The percetange is not equal to 100%";
          return;
        }
        if (validate) {
          await this.churchGoals({
            goals: this.goals,
            church_id: this.church.church_id
          });
          this.$router.push({
            name: "invitations"
          });
        }
      } catch (e) {
        console.error(e, "Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    initGoals() {
      if (this.getGoals) {
        this.goals.map(g => {
          var go = this.getGoals.find(gg => gg.goal == g.goal);
          g.frequency = this.frequencies.find(f => f.id == go.frequency);
          g.percentage = parseFloat(go.percentage);
          g.value = go.value;
        });
      }
    }
  }
};
</script>
