import axios from "axios";
import Cookies from "js-cookie";
import * as types from "../mutation-types";

// state
export const state = {
  token: Cookies.get("token"),
  code: Cookies.get("code"),
  user: Cookies.get("user") ? Cookies.getJSON("user") : null,
  user_info: Cookies.get("user_info") ? Cookies.getJSON("user_info") : null,
  church: {
    info: Cookies.get("church_info") ? Cookies.getJSON("church_info") : null,
    settings: Cookies.get("church_settings")
      ? Cookies.getJSON("church_settings")
      : null
  },
  subdomain: Cookies.get("subdomain") ? Cookies.get("subdomain") : null,
  goals: Cookies.get("goals") ? Cookies.getJSON("goals") : null,
  info: Cookies.get("register_info") ? Cookies.getJSON("register_info") : null
};

export const getters = {
  token: state => state.token,
  code: state => state.code,
  user: state => state.user,
  church_info: state => state.church.info,
  church_settings: state => state.church.settings,
  subdomain: state => state.subdomain,
  goals: state => state.goals,
  check: state => state.user !== null && state.user.role,
  info: state => state.info
};

export const mutations = {
  [types.SAVE_TOKEN_CODE](state, { token, code = null }) {
    state.token = token;
    state.code = code;
    Cookies.set("token", token, { expires: 0.5 });
    Cookies.set("code", code, { expires: 0.5 });
  },
  [types.SAVE_USER](state, payload) {
    state.user = payload;
    let info = {
      first_name: payload.first_name,
      last_name: payload.last_name,
      profile_picture: payload.profile_picture
    };
    state.user_info = info;
    Cookies.set("user", payload, { expires: 0.5 });
    Cookies.set("user_info", info, { expires: 0.5 });
  },
  [types.UPDATE_USER](state, payload) {
    let info = {
      first_name: payload.first_name,
      last_name: payload.last_name,
      profile_picture: payload.profile_picture
    };
    state.user_info = info;
    Cookies.set("user_info", info, { expires: 0.5 });
  },
  [types.SAVE_CHURCH_INFO](state, payload) {
    state.church.info = payload;
    Cookies.set("church_info", payload, { expires: 0.5 });
  },
  [types.SAVE_CHURCH_SETTINGS](state, payload) {
    state.church.settings = payload;
    Cookies.set("church_settings", payload, { expires: 0.5 });
  },
  [types.SAVE_DOMAIN](state, subdomain) {
    state.subdomain = subdomain;
    Cookies.set("subdomain", subdomain, { expires: 0.5 });
  },
  [types.SAVE_GOALS](state, payload) {
    state.goals = payload;
    Cookies.set("goals", payload, { expires: 0.5 });
  },
  [types.SAVE_INFO](state, payload) {
    state.info = payload;
    Cookies.set("register_info", payload, { expires: 0.5 });
  },
  [types.LOGOUT](state) {
    state.user = null;
    state.subdomain = null;
    state.token = null;
    state.code = null;
    state.church_info = null;
    state.church_settings = null;
    state.goals = null;
    state.info = null;

    Cookies.remove("user");
    Cookies.remove("subdomain");
    Cookies.remove("token");
    Cookies.remove("code");
    Cookies.remove("church_info");
    Cookies.remove("church_settings");
    Cookies.remove("goals");
  }
};

export const actions = {
  async sendCode({ commit }, payload) {
    try {
      const { data } = await axios.post("send-code", payload);
      await commit(types.SAVE_TOKEN_CODE, {
        token: data.response.token,
        code: data.response.verify_code
      });
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async confirmCode() {
    try {
      await axios.post("confirm-code", {
        token: Cookies.get("token"),
        verify_code: Cookies.get("code")
      });
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async saveProfile({ commit }, payload) {
    try {
      const { data } = await axios.post("profile-info", payload, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      });
      delete data.response.message;
      commit(types.SAVE_USER, data.response);
      return data;
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async saveChurchInfo({ commit }, payload) {
    try {
      const { data } = await axios.post("church-info", payload, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      });
      delete data.response.message;
      await commit(types.SAVE_CHURCH_INFO, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async updateChurchInfo({ commit }, payload) {
    try {
      const contentType = {
        headers: { "Content-Type": "multipart/form-data" }
      };

      if (payload.type) {
        await axios.post("church/edit", payload.body, contentType);
        return;
      }

      const { data } = await axios.post(
        "update-church-info",
        payload.body,
        contentType
      );

      delete data.response.message;
      await commit(types.SAVE_CHURCH_INFO, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async saveChurchSettings({ commit }, payload) {
    try {
      const { data } = await axios.post("church-settings", payload, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      });
      delete data.response.message;
      await commit(types.SAVE_CHURCH_SETTINGS, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async login({ commit, dispatch }, payload) {
    try {
      const { data } = await axios.post("login", payload);
      if (payload.remember) {
        await dispatch("remember", data.response.user_id);
      }
      delete data.response.message;
      await commit(types.SAVE_USER, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  }, // eslint-disable-next-line
  async reset({ commit }, payload) {
    try {
      const { data } = await axios.post("forgot-password", payload);
      return data;
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  }, // eslint-disable-next-line
  async remember({ commit }, payload) {
    try {
      axios.post("remember-me", {
        remember_me: 1,
        user_id: payload
      });
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async churchGoals({ commit }, payload) {
    try {
      const { goals, church_id } = payload;
      goals.map(g => {
        g.church_id = church_id;
        g.frequency = g.frequency.id;
        g.percentage = String(g.percentage.toFixed(2));
      });

      const { data } = await axios.post("church-goals", {
        goals: goals
      });

      delete data.response.message;
      await commit(types.SAVE_GOALS, data.response.goals);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async terms({ state, commit }) {
    try {
      const user = state.user;
      await axios.post("review-terms", {
        review_terms: 1,
        user_id: user.user_id
      });
      const { data } = await axios.post("users/profile", {
        user_id: state.user.user_id,
        type_user: "member-group"
      });
      await commit(types.SAVE_USER, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  // eslint-disable-next-line
  async admins({ commit }, payload) {
    try {
      await axios.post("add-admin", { admin: payload });
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async getGeneralInfo({ state, commit }) {
    try {
      const { data } = await axios.post("home", {
        user_id: state.user.user_id,
        role_id: state.user.role,
        church_id: state.user.church_id
      });
      await commit(types.SAVE_INFO, data.response);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async saveInfo({ commit }, payload) {
    try {
      await commit(types.SAVE_INFO, payload);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  logout({ commit }) {
    try {
      commit(types.LOGOUT);
    } catch (e) {
      commit(types.LOGOUT);
      const error = e.response.data.error;

      throw error;
    }
  },
  async saveToken({ commit }, payload) {
    try {
      await commit(types.SAVE_TOKEN_CODE, {
        token: payload.token
      });
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  },
  async updateUser({ commit }, payload) {
    try {
      await commit(types.UPDATE_USER, payload);
    } catch (e) {
      const error = e.response.data.error;

      throw error;
    }
  }
};
