<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapGetters } from "vuex";
export default {
  name: "ProfileMember",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: true,
      user: [],
      active: "profile",
      timeline: [],
      comment: null,
      menu: [],
      date: null,
      center: {
        lat: 13.713513,
        lng: -89.247708
      },
      steps: [
        {
          id: 1,
          name: "Conversin",
          unlock: require("@/assets/images/steps/salvacion.png"),
          next: require("@/assets/images/steps/salvacion-proximo.png"),
          warning: require("@/assets/images/steps/salvacion-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 2,
          name: "Bautismo en Agua",
          unlock: require("@/assets/images/steps/bautismo.png"),
          next: require("@/assets/images/steps/bautismo-proximo.png"),
          warning: require("@/assets/images/steps/bautismo-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 3,
          name: "Espritu Santo",
          unlock: require("@/assets/images/steps/espiritu-santo.png"),
          next: require("@/assets/images/steps/espiritu-santo-proximo.png"),
          warning: require("@/assets/images/steps/espiritu-santo-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        },
        {
          id: 4,
          name: "Servicio",
          unlock: require("@/assets/images/steps/servicio.png"),
          next: require("@/assets/images/steps/servicio-proximo.png"),
          warning: require("@/assets/images/steps/servicio-warning.png"),
          active: false,
          disabled: true,
          step_date: null
        }
      ],
      user_steps: []
    };
  },
  computed: {
    ...mapGetters({
      church: "auth/church_info",
      info: "auth/info",
      auth_user: "auth/user"
    })
  },
  mounted() {
    EventBus.$on("show-profile-member", value => {
      this.user = value;
      this.show = !this.show;
      this.loading = false;
      this.getTimeline();
      this.steps.forEach((s, i) => {
        if (!value.steps) {
          value.steps = [];
        }
        value.steps.forEach(s1 => {
          if (s.id == s1.step_id) {
            s.step_date = s1.step_date;
            if (value.steps.length == i + 1) {
              return;
            }
            this.steps[i + 1].disabled = false;
          }
        });
      });
    });
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    edit() {
      this.show = false;
      EventBus.$emit("edit-profile-member", this.user);
    },
    setSteps(steps) {
      let _steps = [];
      steps.forEach(_s => {
        _steps.push(_s.name);
      });
      this.steps.forEach(s => {
        _steps.includes(s.name) ? (s.active = true) : (s.active = false);
      });
    },
    async getTimeline() {
      try {
        const { data } = await window.axios.post(
          "general/timeline/list-timeline",
          {
            user_id: this.auth_user.user_id,
            role_id: this.auth_user.role,
            church_id: 1,
            type: "member",
            id_timeline_owner: this.user.member_id,
            language: "es",
            start_date: null,
            end_date: null
          }
        );
        this.timeline = data.response;
      } catch (error) {
        console.error(error);
        return error;
      }
    },
    checkImageStep(step, index) {
      if (step.step_date) {
        this.steps[index].disabled = false;
        return step.unlock;
      }
      if (index > 0) {
        if (this.steps[index - 1].step_date) {
          return step.next;
        } else {
          return step.warning;
        }
      }
      this.steps[index].disabled = false;
      return step.next;
    },
    async addCommentTimeline() {
      try {
        await window.axios.post("/general/timeline/add-comments", {
          owner_id: this.auth_user.user_id,
          role_id: this.auth_user.role,
          church_id: 1,
          type: "member",
          commented_id: this.user.member_id,
          comment: this.comment
        });
        this.comment = null;
        this.getTimeline();
      } catch (error) {
        return error;
      }
    },
    selectDate(date, step, index) {
      window.axios.post("members/cell-members/assign-steps", {
        user_id: this.auth_user.user_id,
        role_id: this.auth_user.role,
        member_id: this.user.member_id,
        church_id: 1,
        step_id: step.id,
        step_name: step.name,
        step_date: date
      });
      this.steps[index].step_date = date;
      this.steps[index + 1].disabled = false;
    }
  },
  async edit() {
    const { data } = await window.axios.get("members/cell-members/profile", {
      params: {
        user_id: this.auth_user.user_id,
        role_id: this.auth_user.role,
        church_id: this.auth_user.church_id,
        member_id: this.user.member_id
      }
    });
    EventBus.$emit("edit-profile-member", data.response);
  }
};
</script>
