<template>
  <v-dialog :value="true" scrollable fullscreen transition="dialog-transition">
    <div class="bg-white h-auto pt-8 pb-6 text-center shadow w-screen relative">
      <router-link
        :to="{ name: 'districts-profile', params: {id: $route.params.id}}"
        class="absolute pin-t pin-r p-6"
      >
        <v-icon class="text-grey">close</v-icon>
      </router-link>
      <div class="flex justify-center items-center h-full font-serif w-full">
        <div class="flex w-full justify-center">
          <div class="w-full flex flex-col">
            <div class="flex flex-col mb-10">
              <h1 class="text-4xl font-normal tracking-wide text-blue-darkest">District
                <span v-if="form.district_code">{{ form.district_code }}</span>
              </h1>
              <p class="text-xl text-blue-darkest">Edit Profile</p>
            </div>
            <div class="flex justify-center w-full">
              <form class="w-1/2" @submit.prevent="save">
                <div class="flex w-full justify-between items-end mb-3 flex-wrap">
                  <div class="w-1/2 px-4 text-left">
                    <label
                      class="text-sm text-grey tracking-wide"
                    >District Name (Based on structure)
                      <span class="text-red font-bolf text-lg">*</span>
                    </label>
                    <input
                      v-model="form.district_code"
                      type="text"
                      class="form-input w-full py-3 mt-1 text-center focus:border-blue focus:bg-blue-lightest"
                    >
                  </div>
                  <div class="w-1/2 px-4 text-left">
                    <label class="text-sm text-grey tracking-wide">Parent District</label>
                    <v-select
                      v-validate="'required'"
                      :options="districts"
                      :searchable="true"
                      :show-labels="false"
                      :class="{ 'has-error' : errors.has('parent_district') }"
                      v-model="form.parent_district"
                      allow-emp
                      name="parent_district"
                      track-by="district_id"
                      label="district_id"
                      placeholder
                      class="border border-solid border-grey-light rounded w-full py-px mt-1"
                      data-vv-as="Parent District"
                    >
                      <template slot="singleLabel" slot-scope="props">
                        <div class="flex items-center">
                          <div class="mx-2">
                            <span class="text-sm">District {{ props.option.district_code }}</span>
                          </div>
                        </div>
                      </template>
                      <template slot="option" slot-scope="props">
                        <div class="flex items-center">
                          <div class="mx-2">
                            <span class="text-sm">District {{ props.option.district_code }}</span>
                          </div>
                        </div>
                      </template>
                    </v-select>
                  </div>
                </div>
                <div class="flex w-full justify-between items-end mb-12">
                  <div class="w-1/2 px-4 text-left">
                    <label class="text-sm text-grey tracking-wide">District Pastor</label>
                    <v-select
                      v-validate="'required'"
                      :options="pastors"
                      :searchable="true"
                      :show-labels="false"
                      :class="{ 'has-error' : errors.has('parent_district') }"
                      v-model="form.district_pastor"
                      name="parent_district"
                      track-by="user_id"
                      label="user_id"
                      placeholder
                      class="border border-solid border-grey-light rounded py-px mt-1"
                      data-vv-as="Parent District"
                    >
                      <template slot="singleLabel" slot-scope="props">
                        <div class="flex items-center m-0 p-0">
                          <img
                            :src="props.option.profile_picture ? props.option.profile_picture : require('@/assets/images/avatar.png')"
                            alt="Profile Picture"
                            class="w-6 h-6 rounded-full"
                          >
                          <div class="mx-2">
                            <span
                              class="text-sm"
                            >{{ props.option.first_name }} {{ props.option.last_name }}</span>
                          </div>
                        </div>
                      </template>
                      <template slot="option" slot-scope="props">
                        <div class="flex items-center m-0">
                          <img
                            :src="props.option.profile_picture ? props.option.profile_picture : require('@/assets/images/avatar.png')"
                            alt="Profile Picture"
                            class="w-6 h-6 rounded-full"
                          >
                          <div class="mx-2">
                            <span
                              class="text-sm"
                            >{{ props.option.first_name }} {{ props.option.last_name }}</span>
                          </div>
                        </div>
                      </template>
                    </v-select>
                    <p
                      v-if="!form.district_pastor"
                      class="text-xs tracking-wide pt-2 text-grey-dark"
                    >Add to
                      <a
                        href
                        class="no-underline"
                        @click.prevent="showInvitation = true"
                      >Invite a New District Pastor</a>
                    </p>
                  </div>
                </div>
                <div class="flex w-full justify-end mb-3">
                  <router-link
                    :to="{ name: 'districts-profile', params: {id: $route.params.id}}"
                    class="rounded border border-solid border-grey px-10 mx-2 no-underline text-grey text-sm h-10 flex items-center font-medium tracking-wide hover:bg-grey-lightest"
                  >Cancel</router-link>
                  <button
                    type="submit"
                    class="rounded border border-solid border-blue-darker bg-blue-darker text-white px-10 mx-2 text-sm h-10 items-center font-medium tracking-wide hover:bg-blue-darkest"
                  >Save</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <v-dialog v-model="showInvitation" max-width="450px">
      <auth-card :width="450" :padding="0">
        <template slot="header">
          <div class="relative font-serif">
            <v-btn icon class="absolute pin-t pin-r -mt-6" @click="showInvitation = false">
              <v-icon class="text-grey">close</v-icon>
            </v-btn>
            <h1 class="font-normal text-4xl text-blue-darker">Send Invitation</h1>
            <div class="flex justify-center items-center">
              <p
                class="text-base text-white font-bold text-2xl bg-purple mx-1 h-16 w-16 p-4 rounded-full"
              >D{{ district.district_code }}</p>
              <p
                class="text-base text-blue-darker font-bold text-2xl mx-1"
              >D{{ district.district_code }}</p>
            </div>
          </div>
        </template>
        <template slot="form">
          <form autocomplete="off" @submit.prevent="submit">
            <div class="px-8">
              <div class="flex text-left mb-3 text-sm">
                <div class="px-1 tracking-wide font-medium">Email</div>
              </div>
              <div class="flex mb-4 items-end">
                <div class="flex flex-col h-16 w-full">
                  <input
                    v-validate="'required'"
                    :class="{ 'has-error' : errors.has('email') }"
                    v-model="invitation.email"
                    name="email"
                    type="email"
                    class="form-input mb-1 focus:border-blue w-full"
                    placeholder="Email"
                    data-vv-as="email"
                  >
                  <small class="text-error flex">{{ errors.first('email') }}</small>
                </div>
              </div>
            </div>
            <div class="border-t opacity-50 mb-8 border-grey-light"/>
            <div class="flex justify-center">
              <button
                :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                :disabled="errors.any()"
                class="w-1/3 rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
                @click.prevent="showInvitation = false"
              >
                <span class="font-semibold tracking-wide">Send</span>
              </button>
            </div>
          </form>
        </template>
      </auth-card>
    </v-dialog>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "DistrictEdit",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Edit District",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      district: [],
      districts: [],
      pastors: [],
      showInvitation: false,
      invitation: {
        email: ""
      },
      form: {
        district_code: null,
        parent_district: null,
        district_pastor: null
      }
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    this.get();
    this.getDistricts();
    this.getPastors();
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("districts/profile", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          type_user: "member-group",
          district_id: this.$route.params.id
        });
        this.district = data.response;
        this.form.district_code = data.response.district_code;
        this.getCurrentPastor(data.response.district_pastor_id);
      } catch (error) {
        return error;
      }
    },
    async getDistricts() {
      try {
        const { data } = await window.axios.post("districts/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: "1"
        });
        this.districts = data.response;
        this.form.parent_district = this.districts.find(
          d => d.district_id == this.district.parent_id
        );
      } catch (error) {
        return error;
      }
    },
    async getPastors() {
      try {
        const { data } = await window.axios.post("groups/list-user-assign", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.church.church_id
        });
        this.pastors = data.response ? data.response : [];
      } catch (error) {
        return error;
      }
    },
    async save() {
      try {
        await window.axios.post("districts/edit", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.church.church_id,
          district_id: this.$route.params.id,
          district_code: this.district.district_code,
          parent_district: this.form.parent_district
            ? this.form.parent_district.district_id
            : null,
          district_pastor: this.form.district_pastor.user_id
        });

        this.$router.replace({
          name: "districts-profile",
          params: { id: this.$route.params.id }
        });
      } catch (error) {
        console.log(error);
        return error;
      }
    },
    async getCurrentPastor(user_id) {
      try {
        const { data } = await window.axios.post("users/profile", {
          user_id: user_id,
          type_user: "member-group"
        });
        this.pastors.push({
          first_name: data.response.first_name,
          last_name: data.response.last_name,
          profile_picture: data.response.profile_picture,
          user_id: data.response.user_id
        });
        this.form.district_pastor = this.pastors.find(
          p => p.user_id == this.district.district_pastor_id
        );
      } catch (error) {
        return error;
      }
    },
    async sendInvitation() {
      try {
        const { data } = await window.axios.post("general/invitations-edit", {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.church.church_id,
          email: this.invitation.email
        });

        this.form.district_pastor = data.response.id;
      } catch (error) {
        return error;
      }
    }
  }
};
</script>
