import store from "@/store";

export default async (to, from, next) => {
  if (to.query.type === "new_password") {
    next({ name: "new-password", params: { email: to.query.email } });
  }
  if (to.query.type === "login") {
    next({ name: "signin", params: { email: to.query.church_id } });
  }
  if (store.getters["auth/check"] && store.getters["auth/user"].role) {
    try {
      await store.dispatch("auth/getGeneralInfo");
    } catch (e) {
      console.error(e);
    }
  }

  next();
};
