<template>
  <v-dialog :value="true" scrollable fullscreen transition="dialog-transition">
    <div class="bg-white h-auto pt-8 pb-6 text-center shadow w-screen relative">
      <router-link
        :to="{ name: 'cell-groups-profile', params: {id: $route.params.id}}"
        class="absolute pin-t pin-r p-6"
      >
        <v-icon class="text-grey">close</v-icon>
      </router-link>
      <div class="flex justify-center items-center h-full font-serif w-full">
        <div class="flex w-full justify-center">
          <div class="w-full flex flex-col">
            <div class="flex flex-col mb-10 items-center">
              <div
                class="rounded-full -mt-100 h-32 w-32 bg-blue text-white flex items-center justify-center text-5xl mb-3"
              >C{{ cell.cell_code }}</div>
              <h1
                class="text-4xl font-normal tracking-wide text-blue-darkest"
              >D0 Z{{cell.zone_code}} S{{cell.sector_code}} C{{cell.cell_code}}</h1>
              <p class="text-xl text-blue-darkest">Editar perfil</p>
            </div>
            <div class="flex justify-center w-full">
              <ValidationObserver ref="observer">
                <form slot-scope="{ invalid }" class="w-1/2" @submit.prevent="submit">
                  <div class="flex w-full justify-between items-end mb-3">
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Da de reunin</label>
                      <ValidationProvider name="da de reunin" rules="required">
                        <template slot-scope="{ errors, valid }">
                          <v-select
                            :error-messages="errors"
                            :success="valid"
                            :items="meets"
                            v-model="cell.meets"
                            name="meets"
                            class="m-0 p-0 text-sm"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Hora de reunin</label>
                      <ValidationProvider name="hora de reunin" rules="required">
                        <template slot-scope="{ errors, valid }">
                          <v-select
                            :error-messages="errors"
                            :success="valid"
                            :items="hours"
                            v-model="cell.meets_time"
                            name="meets_time"
                            class="m-0 p-0 text-sm"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                  </div>
                  <div class="flex w-full justify-between items-end mb-3">
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Direccin</label>
                      <ValidationProvider name="direccin" rules="required">
                        <template slot-scope="{ errors, valid }">
                          <v-text-field
                            :error-messages="errors"
                            :success="valid"
                            v-model="cell.address"
                            name="address"
                            class="m-0 p-0"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                    <div class="w-1/2 px-4 text-left">
                      <label for="zip_code" class="w-full text-grey text-sm">Cdigo Postal</label>
                      <ValidationProvider name="cdigo postal" rules="required|min:4">
                        <template slot-scope="{ errors, valid }">
                          <v-text-field
                            :error-messages="errors"
                            :success="valid"
                            v-model="cell.zip_code"
                            name="zip_code"
                            class="m-0 p-0"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                  </div>
                  <div class="flex w-full justify-between items-end mb-3">
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Ciudad</label>
                      <ValidationProvider name="ciudad" rules="required|min:4">
                        <template slot-scope="{ errors, valid }">
                          <v-text-field
                            v-model="cell.city"
                            :success="valid"
                            :error-messages="errors"
                            name="city"
                            class="m-0 p-0"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                    <div class="w-1/2 px-4 text-left">
                      <label for="phone_number" class="text-sm text-grey tracking-wide">Telfono</label>
                      <ValidationProvider name="telfono" rules="required|min:4">
                        <template slot-scope="{ errors, valid }">
                          <v-text-field
                            v-model="cell.phone_number"
                            :success="valid"
                            :error-messages="errors"
                            name="phone_number"
                            class="m-0 p-0"
                          />
                        </template>
                      </ValidationProvider>
                    </div>
                  </div>
                  <div class="flex w-full justify-between items-end mb-12">
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Fecha de inicio</label>
                      <v-menu
                        ref="menu"
                        :close-on-content-click="false"
                        v-model="since"
                        :nudge-right="40"
                        lazy
                        transition="scale-transition"
                        offset-y
                        full-width
                        class="m-0 p-0"
                        min-width="290px"
                      >
                        <v-text-field
                          v-validate="'required'"
                          slot="activator"
                          :value="formatDate(cell.since)"
                          :error-messages="errors.collect('since')"
                          name="since"
                          readonly
                          class="h-12 text-sm m-0 p-0"
                        >
                          <v-icon slot="prepend-inner" class="text-xl text-grey">calendar_today</v-icon>
                        </v-text-field>
                        <v-date-picker
                          ref="picker"
                          v-model="cell.since"
                          :max="new Date().toISOString().substr(0, 10)"
                          min="1950-01-01"
                          no-title
                          @input="since = false"
                        />
                      </v-menu>
                    </div>
                    <div class="w-1/2 px-4 text-left">
                      <label class="text-sm text-grey tracking-wide">Anfitrin</label>
                      <v-select
                        :items="hosts"
                        v-model="cell.host_id"
                        item-text="full_name"
                        item-value="id"
                        placeholder="Elige un miembro"
                        name="host"
                        class="m-0 p-0 text-sm"
                      >
                        <template slot="selection" slot-scope="{ item, index }">
                          <v-chip
                            close
                            color="teal"
                            class="h-8"
                            text-color="white"
                            @input="cell.host_id = null"
                          >
                            <v-avatar>
                              <img
                                :src="item.profile_picture ? item.profile_picture: require('@/assets/images/avatar.png')"
                                alt="trevor"
                                class="object-cover"
                              >
                            </v-avatar>
                            <span class="w-24">{{ item.full_name }}</span>
                          </v-chip>
                        </template>
                      </v-select>
                    </div>
                  </div>
                  <div class="flex justify-end mb-3">
                    <div>
                      <button
                        :class="[ invalid ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
                        :disabled="invalid"
                        type="submit"
                        class="w-36 rounded py-3 text-white flex items-center justify-center no-outline"
                      >
                        <template v-if="!loading">
                          <span class="font-semibold tracking-wide">Guardar</span>
                        </template>
                        <template v-else>
                          <p
                            class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                          >Cargando...</p>
                        </template>
                      </button>
                    </div>
                  </div>
                </form>
              </ValidationObserver>
            </div>
          </div>
        </div>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";
import { ValidationProvider, ValidationObserver } from "vee-validate";
export default {
  name: "CellGroupEdit",
  layout: "auth",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Editar Celula",
    htmlAttrs: {
      lang: "en"
    }
  },
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      loading: false,
      districts: [],
      pastors: [],
      leaders: [],
      zones: [],
      hours: [
        "4:00 PM",
        "5:00 PM",
        "6:00 PM",
        "7:00 PM",
        "8:00 PM",
        "9:00 PM",
        "10:00 PM"
      ],
      since: null,
      cell: {
        host: null,
        since: null
      },
      hosts: [],
      form: {
        meet: null,
        zip_code: null
      },
      meets: ["Jueves", "Viernes", "Sabado"]
    };
  },
  computed: mapState({
    user: state => state.auth.user,
    church: state => state.auth.church.info
  }),
  mounted() {
    this.get();
    this.getHosts();
    this.host = this.user;
  },
  methods: {
    async get() {
      try {
        const { data } = await window.axios.post("cells/profile", {
          cell_id: this.$route.params.id,
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: 1
        });
        this.cell = data.response;
        this.cell.user_id = this.user.user_id;
        this.cell.role_id = this.user.role;
        this.cell.church_id = 1;
      } catch (error) {
        return error;
      }
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          await window.axios.put("cells/edit", this.cell);

          this.$router.replace({
            name: "cell-groups-profile",
            params: { id: this.$route.params.id }
          });
        }
      } catch (e) {
        console.error("Correct them errors!");
      } finally {
        this.loading = false;
      }
    },
    async getHosts() {
      try {
        const { data } = await window.axios.get("cells/member-list", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: 1,
            cell_id: this.$route.params.id
          }
        });
        this.hosts = data.response == null ? [] : data.response;
      } catch (error) {
        return error;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    }
  }
};
</script>
