<template>
  <v-dialog v-model="show" max-width="500px">
    <auth-card :width="500" :padding="0">
      <template slot="header">
        <div class="relative font-serif">
          <v-btn icon class="absolute pin-t pin-r -mt-6" @click="show = false">
            <v-icon class="text-grey">close</v-icon>
          </v-btn>
          <h1 class="font-normal text-4xl text-blue-darker">Send Invitations</h1>
          <p class="text-base text-blue-darker">District Pastor</p>
        </div>
      </template>
      <template slot="form">
        <form autocomplete="off" @submit.prevent="submit">
          <div class="px-8 ml-3">
            <div class="flex text-left mb-3 text-sm">
              <div class="w-3/4 px-1 tracking-wide font-medium">Email</div>
              <div class="w-1/3 px-1 tracking-wide font-medium">District</div>
            </div>
            <div v-for="(invite, index) in invitations" :key="index" class="flex mb-4 items-end">
              <div class="flex flex-col w-3/4 mr-3 h-16">
                <input
                  v-validate="'required'"
                  :class="{ 'has-error' : errors.has(`email${index}`) }"
                  :name="`email${index}`"
                  v-model="invite.email"
                  type="email"
                  class="form-input mb-1 focus:border-blue"
                  placeholder="Email"
                  data-vv-as="email"
                >
                <small class="text-error flex">{{ errors.first(`email${index}`) || error }}</small>
              </div>
              <div class="flex flex-col w-1/3 h-16">
                <v-select
                  v-validate="'required'"
                  :options="districts"
                  :searchable="true"
                  :show-labels="false"
                  :class="{ 'has-error' : errors.has(`district${index}`) }"
                  v-model="invite.district"
                  :name="`district${index}`"
                  track-by="district_code"
                  label="district_code"
                  placeholder
                  class="border border-solid border-grey-light rounded py-px mb-1"
                  data-vv-as="district"
                />
                <small class="text-error flex">{{ errors.first(`district${index}`) }}</small>
              </div>
            </div>
          </div>
          <div class="mt-8 mb-6">
            <a
              v-if="invitations.lenght < 5"
              class="no-underline heebo text-blue text-sm hover:underline"
              @click.prevent="incrementFields()"
            >Add more</a>
          </div>
          <div class="border-t opacity-50 mb-8 border-grey-light"/>
          <div class="flex justify-center">
            <button
              :class="[ errors.any() ? 'bg-grey' : 'bg-blue-darker hover:bg-blue-darkest' ]"
              :disabled="errors.any()"
              class="w-1/3 rounded py-3 px-6 text-white heebo flex items-center justify-center no-outline"
              type="submit"
            >
              <template v-if="!loading">
                <span class="font-semibold tracking-wide">Send</span>
              </template>
              <template v-else>
                <p
                  class="text-white text-base font-medium heebo flex items-center justify-center animate-loading tracking-wide"
                >Cargando...</p>
              </template>
            </button>
          </div>
        </form>
      </template>
    </auth-card>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import AuthCard from "@/components/AuthCard";
import { mapActions, mapState } from "vuex";
export default {
  name: "SendInvitations",
  components: {
    AuthCard
  },
  data() {
    return {
      show: false,
      loading: false,
      error: null,
      invitations: [
        {
          email: "",
          district: "",
          group_id: "",
          user_id: "",
          church_id: "",
          role_id: ""
        }
      ],
      districts: []
    };
  },
  computed: mapState({
    church: state => state.auth.church_info,
    user: state => state.auth.user
  }),
  watch: {
    show(newValue) {
      if (newValue) {
        this.invitations = [{ name: "", email: "", owner: 0, church_id: "" }];
      }
    }
  },
  mounted() {
    EventBus.$on("show-send-invitation", value => {
      this.show = value;
    });
    this.getDistricts();
  },
  methods: {
    ...mapActions({
      admins: "auth/admins"
    }),
    incrementFields() {
      return this.invitations.push({
        email: null,
        district: "",
        group_id: "",
        user_id: "",
        church_id: "",
        role_id: ""
      });
    },
    async getDistricts() {
      try {
        const { data } = await window.axios.post("districts/list", {
          user_id: this.user.user_id,
          rol_id: this.user.role,
          active: 1
        });
        this.districts = data.response;
      } catch (error) {
        return error;
      }
    },
    async submit() {
      this.loading = true;
      try {
        const validate = await this.$validator.validateAll();
        if (validate) {
          this.invitations.map(i => {
            i.group_id = i.district.district_id;
            i.user_id = this.user.user_id;
            i.role_id = this.user.role;
            i.church_id = this.church.church_id;
          });
          await window.axios.post("general/send-invitations", {
            invitations: this.invitations
          });
          this.show = false;
          this.invitations = [
            {
              email: "",
              district: "",
              group_id: "",
              user_id: "",
              church_id: "",
              role_id: ""
            }
          ];
        }
      } catch (e) {
        const { data } = e.response;
        this.error = data.error.message;
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>
