<template>
  <v-dialog v-model="show" max-width="500px" class="rounded-lg">
    <div class="bg-white rounded-lg h-auto shadow relative">
      <div>
        <v-btn icon class="absolute pin-t pin-r -mt-10 -mr-10" @click="show = false">
          <v-icon class="text-grey-lighter">close</v-icon>
        </v-btn>
      </div>
      <div class="flex justify-between items-center px-4 bg-grey-light rounded-t-lg pt-10">
        <div class="text-center">
          <p class="text-sm text-2xl">{{ report.name ? report.name : '' }}</p>
        </div>
        <div class="flex justify-center">
          <button
            class="border border-solid border-grey rounded-l h-10 w-10 hover:bg-grey-lightest"
          >
            <v-icon class="text-blue-light p-1">print</v-icon>
          </button>
          <button
            class="border border-solid border-grey rounded-r h-10 w-10 hover:bg-grey-lightest"
            @click="download"
          >
            <v-icon class="text-blue-light p-1">picture_as_pdf</v-icon>
          </button>
        </div>
        <div class="text-center">
          <button
            :class="[ errors.any() ? 'bg-grey' : 'bg-blue hover:bg-blue-dark' ]"
            :disabled="errors.any()"
            class="rounded py-3 w-32 text-white heebo flex items-center justify-center no-outline mx-1"
            @click.prevent="createReport"
          >Editar</button>
        </div>
      </div>
      <div>
        <div class="border-b-2">
          <div class="flex justify-between w-full p-6">
            <div class="w-1/3">
              <p class="text-grey text-sm">Creado por</p>
              <p
                class="text-blue-darker text-xs"
              >{{ report.creator_name }} {{ report.creator_last_name }}</p>
            </div>
            <div class="w-1/5">
              <p class="text-grey text-sm">Fecha</p>
              <p class="text-blue-darker text-xs">{{ formatDate(report.creation_date) }}</p>
            </div>
            <div class="w-1/5">
              <p class="text-grey text-sm">Ao</p>
              <p class="text-blue-darker text-xs">{{ report.year }}</p>
            </div>
            <div class="w-1/5">
              <p class="text-grey text-sm">Trimestre</p>
              <p class="text-blue-darker text-xs">{{ report.quarter }}</p>
            </div>
            <div class="w-1/5">
              <p class="text-grey text-sm">Semana</p>
              <p class="text-blue-darker text-xs">{{ report.week }}</p>
            </div>
          </div>
          <div class="border-t-2"/>
          <div class="flex justify-between border-b-2 px-6 py-3 text-sm">
            <p class="uppercase font-medium heebo">Asistencia a Clula</p>
            <p class="font-medium heebo">{{ sumCellAssistance }}</p>
          </div>
          <div class="px-6 py-2 py-2 text-sm">
            <div class="flex justify-between py-px">
              <p class="text-grey">Miembros</p>
              <p class="text-grey">{{ report.total_member_assistance }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Nios</p>
              <p class="text-grey">{{ report.total_kids_assistance }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Invitados</p>
              <p class="text-grey">{{ report.total_guest_assistance }}</p>
            </div>
          </div>
        </div>
        <div class="border-b-2">
          <div class="flex justify-between border-b-2 px-6 py-3 text-sm">
            <p class="uppercase font-medium heebo">Asistencia a Iglesia</p>
            <p class="font-medium heebo">{{ sumChurchAssistance }}</p>
          </div>
          <div class="px-6 py-2 py-2 text-sm">
            <div class="flex justify-between py-px">
              <p class="text-grey">Doctrina</p>
              <p class="text-grey">{{ report.total_doctrine }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Celebracin</p>
              <p class="text-grey">{{ report.total_celebration }}</p>
            </div>
          </div>
        </div>
        <div class="border-b-2">
          <div class="flex justify-between border-b-2 px-6 py-3 text-sm">
            <p class="uppercase font-medium heebo">Rendimiento</p>
            <p class="font-medium heebo">{{ sumPerformance }}</p>
          </div>
          <div class="px-6 py-2 py-2 text-sm">
            <div class="flex justify-between py-px">
              <p class="text-grey">Conversin</p>
              <p class="text-grey">{{ report.total_salvation }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Bautismo</p>
              <p class="text-grey">{{ report.total_baptized }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Visitas Programadas</p>
              <p class="text-grey">{{ report.total_schedule_visits }}</p>
            </div>
          </div>
        </div>
        <div>
          <div class="flex justify-between border-b-2 px-6 py-3 text-sm">
            <p class="uppercase font-medium heebo">Donaciones</p>
            <p class="font-medium heebo">$ {{ sumDonations }}</p>
          </div>
          <div class="px-6 py-2 text-sm">
            <div class="flex justify-between py-px">
              <p class="text-grey">Ofrenda</p>
              <p class="text-grey">$ {{ report.donations_offering }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Eventos</p>
              <p class="text-grey">$ {{ report.donations_events }}</p>
            </div>
            <div class="flex justify-between py-px">
              <p class="text-grey">Templo</p>
              <p class="text-grey">$ {{ report.donations_transport }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";
import EventBus from "@/bus";
export default {
  name: "DialogDetailReport",
  data() {
    return {
      show: false,
      report: {
        name: "No disponible",
        creator_name: "No disponible",
        creator_last_name: "",
        year: "N/D",
        quarter: "N/D",
        week: "N/D",
        donations_offering: "0.00",
        donations_events: "0.00",
        donations_transport: "0.00",
        total_donations: 0,
        total_member_assistance: 0,
        total_kids_assistance: 0,
        total_guest_assistance: 0,
        total_baptized: 0,
        total_salvation: 0,
        total_schedule_visits: 0,
        total_doctrine: 0,
        total_celebration: 0,
        report_id: null
      }
    };
  },
  computed: {
    ...mapState({
      church: state => state.auth.church.info,
      user: state => state.auth.user
    }),
    sumCellAssistance() {
      let sum =
        parseFloat(this.report.total_member_assistance) +
        parseFloat(this.report.total_kids_assistance) +
        parseFloat(this.report.total_guest_assistance);
      return sum;
    },
    sumChurchAssistance() {
      let sum =
        parseFloat(this.report.total_doctrine) +
        parseFloat(this.report.total_celebration);
      return sum;
    },
    sumDonations() {
      let sum =
        parseFloat(this.report.donations_offering) +
        parseFloat(this.report.donations_events) +
        parseFloat(this.report.donations_transport);
      return parseFloat(sum).toFixed(2);
    },
    sumPerformance() {
      let sum =
        parseFloat(this.report.total_baptized) +
        parseFloat(this.report.total_salvation) +
        parseFloat(this.report.total_schedule_visits);
      return sum;
    }
  },
  mounted() {
    EventBus.$on("show-detail-report", value => {
      this.getReport(value);
      this.show = !this.show;
    });
  },
  methods: {
    async getReport(report) {
      try {
        const { data } = await window.axios.get("reports/detail", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            report_id: report.report_id
          }
        });
        this.report = data.response;
      } catch (error) {
        return error;
      }
    },
    formatDate(date) {
      if (date) {
        return new Date(date).toLocaleString("es", {
          year: "numeric",
          month: "short",
          day: "numeric"
        });
      }
      return "";
    },
    async download() {
      const { data } = await window.axios.post(
        "download/report",
        {
          report_id: this.report.report_id,
          cell_id: this.user.group.parent_id
        },
        {
          responseType: "blob",
          headers: {
            Accept: "application/pdf"
          }
        }
      );
      //Build a URL from the file
      const file = new Blob([data], { type: "application/pdf" });
      const fileURL = URL.createObjectURL(file);
      // //Open the URL on new Window
      window.open(fileURL);
    },
    showReport(value, report_id) {
      this.show = false;
      EventBus.$emit("show-dialog-report", {
        value: value,
        report_id: report_id
      });
    },
    async createReport() {
      try {
        const { data } = await window.axios.get("reports/detail", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: this.user.church_id,
            report_id: this.report.report_id
          }
        });
        this.showReport(data.response, this.report.report_id);
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
