<template>
  <div class="flex w-full">
    <div class="bg-white h-full min-h-screen w-12 px-8">
      <div class="flex flex-col items-center py-6">
        <router-link
          :to="{name: 'cell-groups-profile', params: {id: $route.params.id}}"
          :class="$route.name == 'cell-groups-profile' ? 'text-blue' : 'text-grey'"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-account-box text-xl"/>
        </router-link>
        <router-link
          v-supervisor="user"
          :to="{name: 'cell-groups-members', params: {id: $route.params.id}}"
          :class="$route.name == 'cell-groups-members' ? 'text-blue' : 'text-grey'"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-people text-xl"/>
        </router-link>
        <router-link
          v-supervisor="user"
          :class="$route.name == 'cell-groups-reports' ? 'text-blue' : 'text-grey'"
          :to="{name: 'cell-groups-reports', params: {id: $route.params.id}}"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-list text-xl"/>
        </router-link>
        <router-link
          :to="{name: 'cell-groups-timeline', params: {id: $route.params.id}}"
          class="my-3 no-underline hover:text-blue"
        >
          <v-icon
            :class="$route.name == 'cell-groups-timeline' ? 'text-blue' : 'text-grey'"
            class="font-medium"
          >list</v-icon>
        </router-link>
      </div>
    </div>
    <div class="w-full">
      <div class="flex flex-col justify-between mb-8 p-8">
        <div>
          <div class="mb-2 flex">
            <h1 class="font-normal text-blue-darkest">Timeline</h1>
            <!-- <p class="mx-3 flex items-center text-sm">
              <v-icon small
                      class="text-blue-darker mx-2">home</v-icon> - Groups - Cell Groups - D1 Z1 S3 C4
            </p>-->
          </div>
        </div>
      </div>
      <div class="px-8">
        <div class="bg-white my-4 py-8 border border-grey-light rounded-sm">
          <form class="mb-6" @submit.prevent="addCommentTimeline">
            <div class="text-center w-full px-8">
              <textarea
                v-model="comment"
                class="form-input border-grey outline-none focus:border-blue focus:border-2 relative w-full h-32"
                placeholder="Comentario"
              />
            </div>
            <div class="flex justify-end px-8 items-center">
              <button
                :disabled="comment == null || comment == ''"
                :class="{'opacity-50': comment == null || comment == ''}"
                class="bg-blue-darker text-white px-4 py-3 rounded font-medium w-36"
                type="submit"
              >Comentar</button>
            </div>
          </form>
          <div v-for="(item, index) in timeline" :key="index">
            <timeline-comment
              v-if="item.is_comment == 1"
              :timeline="item"
              :user="user"
              type="cell"
              class="px-8"
            />
            <div v-else class="flex px-8 items-center my-1">
              <timeline-activity :timeline="item"/>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "CellGroupsTimeline",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Cell Group Timeline",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      timeline: [],
      comment: null
    };
  },
  computed: mapState({
    user: state => state.auth.user,
    church: state => state.auth.church.info
  }),
  mounted() {
    this.getTimeline();
    EventBus.$on("delete-comment", () => {
      this.getTimeline();
    });
    EventBus.$on("update-comment", () => {
      this.getTimeline();
    });
  },
  methods: {
    async getTimeline() {
      try {
        const { data } = await window.axios.post(
          "/general/timeline/list-timeline",
          {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: 1,
            type: "cell",
            id_timeline_owner: this.$route.params.id,
            language: "es",
            start_date: null,
            end_date: null
          }
        );
        this.timeline = data.response;
      } catch (error) {
        console.error(error);
        return error;
      }
    },
    async addCommentTimeline() {
      try {
        await window.axios.post("/general/timeline/add-comments", {
          owner_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.user.church_id,
          type: "cell",
          commented_id: this.$route.params.id,
          comment: this.comment
        });
        this.comment = null;
        this.getTimeline();
      } catch (error) {
        console.log(error);
        return error;
      }
    }
  }
};
</script>
