<template>
  <v-dialog v-model="show" max-width="500px">
    <div
      class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
    >
      <h1 class="text-blue-darker mb-3 font-medium">Transfer</h1>
      <div class="flex justify-center">
        <button
          class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
          @click="showZone = true"
        >
          <p
            class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
          >Zone</p>
        </button>
        <button
          class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
          @click="showZonePastor = true"
        >
          <p
            class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
          >Zone Pastor</p>
        </button>
      </div>
    </div>
    <v-dialog v-model="showZone" max-width="400px">
      <div
        class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
      >
        <h1 class="text-blue-darker mb-1 font-normal">Transfer</h1>
        <h2 class="text-blue-darker mb-8 font-normal text-base">Zone</h2>
        <div class="w-2/3">
          <div class="mb-4">
            <label class="text-sm">Zone</label>
            <v-select
              v-validate="'required'"
              :options="zones"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('name') }"
              v-model="transfer_zone.zone"
              track-by="full_name"
              label="full_name"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="name"
              data-vv-as="name"
            />
          </div>
          <div>
            <label class="text-sm">Target</label>
            <v-select
              v-validate="'required'"
              :options="groups_transfer"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('district_id') }"
              v-model="transfer_zone.target"
              track-by="district_id"
              label="district_id"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="district_id"
              data-vv-as="name"
            />
          </div>
        </div>
        <div class="border border-solid w-full my-8"/>
        <div class="flex w-full justify-end px-6">
          <button
            class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
            @click="transferZone()"
          >
            <span
              class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
            >Transfer</span>
          </button>
        </div>
      </div>
    </v-dialog>
    <v-dialog v-model="showZonePastor" max-width="350px">
      <div
        class="bg-white rounded-lg h-auto pt-6 pb-6 shadow px-0 flex flex-col items-center justify-center w-full"
      >
        <h1 class="text-blue-darker mb-1 font-normal">Transfer</h1>
        <h2 class="text-blue-darker mb-8 font-normal text-base">Zone Pastor</h2>
        <div class="w-2/3">
          <div class="mb-3">
            <label class="text-sm">Name</label>
            <v-select
              v-validate="'required'"
              :options="users_transfer"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('zone') }"
              v-model="transfer_zone_pastor.pastor"
              track-by="user_id"
              label="user_id"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="zone"
              data-vv-as="zone"
            />
          </div>
          <div>
            <label class="text-sm">Target</label>
            <v-select
              v-validate="'required'"
              :options="groups_transfer"
              :searchable="true"
              :show-labels="false"
              :class="{ 'has-error' : errors.has('name') }"
              v-model="transfer_zone_pastor.target"
              track-by="full_name"
              label="full_name"
              placeholder
              class="border border-solid border-grey-light rounded my-1 w-full"
              name="name"
              data-vv-as="name"
            />
          </div>
        </div>
        <div class="border border-solid w-full my-8"/>
        <div class="flex w-full justify-end px-6">
          <button
            class="group rounded py-3 mx-3 w-48 text-white heebo flex items-center justify-center no-outline hover:bg-blue-darker border border-solid border-blue-darker"
            @click="transferZonePastor()"
          >
            <span
              class="text-blue-darker group-hover:text-white text-base font-medium heebo flex items-center justify-center tracking-wide"
            >Transfer</span>
          </button>
        </div>
      </div>
    </v-dialog>
  </v-dialog>
</template>

<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "TransferDistricts",
  data() {
    return {
      show: false,
      showZone: false,
      showZonePastor: false,
      transfer_zone: {
        zone: null,
        target: null
      },
      transfer_zone_pastor: {
        pastor: null,
        target: null
      },
      zones: [],
      users_transfer: [],
      groups_transfer: []
    };
  },
  computed: mapState({
    church: state => state.auth.church.info,
    user: state => state.auth.user
  }),
  mounted() {
    EventBus.$on("show-transfer-districts", () => {
      this.show = !this.show;
    });
    this.getZones();
    this.getGroupsTransfer();
    this.getUsersTransfer();
  },
  methods: {
    async getZones() {
      const { data } = await window.axios.post("districts/zones-groups-list", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        district_id: this.user.group.parent_id,
        active: "1"
      });

      this.zones = data.response;
    },
    async getGroupsTransfer() {
      const { data } = await window.axios.post("groups/list-groups-target", {
        user_id: this.user.user_id,
        role_id: this.user.role
      });

      this.groups_transfer = data.response.districts;
    },
    async getUsersTransfer() {
      const { data } = await window.axios.post("groups/list-user-transfer", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        church_id: this.church.church_id,
        parent_id: this.user.group.parent_id
      });

      this.users_transfer = data.response;
    },
    async transferZone() {
      await window.axios.post("districts/transfer-zone", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        zone_id: this.transfer_zone.zone.id,
        district_id: this.transfer_zone.target.id,
        district_code: this.transfer_zone.target.district_code
      });

      this.showZone = false;
    },
    async transferZonePastor() {
      await window.axios.post("districts/transfer-zone-pastor", {
        user_id: this.user.user_id,
        role_id: this.user.role,
        zone_pastor_id: this.transfer_zone_pastor.pastor.user_id,
        district_id: this.transfer_zone_pastor.target.id
      });

      this.showZonePastor = false;
    }
  }
};
</script>
