<template>
  <div class="flex w-full">
    <div class="bg-white h-full min-h-screen w-12 px-8">
      <div class="flex flex-col items-center py-6">
        <router-link
          :to="{name: 'sectors-profile', params: {id: $route.params.id}}"
          :class="$route.name == 'sectors-profile' ? 'text-blue' : 'text-grey'"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-account-box text-xl"/>
        </router-link>
        <router-link
          :to="{name: 'sectors-cell-groups', params: {id: $route.params.id}}"
          :class="$route.name == 'sectors-cell-groups' ? 'text-blue' : 'text-grey'"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-people text-xl"/>
        </router-link>
        <router-link
          v-supervisor="user"
          :to="{name: 'sectors-reports', params: {id: $route.params.id}}"
          :class="$route.name == 'sectors-reports' ? 'text-blue' : 'text-grey'"
          class="my-3 no-underline hover:text-blue"
        >
          <span class="icon-list text-xl"/>
        </router-link>
        <router-link
          v-supervisor="user"
          :to="{name: 'sectors-timeline', params: {id: $route.params.id}}"
          class="my-3 no-underline hover:text-blue"
        >
          <v-icon
            :class="$route.name == 'sectors-timeline' ? 'text-blue' : 'text-grey'"
            class="font-medium"
          >list</v-icon>
        </router-link>
      </div>
    </div>
    <div class="w-full">
      <div class="flex flex-col justify-between mb-8 p-8">
        <div>
          <h1 class="font-normal text-blue-darkest">Cell Groups</h1>
          <div class="text-blue-darker text-base">
            Showing
            <span class="px-2 bg-blue-lighter rounded text-blue mx-1">{{ cells.length }}</span>
            out of {{ cells.length }}
            <a
              class="px-2 bg-blue-lighter rounded text-blue mx-1"
              @click.prevent="get(active ? false : true)"
            >{{ active ? 'active': 'inactive' }}</a>
            <span class="px-2 rounded text-blue-darkest">cell groups</span>
          </div>
        </div>
      </div>
      <div class="px-8 w-full">
        <v-data-table
          :headers="headers"
          :items="cells"
          :loading="loading"
          hide-actions
          class="elevation-0 border border-grey-light rounded"
        >
          <template slot="headerCell" slot-scope="props">
            <span class="uppercase tracking-wide text-grey">{{ props.header.text }}</span>
          </template>
          <template slot="items" slot-scope="props">
            <tr class="group">
              <td
                :class="props.item.is_complete == 0 ? 'border-red' : 'group-hover:border-blue'"
                class="flex items-center py-8 pl-6 group-hover:border-l-4 group-hover:pl-5 -ml-px"
              >
                <span class="-ml-2 w-8">
                  <v-tooltip right color="red">
                    <v-icon
                      v-if="props.item.is_complete == 0"
                      slot="activator"
                      color="red"
                      class="m-0 p-0 cursor-pointer"
                    >info_outline</v-icon>
                    <span>Incomplete information</span>
                  </v-tooltip>
                </span>
                <div
                  class="mx-1 bg-green text-white h-8 w-8 flex items-center justify-center rounded-full"
                >C{{ props.item.cell_code }}</div>
                <span class="mx-1">D{{ props.item.district_code }}</span>
                <span class="mx-1">Z{{ props.item.zone_code }}</span>
                <span class="mx-1">S{{ props.item.sector_code }}</span>
                <span class="mx-1">C{{ props.item.cell_code }}</span>
              </td>
              <td
                class="text-base text-blue tracking-wide"
              >{{ props.item.leader ? props.item.leader : '--' }}</td>
              <td class="text-base">{{ formatDate(props.item.start_date) }}</td>
              <td
                class="text-center text-base text-blue tracking-wide"
              >{{ props.item.city ? props.item.city : '--' }}</td>
              <td class="text-center text-base text-blue">{{ props.item.members.padStart(2, "0") }}</td>
              <td class="text-center">
                <v-menu
                  :nudge-bottom="10"
                  origin="center center"
                  transition="scale-transition"
                  offset-y
                  bottom
                >
                  <v-btn slot="activator" flat icon color="grey" class="no-outline">
                    <v-icon>info_outline</v-icon>
                  </v-btn>
                  <div>
                    <ul class="p-3 list-reset heebo bg-white w-48">
                      <li class="my-2 group">
                        <a
                          class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                          @click="profile(props.item)"
                        >
                          <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>Profile
                        </a>
                      </li>
                      <!-- <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                          Edit
                        </a>
                      </li>
                      <li class="my-2 group">
                        <a class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark">
                          <v-icon class="group-hover:text-blue text-grey">delete</v-icon>
                          Delete
                        </a>
                      </li>-->
                    </ul>
                  </div>
                </v-menu>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SectorsCellGroups",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Sectors Cell Groups",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Cell Id", value: "cell_code" },
        { text: "Leader", value: "leader" },
        { text: "Start Date", value: "start_date" },
        { text: "City", value: "city", align: "center" },
        { text: "Members", value: "members", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      cells: [],
      active: true,
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      user: "auth/user"
    })
  },
  mounted() {
    this.get(true);
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.post("sectors/cell-groups-list", {
          role_id: this.user.role,
          sector_id: this.$route.params.id,
          user_id: this.user.user_id,
          active: active ? "1" : "0"
        });
        data.response ? (this.cells = data.response) : (this.cells = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    }
  }
};
</script>
