<template>
  <div class="flex w-full">
    <div class="p-8 w-full">
      <div class="flex items-center justify-between mb-6">
        <div class="flex flex-col mb-8">
          <div class="flex items-center">
            <h1 class="font-normal text-blue-darkest">Invitados</h1>
          </div>
          <div>
            Mostrando
            <span class="px-2 bg-blue-lightest rounded text-blue mx-1">
              <select v-model="pagination.rowsPerPage" name="show_items" class="outline-none">
                <option value="5">5</option>
                <option value="10">10</option>
                <option value="20">20</option>
              </select>
            </span>
            de {{ guests.length }}
          </div>
        </div>
        <div>
          <button
            class="rounded py-2 px-4 text-white flex items-center justify-center no-outline bg-gradient"
            @click.prevent="addGuest"
          >
            <v-icon class="mr-2 text-white">add</v-icon>
            <span class="font-medium">Agregar Invitado</span>
          </button>
        </div>
      </div>
      <div>
        <div class="mb-3">
          <p class="text-grey tracking-wide mb-1 hidden">Filter by</p>
          <div class="flex justify-between">
            <div class="hidden">
              <v-select
                :options="[]"
                :searchable="true"
                :show-labels="false"
                track-by="name"
                label="name"
                placeholder="District"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                name="district"
              />
              <v-select
                :options="[]"
                :searchable="true"
                :show-labels="false"
                track-by="name"
                label="name"
                placeholder="Zone"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                name="zone"
              />
              <v-select
                :options="[]"
                :searchable="true"
                :show-labels="false"
                track-by="name"
                label="name"
                placeholder="Sector"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white mr-3"
                name="sector"
              />
              <v-select
                :options="[]"
                :searchable="true"
                :show-labels="false"
                track-by="name"
                label="name"
                placeholder="Cell Group"
                class="border border-solid border-grey-light rounded py-1 w-42 bg-white"
                name="sector"
              />
            </div>
            <div class="flex w-full justify-end items-center">
              <button
                class="bg-white p-2 rounded border border-solid border-grey-light hover:opacity-75 mx-3"
                @click="download"
              >
                <v-icon color="blue">get_app</v-icon>
              </button>
              <input
                v-model="search"
                type="text"
                class="form-input bg-white w-64"
                placeholder="Buscar"
              >
            </div>
          </div>
        </div>
        <v-data-table
          :headers="headers"
          :items="guests"
          :loading="loading"
          :search="search"
          hide-actions
          no-data-text="No hay informacin"
          class="elevation-0 border border-grey-lighter rounded"
        >
          <template slot="headerCell" slot-scope="props">
            <span class="uppercase tracking-wide text-grey">{{ props.header.text }}</span>
          </template>
          <template slot="items" slot-scope="props">
            <td class="flex items-center text-base text-blue">
              <img
                :src="props.item.profile_picture ? props.item.profile_picture : require('@/assets/images/avatar.png')"
                alt="Profile Picture"
                class="w-8 h-8 bg-white rounded-full mr-3 object-cover"
              >
              {{ props.item.full_name }}
            </td>
            <td class="text-sm text-blue-darker tracking-wide">
              <span class="mx-1">D{{ props.item.district_code }}</span>
              <span class="mx-1">Z{{ props.item.zone_code }}</span>
              <span class="mx-1">S{{ props.item.sector_code }}</span>
              <span class="mx-1">C{{ props.item.cell_code }}</span>
            </td>
            <td
              class="text-center text-base tracking-wide"
            >{{ props.item.last_assistance ? formatDate(props.item.last_assistance) : '--' }}</td>
            <td
              class="text-sm text-blue-darker tracking-wide"
            >{{ props.item.city ? props.item.city : '--' }}</td>
            <td
              class="text-center text-base text-blue-darker"
            >{{ props.item.assistance.padStart(2, '0') }}</td>
            <td class="text-center">
              <v-menu
                :nudge-bottom="10"
                origin="center center"
                transition="scale-transition"
                offset-y
                bottom
              >
                <v-btn slot="activator" flat icon color="grey" class="no-outline">
                  <v-icon>more_vert</v-icon>
                </v-btn>
                <div>
                  <ul class="p-3 list-reset heebo bg-white w-36">
                    <li class="mb-3 group">
                      <a
                        class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                        @click="profile(props.item)"
                      >
                        <v-icon class="group-hover:text-blue text-grey">info_outline</v-icon>
                        <span class="mx-2">Perfil</span>
                      </a>
                    </li>
                    <li :class="props.item.promote == 1 ? 'mb-3' : ''" class="group">
                      <a
                        class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                        @click="edit(props.item)"
                      >
                        <v-icon class="group-hover:text-blue text-grey">edit</v-icon>
                        <span class="mx-2">Editar</span>
                      </a>
                    </li>
                    <li v-if="props.item.promote == 1" class="mb-3 group">
                      <a
                        class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                        @click.prevent="promove(props.item)"
                      >
                        <v-icon class="group-hover:text-blue text-grey">people</v-icon>
                        <span class="mx-2">Promover</span>
                      </a>
                    </li>
                    <li class="mb-3 group hidden">
                      <a
                        class="no-underline group-hover:text-blue-darker text-sm tracking-wide text-grey-dark"
                      >
                        <v-icon class="group-hover:text-blue text-grey">delete</v-icon>Delete
                      </a>
                    </li>
                  </ul>
                </div>
              </v-menu>
            </td>
          </template>
        </v-data-table>
        <div v-if="pages != 0" class="text-right pt-2">
          <v-pagination v-model="pagination.page" :length="pages"/>
        </div>
      </div>
    </div>
    <profile-guest/>
    <promove-guest/>
    <edit-profile-guest/>
  </div>
</template>

<script>
import EventBus from "@/bus";
import { mapState } from "vuex";
export default {
  name: "MembersGuests",
  middleware: "auth",
  metaInfo: {
    title: "Celulas App",
    titleTemplate: "%s - Members Guests",
    htmlAttrs: {
      lang: "en"
    }
  },
  data() {
    return {
      headers: [
        { text: "Invitado", value: "full_name" },
        { text: "ID", value: "cell_id" },
        { text: "ltima Asistencia", value: "last_assistance" },
        { text: "Ciudad", value: "city" },
        { text: "Asistencia", value: "assistance", align: "center" },
        { text: "", value: "details", align: "center" }
      ],
      guests: [],
      active: true,
      loading: true,
      search: "",
      pagination: {
        totalItems: 0,
        rowsPerPage: 5
      }
    };
  },
  computed: {
    ...mapState({
      user: state => state.auth.user,
      church: state => state.auth.church.info
    }),
    pages() {
      if (
        this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) {
        return 0;
      }

      return Math.ceil(
        this.pagination.totalItems / this.pagination.rowsPerPage
      );
    }
  },
  mounted() {
    this.get(true);
    EventBus.$on("save-guest-member", () => {
      this.get(true);
    });
  },
  methods: {
    async get(active) {
      this.loading = true;
      try {
        const { data } = await window.axios.get("members/guests/list", {
          params: {
            user_id: this.user.user_id,
            role_id: this.user.role,
            church_id: 1,
            parent_id: this.user.group.parent_id
          }
        });
        data.response ? (this.guests = data.response) : (this.guests = []);
      } catch (error) {
        return error;
      } finally {
        this.active = active;
        this.loading = false;
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString("es", {
        year: "numeric",
        month: "short",
        day: "numeric"
      });
    },
    async profile(user) {
      const { data } = await window.axios.get("members/guests/profile", {
        params: {
          guest_id: user.member_id,
          user_id: this.user.user_id,
          church_id: this.user.church_id,
          role_id: this.user.role
        }
      });
      EventBus.$emit("show-profile-guest", data.response);
    },
    async edit(user) {
      const { data } = await window.axios.get("members/guests/profile", {
        params: {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: 1,
          guest_id: user.member_id
        }
      });
      EventBus.$emit("edit-profile-guest", data.response);
    },
    promove(user) {
      EventBus.$emit("promove-guest", user);
    },
    addGuest() {
      EventBus.$emit("add-guest", this.user);
    },
    async download() {
      const { data } = await window.axios.post(
        "download/member-list",
        {
          user_id: this.user.user_id,
          role_id: this.user.role,
          church_id: this.user.church_id,
          parent_id: this.user.group.parent_id,
          active: "1",
          type_member: "guest",
          type_report: "pdf"
        },
        {
          responseType: "blob",
          headers: {
            Accept: "application/pdf"
          }
        }
      );
      //Build a URL from the file
      const file = new Blob([data], { type: "application/pdf" });
      const fileURL = URL.createObjectURL(file);
      // //Open the URL on new Window
      window.open(fileURL);
    }
  }
};
</script>
